import { createStore, combineReducers, applyMiddleware } from "redux";
import thunk from "redux-thunk";
import { composeWithDevTools } from "redux-devtools-extension";
import {
  bookListReducers,
  bookDetailsReducers,
  addBookReducer,
  addBookReviewReducer,
  updateBookReviewReducer,
  deleteBookReviewReducer,
} from "./reducers/bookReducers";
import {
  userLoginReducer,
  userRegisterReducer,
  userDetailsReducer,
  userUpdateReducer,
  userDeleteReducer,
  memberLogoutReducer,
  updateUserTokenReducer,
} from "./reducers/userReducers";
import {
  chamberListReducers,
  relatedMembersListReducers,
  practiceAreaListReducers,
  addPracticeAreaReducer,
  deletePracticeAreaReducer,
  qualificationListReducers,
  addQualificationReducer,
  updateQualificationReducer,
  contactListReducers,
  updateContactReducer,
  addMemberReviewReducer,
  updateMemberReviewReducer,
  deleteMemberReviewReducer,
  updateMemberDescriptionReducer,
  updateBannerDetailsReducer,
  updateDpReducer,
  verifyMemberReducer,
  memberDetailsReducers,
} from "./reducers/chamberReducers";
import {
  listQuestionsReducer,
  listUnansweredQuestionsReducer,
  listTopQuestionsReducer,
  listRecentQuestionsReducer,
  createQuestionReducer,
  updateQuestionReducer,
  deleteQuestionReducer,
  createAnswerReducer,
  updateAnswerReducer,
  deleteAnswerReducer,
  createUpvoteReducer,
  createDownvoteReducer,
  createCommentReducer,
  updateCommentReducer,
  deleteCommentReducer,
  listTagsReducer,
} from "./reducers/communityReducers";

import { categoriesReducers } from "./reducers/categoryReducers";
import {
  paymentPlanReducer,
  planStatusReducer,
} from "./reducers/paymentReducers";

import {
  blogPostReducers,
  createPostCommentReducer,
  updatePostCommentReducer,
  deletePostCommentReducer,
} from "./reducers/blogReducers";

import { addEmailReducer } from "./reducers/mailReducers";

import {
  bookmarkBookListReducer,
  bookmarkLawyerListReducer,
  addBookmarkBookReducer,
  addBookmarkLawyerReducer,
  deleteBookmarkBookReducer,
  deleteBookmarkLawyerReducer,
} from "./reducers/bookmarkReducers";
import {
  deleteLawyerNotifyReducers,
  deleteQuestionNotifyReducers,
  deleteRecentQuestionNotifyReducers,
  lawyerNotifyReducers,
  questionNotifyReducers,
  recentQuestionNotifyReducers,
  planNotifyReducers,
  deletePlanNotifyReducers,
} from "./reducers/notifyReducers";

const reducer = combineReducers({
  userLogin: userLoginReducer,
  userRegister: userRegisterReducer,
  updateUserToken: updateUserTokenReducer,
  userDetails: userDetailsReducer,
  memberLogout: memberLogoutReducer,
  verifyMember: verifyMemberReducer,
  userUpdate: userUpdateReducer,
  userDelete: userDeleteReducer,
  membersListing: chamberListReducers,
  memberDetails: memberDetailsReducers,
  updateBannerDetails: updateBannerDetailsReducer,
  updateProfileImage: updateDpReducer,
  updateDescription: updateMemberDescriptionReducer,
  relatedMembersListing: relatedMembersListReducers,
  practiceAreaList: practiceAreaListReducers,
  addPracticeArea: addPracticeAreaReducer,
  deletePracticeArea: deletePracticeAreaReducer,
  qualificationList: qualificationListReducers,
  addQualification: addQualificationReducer,
  updateQualification: updateQualificationReducer,
  contactList: contactListReducers,
  updateContactList: updateContactReducer,
  addMemberReview: addMemberReviewReducer,
  updateMemberReview: updateMemberReviewReducer,
  deleteMemberReview: deleteMemberReviewReducer,
  bookList: bookListReducers,
  bookDetails: bookDetailsReducers,
  categoriesList: categoriesReducers,
  questionsList: listQuestionsReducer,
  unansweredQuestionsList: listUnansweredQuestionsReducer,
  topQuestionsList: listTopQuestionsReducer,
  recentQuestionsList: listRecentQuestionsReducer,
  questionCreate: createQuestionReducer,
  questionUpdate: updateQuestionReducer,
  questionDelete: deleteQuestionReducer,
  answerCreate: createAnswerReducer,
  answerUpdate: updateAnswerReducer,
  answerDelete: deleteAnswerReducer,
  upvoteCreate: createUpvoteReducer,
  downvoteCreate: createDownvoteReducer,
  commentCreate: createCommentReducer,
  commentUpdate: updateCommentReducer,
  commentDelete: deleteCommentReducer,
  tagsList: listTagsReducer,
  uploadBook: addBookReducer,
  bookReview: addBookReviewReducer,
  updateBookReview: updateBookReviewReducer,
  deleteBookReview: deleteBookReviewReducer,
  blogPosts: blogPostReducers,
  postComment: createPostCommentReducer,
  updatePostComment: updatePostCommentReducer,
  deletePostComment: deletePostCommentReducer,
  addSubscriberEmail: addEmailReducer,
  bookBookmarks: bookmarkBookListReducer,
  lawyerBookmarks: bookmarkLawyerListReducer,
  createBookBookmark: addBookmarkBookReducer,
  createLawyerBookmark: addBookmarkLawyerReducer,
  deleteBookBookmark: deleteBookmarkBookReducer,
  deleteLawyerBookmark: deleteBookmarkLawyerReducer,
  lawyerNotifications: lawyerNotifyReducers,
  questionNotifications: questionNotifyReducers,
  recentQuestionNotifications: recentQuestionNotifyReducers,
  deleteLawyerNotifications: deleteLawyerNotifyReducers,
  deleteQuestionNotifications: deleteQuestionNotifyReducers,
  deleteRecentQuestionNotifications: deleteRecentQuestionNotifyReducers,
  planNotifications: planNotifyReducers,
  deletePlanNotifications: deletePlanNotifyReducers,
  paymentPlan: paymentPlanReducer,
  planStatus: planStatusReducer,
});

const userInfoFromStorage = localStorage.getItem("userInfo")
  ? JSON.parse(localStorage.getItem("userInfo"))
  : null;

const initialState = {
  userLogin: { userInfo: userInfoFromStorage },
};

const middleware = [thunk];

const store = createStore(
  reducer,
  initialState,
  composeWithDevTools(applyMiddleware(...middleware))
);

export default store;
