import React, { useState, useEffect } from "react";
import "./Home.css";
import Navbar from "./Navbar";
import { Link, useHistory } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { membersList } from "../../actions/chamberActions";
import { planStatusAction } from "../../actions/paymentActions";
import headerimg from "../../images/header.jpg";
import { LazyLoadComponent } from "react-lazy-load-image-component";

function Home() {
  const [countryName, setCountryName] = useState("");
  const [stateName, setStateName] = useState("");
  const [cityName, setCityName] = useState("");
  const [stateCode, setStateCode] = useState("");
  const [cityCode, setCityCode] = useState("");
  const [country, setCountry] = useState([]);
  const [state, setState] = useState([]);
  const [city, setCity] = useState([]);
  const [keyword, setKeyword] = useState("");
  const [findlawyer, setfindLawyer] = useState(true);
  const [findquestion, setfindQuestion] = useState(false);

  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(membersList());
  }, []);

  const userLogin = useSelector((state) => state.userLogin);
  const { userInfo } = userLogin;

  const membersListContainer = useSelector((state) => state.membersListing);
  const { members, success } = membersListContainer;

  useEffect(() => {
    try {
      userInfo &&
        success &&
        members.find(
          (member) =>
            member.member == userInfo.id &&
            dispatch(planStatusAction(member._id))
        );
    } catch (e) {
      return;
    }
  }, [success]);

  var headers = new Headers();
  headers.append(
    "X-CSCAPI-KEY",
    "VU1VYXF3U25EMkxTNFF0YmtNU3BwSHI5RVQzS3hZTDI4czVvUFl5OQ=="
  );

  var requestOptions = {
    method: "GET",
    headers: headers,
    redirect: "follow",
  };

  useEffect(async () => {
    await fetch("https://api.countrystatecity.in/v1/countries", requestOptions)
      .then((response) => response.text())
      .then((result) => (setCountry(JSON.parse(result))))
      .catch((error) => error);
  }, []);

  useEffect(async () => {
    await fetch(
      `https://api.countrystatecity.in/v1/countries/${stateCode}/states`,
      requestOptions
    )
      .then((response) => response.text())
      .then((result) => setState(JSON.parse(result)))
      .catch((error) => error);
  }, [stateCode]);

  useEffect(async () => {
    await fetch(
      `https://api.countrystatecity.in/v1/countries/${stateCode}/states/${cityCode}/cities`,
      requestOptions
    )
      .then((response) => response.text())
      .then((result) => setCity(JSON.parse(result)))
      .catch((error) => error);
  }, [cityCode]);

  let history = useHistory();

  const handleFindBtn = (e) => {
    e.preventDefault();

    countryName &&
      stateName &&
      cityName &&
      history.push(
        `/findmembers/?search=${countryName}+${stateName}+${cityName}`
      );
  };

  let regionNamesInEnglish = new Intl.DisplayNames(["en"], { type: "region" });

  return (
    <div>
      <LazyLoadComponent>
        <div
          className="header w-full"
          style={{
            backgroundImage: `url(${headerimg})`,
            backgroundSize: "cover",
            height: "700px",
            backgroundPosition: "center",
            overflowX: "hidden",
            isolation: "isolate",
            display: "block",
          }}
        >
          <Navbar />
          <div
            style={{ maxWidth: "800px" }}
            className="main-header mx-auto px-5"
          >
            <h1 className="main-header-text text-white text-6xl font-bold pb-2 w-full bg-transparent">
              Hire Great Lawyers, Fast.
            </h1>
            <p
              style={{ maxWidth: "384px" }}
              className="text-white mb-5 font-roboto text-md px-2 bg-transparent"
            >
              Find the best lawyers, or ask simple question on our community.
            </p>
          </div>

          <div className="options-btn mx-auto w-full text-center my-4 px-4 text-gray-900">
            <input
              type="radio"
              id="practice"
              name="radio-options"
              className="radio-btn"
              hidden
              onChange={(e) =>
                e.target.checked ? setfindLawyer(true) : setfindLawyer(false)
              }
            />

            <label
              className={`client-label font-roboto radio-label h-full py-2 w-40 inline-block px-4 text-center rounded-l ${
                findlawyer ? "bg-gray-600 text-white rounded-l" : "bg-gray-300"
              }`}
              htmlFor="practice"
              onClick={(e) => {
                return findlawyer == false && findquestion != true
                  ? setfindLawyer(true)
                  : (setfindLawyer(true), setfindQuestion(false));
              }}
            >
              Find a lawyer
            </label>

            <input
              type="radio"
              id="bylawyer"
              name="radio-options"
              className="radio-btn"
              hidden
              onChange={(e) =>
                e.target.checked
                  ? setfindQuestion(true)
                  : setfindQuestion(false)
              }
            />
            <label
              className={`font-roboto rounded-r radio-label h-full py-2 w-40 inline-block text-center ${
                findquestion
                  ? "bg-gray-600 text-white rounded-l"
                  : "bg-gray-300"
              }`}
              htmlFor="bylawyer"
              onClick={(e) => {
                return (
                  findquestion == false &&
                  findlawyer == true &&
                  (setfindQuestion(true), setfindLawyer(false))
                );
              }}
            >
              Ask a question
            </label>
          </div>

          {findlawyer == true && (
            <div
              style={{ maxWidth: "630px" }}
              className="options bg-gray-300 h-10 mx-auto rounded-sm mb-5"
            >
              <form
                onSubmit={handleFindBtn}
                className="flex justify-evenly pt-1 mx-auto text-gray-900"
              >
                <select
                  className="h-8 w-44 rounded-sm shadow-lg"
                  name="country"
                  id="country"
                  required
                  onChange={(e) => (
                    setStateCode(e.target.value),
                    e.target.value == ""
                      ? ""
                      : setCountryName(regionNamesInEnglish.of(e.target.value))
                  )}
                >
                  <option value="country" disabled selected>
                    Country
                  </option>

                  <option value="">{countryName}</option>

                  {country.map((data) => (
                    <option
                      className="country-code"
                      value={data.iso2}
                      placeholder={data.name}
                    >
                      {data.name}
                    </option>
                  ))}
                </select>

                <select
                  className="h-8 w-44 rounded-sm shadow-lg"
                  name="state"
                  id="state"
                  required
                  onChange={(e) => (
                    setCityCode(e.target.value), setStateName(e.target.value)
                  )}
                >
                  <option value="state" selected disabled>
                    State
                  </option>
                  <option value="state">{stateName}</option>
                  {state != "" &&
                    Array.from(state).map((data) => (
                      <option value={data.iso2}>{data.name}</option>
                    ))}
                </select>

                <select
                  className="h-8 w-44 rounded-sm shadow-lg"
                  name="city"
                  id="city"
                  required
                  onChange={(e) => setCityName(e.target.value)}
                >
                  <option value="city" selected disabled>
                    City
                  </option>

                  {city != "" &&
                    Array.from(city).map((data) => (
                      <option value={data.iso2}>{data.name}</option>
                    ))}
                </select>
                <input
                  className="search-btn w-20 bg-gray-600 text-white"
                  type="submit"
                  value="search"
                />
              </form>
            </div>
          )}

          {findquestion && (
            <form
              onSubmit={(e) => (
                e.preventDefault(), history.push(`/community?search=${keyword}`)
              )}
            >
              <div
                style={{ width: "600px" }}
                className="question-search mx-auto mb-5 h-10 bg-gray-200 rounded-sm"
              >
                <input
                  style={{ width: "80%", height: "72%", margin: "6px 0 0 8px" }}
                  type="text"
                  value={keyword}
                  onChange={(e) => setKeyword(e.target.value)}
                  className="text-gray-700 font-roboto outline-none p-2 bg-transparent"
                  placeholder="ask a question on the community"
                  required
                />
                <button
                  style={{ width: "16%", height: "75%" }}
                  className="bg-gray-600 text-white font-roboto rounded-sm my-1 ml-2"
                >
                  search
                </button>
              </div>
            </form>
          )}
          {userInfo ? (
            <h2 className="text-center font-roboto text-3xl text-white font-semibold">
              WELCOME {userInfo.name.toUpperCase()}
            </h2>
          ) : (
            <div className="w-36 mx-auto">
              <Link to="/signup">
                <button className="sign-btn w-full h-10 block font-semibold text-white bg-brandred rounded">
                  SIGN UP!
                </button>
              </Link>
            </div>
          )}
        </div>
      </LazyLoadComponent>
      <h1 className="second-text text-3xl text-center my-16 font-semibold bg-transparent">
        TOP LAWYERS OF THE MONTH
      </h1>
    </div>
  );
}

export default Home;
