export const chamberListReducers = (state = { members: [] }, action) => {
  switch (action.type) {
    case CHAMBER_LIST_REQUEST:
      return { loading: true, members: [] };

    case CHAMBER_LIST_SUCCESS:
      return { loading: false, success: true, members: action.payload };

    case CHAMBER_LIST_FAIL:
      return { loading: false, error: action.payload };

    default:
      return state;
  }
};

export const memberDetailsReducers = (
  state = { member: { details: [] } },
  action
) => {
  switch (action.type) {
    case MEMBER_DETAILS_REQUEST:
      return { loading: true, ...state };

    case MEMBER_DETAILS_SUCCESS:
      return { loading: false, success: true, member: action.payload };

    case MEMBER_DETAILS_FAIL:
      return { loading: false, error: action.payload };

    default:
      return state;
  }
};

export const updateMemberDescriptionReducer = (state = {}, action) => {
  switch (action.type) {
    case UPDATE_ABOUT_REQUEST:
      return { loading: true };

    case UPDATE_ABOUT_SUCCESS:
      return {
        loading: false,
        success: true,
        message: "Updated successfully",
        state: action.payload,
      };

    case UPDATE_ABOUT_FAIL:
      return { loading: false, error: action.payload };

    case UPDATE_ABOUT_RESET:
      return {};

    default:
      return state;
  }
};

