import React, { useState, useEffect } from "react";
import Navbar from "./HOME/Navbar";
import Card from "./Card";
import "./Findmembers.css";
import { useHistory } from "react-router";
import { membersList } from "../actions/chamberActions";
import { listCategories } from "../actions/categoryActions";
import { useDispatch, useSelector } from "react-redux";
import { LazyLoadComponent } from "react-lazy-load-image-component";
import headerimg from "../images/Discrimination.jpg";

function Findmembers({ history }) {
  const [client, setClient] = useState(true);
  const [lawyer, setLawyer] = useState(false);
  const [inputText, setInputText] = useState("");
  const [country, setCountry] = useState([]);
  const [state, setState] = useState([]);
  const [city, setCity] = useState([]);
  const [countryName, setCountryName] = useState("");
  const [stateName, setStateName] = useState("");
  const [cityName, setCityName] = useState("");
  const [stateCode, setStateCode] = useState("");
  const [cityCode, setCityCode] = useState("");
  const [practice, setPractice] = useState("");
  const [membersLength, setMembersLength] = useState(4);
  // const [subKeyword, setSubKeyword] = useState()

  const mainHistory = useHistory();
  const dispatch = useDispatch();

  let searchList = useSelector((state) => state.membersListing);
  const { members, success } = searchList;

  let categoriesList = useSelector((state) => state.categoriesList);
  const { loading, categoryList, success: categorySuccess } = categoriesList;

  let keyword = history.location.search;

  useEffect(() => {
    dispatch(membersList(keyword));
    console.log(keyword);
  }, []);

  useEffect(() => {
    dispatch(membersList(keyword));
    dispatch(listCategories());
  }, [dispatch, keyword]);

  const submitHandler = (e) => {
    e.preventDefault();

    return lawyer && inputText
      ? mainHistory.push(`/findmembers/?search=${inputText}`)
      : client &&
          countryName &&
          stateName &&
          cityName &&
          practice &&
          mainHistory.push(
            `/findmembers/?search=${countryName}+${stateName}+${cityName}+${practice}`
          );
  };

  console.log(keyword);

  let regionNamesInEnglish = new Intl.DisplayNames(["en"], { type: "region" });

  var headers = new Headers();
  headers.append(
    "X-CSCAPI-KEY",
    "VU1VYXF3U25EMkxTNFF0YmtNU3BwSHI5RVQzS3hZTDI4czVvUFl5OQ=="
  );

  var requestOptions = {
    method: "GET",
    headers: headers,
    redirect: "follow",
  };

  useEffect(async () => {
    await fetch("https://api.countrystatecity.in/v1/countries", requestOptions)
      .then((response) => response.text())
      .then((result) => setCountry(JSON.parse(result)))
      .catch((error) => console.log("error", error));
  }, []);

  useEffect(async () => {
    await fetch(
      `https://api.countrystatecity.in/v1/countries/${stateCode}/states`,
      requestOptions
    )
      .then((response) => response.text())
      .then((result) => setState(JSON.parse(result)))
      .catch((error) => console.log("error", error));
  }, [stateCode]);

  useEffect(async () => {
    await fetch(
      `https://api.countrystatecity.in/v1/countries/${stateCode}/states/${cityCode}/cities`,
      requestOptions
    )
      .then((response) => response.text())
      .then((result) => setCity(JSON.parse(result)))
      .catch((error) => console.log("error", error));
  }, [cityCode]);

  return (
    <div>
      <LazyLoadComponent>
        <div
          style={{ backgroundImage: `url(${headerimg})` }}
          className="bg-cover h-96 bg-center relative"
        >
          <div className="overlay h-full w-full bg-black absolute opacity-20 top-0 left-0"></div>
          <Navbar />
          <h1 className="text-5xl font-bold font-roboto text-white relative z-20 text-center mb-4">
            FIND LAWYERS
          </h1>
          <p className="text-white text-center font-roboto relative z-20 px-4">
            Everyone deserves a good lawyer. Find the best lawyer for you, in
            one simple step
          </p>
        </div>
      </LazyLoadComponent>

      <form onSubmit={submitHandler} className="text-center mt-16">
        <input
          type="radio"
          id="practice"
          name="radio-options"
          className="radio-button"
          hidden
          onChange={(e) =>
            e.target.checked ? setClient(true) : setClient(false)
          }
        />

        <label
          className={`client-label font-roboto text-sm radio-label h-full py-2 w-40 inline-block px-4 text-center ${
            client ? "bg-black text-white rounded" : "bg-gray-200"
          }`}
          htmlFor="practice"
          onClick={(e) => {
            return client == false && lawyer != true
              ? setClient(true)
              : (setClient(true), setLawyer(false));
          }}
        >
          By Practice Area
        </label>

        <input
          type="radio"
          id="bylawyer"
          name="radio-options"
          className="radio-button"
          hidden
          onChange={(e) =>
            e.target.checked ? setLawyer(true) : setLawyer(false)
          }
        />
        <label
          className={`font-roboto text-sm radio-label h-full py-2 w-40 inline-block text-center bg-gray-200`}
          htmlFor="bylawyer"
          onClick={(e) => {
            return (
              lawyer == false &&
              client == true &&
              (setLawyer(true), setClient(false))
            );
          }}
        >
          By Lawyer/Firm Name
        </label>

        {client == true && (
          <div className="search-options mt-10">
            <select
              className="h-12 w-44 rounded-sm shadow-lg outline-none"
              name="country"
              id="country"
              required
              onChange={(e) => (
                setStateCode(e.target.value),
                e.target.value == ""
                  ? ""
                  : setCountryName(regionNamesInEnglish.of(e.target.value))
              )}
            >
              <option value="country" disabled selected>
                Country
              </option>

              <option value="">{countryName}</option>

              {country.map((data) => (
                <option
                  className="country-code"
                  value={data.iso2}
                  placeholder={data.name}
                >
                  {data.name}
                </option>
              ))}
            </select>

            <select
              className="h-12 w-44 rounded-sm shadow-lg outline-none"
              name="state"
              id="state"
              required
              onChange={(e) => {
                try {
                  return (
                    setCityCode(e.target.value), setStateName(e.target.value)
                  );
                } catch (e) {
                  return;
                }
              }}
            >
              <option value="state" selected disabled>
                State
              </option>
              <option value="state">{stateName}</option>
              {state != "" &&
                Array.from(state).map((data) => (
                  <option value={data.iso2}>{data.name}</option>
                ))}
            </select>

            <select
              className="h-12 w-44 rounded-sm shadow-lg outline-none"
              name="city"
              id="city"
              required
              onChange={(e) => setCityName(e.target.value)}
            >
              <option value="city" selected disabled>
                City
              </option>

              {city != "" &&
                Array.from(city).map((data) => (
                  <option value={data.iso2}>{data.name}</option>
                ))}
            </select>

            <select
              className="h-12 w-44 rounded-sm shadow-lg outline-none"
              name="city"
              id="city"
              required
              onChange={(e) => setPractice(e.target.value)}
            >
              <option
                value="Category"
                className="font-roboto"
                selected
                disabled
              >
                Category
              </option>
              {categorySuccess &&
                categoryList.map((data) => {
                  return data.names.map((e) => (
                    <option className="">{e.name}</option>
                  ));
                })}
            </select>
          </div>
        )}

        {lawyer == true && (
          <div className="search-look-container mt-10">
            <input
              className="lawyer-input block border mx-auto mt-12 w-80 py-2 px-3 border-gray-400 rounded outline-none"
              type="text"
              placeholder="Name of lawyer or firm"
              onChange={(e) => setInputText(e.target.value)}
            />
          </div>
        )}

        <input
          className="search-button block mx-auto bg-black text-white w-36 mt-7 py-1 font-roboto rounded"
          type="submit"
          value="Search"
        />
      </form>

      <div className="lawyer-card-container mx-auto mt-20 ">
        {success &&
          members
            .slice(0, membersLength)
            .map(response => 
              response.is_verified && (
              <Card
                name={response.name}
                category={response.category}
                country={response.country}
                state={response.state}
                city={response.city}
                caption={response.caption}
                image={response.profile_image}
                id={response._id}
              />
              ))}
      </div>
              
      {success && membersLength < members.length && (
        <h3
          onClick={() => setMembersLength(members.length)}
          className="text-sm font-semibold text-gray-700 text-center my-10 cursor-pointer"
        >
          See All
        </h3>
      )}

      {success && membersLength == members.length && (
        <h3
          onClick={() => setMembersLength(4)}
          className="text-sm font-semibold text-gray-700 text-center my-10 cursor-pointer"
        >
          See Less
        </h3>
      )}
    </div>
  );
}

export default Findmembers;
