import React, { useState, useEffect } from "react";
import Navbar from "../components/HOME/Navbar";
import "./Community.css";
import { useHistory } from "react-router-dom";
import { useSelector, useDispatch } from "react-redux";
import {
  userQuestion,
  listTags,
  listQuestions,
  listUnansweredQuestions,
} from "../actions/communityActions";
import Questions from "./Questions";
import headerimg from "../images/community.jpg";
import { LazyLoadComponent } from "react-lazy-load-image-component";

function Community({ history }) {
  const [country, setCountry] = useState([]);
  const [desktopFilterCountry, setDesktopFilterCountry] = useState([]);
  const [mobileFilterCountry, setMobileFilterCountry] = useState([]);
  const [state, setState] = useState([]);
  const [desktopFilterState, setDesktopFilterState] = useState([]);
  const [mobileFilterState, setMobileFilterState] = useState([]);
  const [city, setCity] = useState([]);
  const [desktopFilterCity, setDesktopFilterCity] = useState([]);
  const [mobileFilterCity, setMobileFilterCity] = useState([]);
  const [stateCode, setStateCode] = useState("");
  const [desktopFilterStateCode, setDesktopFilterStateCode] = useState([]);
  const [mobileFilterStateCode, setMobileFilterStateCode] = useState([]);
  const [cityCode, setCityCode] = useState("");
  const [desktopFilterCityCode, setDesktopFilterCityCode] = useState([]);
  const [mobileFilterCityCode, setMobileFilterCityCode] = useState([]);
  const [countryName, setCountryName] = useState("");
  const [desktopFilterCountryName, setDesktopFilterCountryName] = useState("");
  const [mobileFilterCountryName, setMobileFilterCountryName] = useState("");
  const [stateName, setStateName] = useState("");
  const [desktopFilterStateName, setDesktopFilterStateName] = useState("");
  const [mobileFilterStateName, setMobileFilterStateName] = useState("");
  const [cityName, setCityName] = useState("");
  const [desktopFilterCityName, setDesktopFilterCityName] = useState("");
  const [mobileFilterCityName, setMobileFilterCityName] = useState("");
  const [questionInput, setQuestionInput] = useState("");

  const [unansweredQuestionsChecker, setUnansweredQuestionsChecker] =
    useState(false);
  const [topQuestionsChecker, setTopQuestionsChecker] = useState(false);
  const [recentQuestionsChecker, setRecentQuestionsChecker] = useState(false);
  const [generalQuestionsChecker, setGeneralQuestionsChecker] = useState(true);
  const [tagsChecker, setTagsChecker] = useState(false);
  const [date, setDate] = useState("");
  const [mobileDate, setMobileDate] = useState("");
  const [tagSize, setTagSize] = useState(4);
  const [tagInput, setTagInput] = useState("");

  const mainHistory = useHistory();

  const dispatch = useDispatch();

  const tagsList = useSelector((state) => state.tagsList);

  const { tags, success: tagsSuccess, loading: tagsLoading } = tagsList;

  const questionsList = useSelector((state) => state.questionsList);

  const {
    userquestions,
    loading,
    success: questionsListSuccess,
  } = questionsList;

  const unansweredQuestionsList = useSelector(
    (state) => state.unansweredQuestionsList
  );

  const {
    unansweredquestions,
    loading: unansweredQuestionsLoading,
    success: unansweredQuestionsSuccess,
  } = unansweredQuestionsList;

  const topQuestionsList = useSelector((state) => state.topQuestionsList);

  const {
    topquestions,
    loading: topQuestionsLoading,
    success: topQuestionsSuccess,
  } = topQuestionsList;

  const recentQuestionsList = useSelector((state) => state.recentQuestionsList);

  const {
    recentquestions,
    loading: recentQuestionsLoading,
    success: recentQuestionsSuccess,
  } = recentQuestionsList;

  const questionCreated = useSelector((state) => state.questionCreate);

  const { success } = questionCreated;

  const questionUpdated = useSelector((state) => state.questionUpdate);

  const { success: questionUpdatedSuccess } = questionUpdated;

  const questionDeleted = useSelector((state) => state.questionDelete);

  const { success: questionDeletedSuccess } = questionDeleted;

  const answerCreated = useSelector((state) => state.answerCreate);

  const { success: answerCreatedSuccess } = answerCreated;

  const answerUpdated = useSelector((state) => state.answerUpdate);

  const { success: answerUpdatedSuccess } = answerUpdated;

  const answerDeleted = useSelector((state) => state.answerDelete);

  const { success: answerDeletedSuccess } = answerDeleted;

  const upvoteCreated = useSelector((state) => state.upvoteCreate);

  const { success: upvoteCreatedSuccess } = upvoteCreated;

  const downvoteCreated = useSelector((state) => state.downvoteCreate);

  const { success: downvoteCreatedSuccess } = downvoteCreated;

  const commentCreated = useSelector((state) => state.commentCreate);

  const { success: commentCreatedSuccess } = commentCreated;

  const commentUpdated = useSelector((state) => state.commentUpdate);

  const { success: commentUpdatedSuccess } = commentUpdated;

  const commentDeleted = useSelector((state) => state.commentDelete);

  const { success: commentDeletedSuccess } = commentDeleted;

  const userDetail = useSelector((state) => state.userDetails);

  const { user, error: detailError } = userDetail;

  const userLogin = useSelector((state) => state.userLogin);

  const { userInfo, error: userInfoError, success: loginSuccess } = userLogin;

  var headers = new Headers();
  headers.append(
    "X-CSCAPI-KEY",
    "VU1VYXF3U25EMkxTNFF0YmtNU3BwSHI5RVQzS3hZTDI4czVvUFl5OQ=="
  );

  var requestOptions = {
    method: "GET",
    headers: headers,
    redirect: "follow",
  };

  useEffect(async () => {
    await fetch("https://api.countrystatecity.in/v1/countries", requestOptions)
      .then((response) => response.text())
      .then((result) => setCountry(JSON.parse(result)))
      .catch((error) => console.log("error", error));
  }, []);

  useEffect(async () => {
    await fetch(
      `https://api.countrystatecity.in/v1/countries/${stateCode}/states`,
      requestOptions
    )
      .then((response) => response.text())
      .then((result) => setState(JSON.parse(result)))
      .catch((error) => console.log("error", error));
  }, [stateCode]);

  useEffect(async () => {
    await fetch(
      `https://api.countrystatecity.in/v1/countries/${stateCode}/states/${cityCode}/cities`,
      requestOptions
    )
      .then((response) => response.text())
      .then((result) => setCity(JSON.parse(result)))
      .catch((error) => console.log("error", error));
  }, [cityCode]);

  var headers = new Headers();
  headers.append(
    "X-CSCAPI-KEY",
    "VU1VYXF3U25EMkxTNFF0YmtNU3BwSHI5RVQzS3hZTDI4czVvUFl5OQ=="
  );

  var requestOptions = {
    method: "GET",
    headers: headers,
    redirect: "follow",
  };

  useEffect(async () => {
    await fetch("https://api.countrystatecity.in/v1/countries", requestOptions)
      .then((response) => response.text())
      .then((result) => setDesktopFilterCountry(JSON.parse(result)))
      .catch((error) => console.log("error", error));
  }, []);

  useEffect(async () => {
    await fetch(
      `https://api.countrystatecity.in/v1/countries/${desktopFilterStateCode}/states`,
      requestOptions
    )
      .then((response) => response.text())
      .then((result) => setDesktopFilterState(JSON.parse(result)))
      .catch((error) => console.log("error", error));
  }, [desktopFilterStateCode]);

  useEffect(async () => {
    await fetch(
      `https://api.countrystatecity.in/v1/countries/${desktopFilterStateCode}/states/${desktopFilterCityCode}/cities`,
      requestOptions
    )
      .then((response) => response.text())
      .then((result) => setDesktopFilterCity(JSON.parse(result)))
      .catch((error) => console.log("error", error));
  }, [desktopFilterCityCode]);

  var headers = new Headers();
  headers.append(
    "X-CSCAPI-KEY",
    "VU1VYXF3U25EMkxTNFF0YmtNU3BwSHI5RVQzS3hZTDI4czVvUFl5OQ=="
  );

  var requestOptions = {
    method: "GET",
    headers: headers,
    redirect: "follow",
  };

  useEffect(async () => {
    await fetch("https://api.countrystatecity.in/v1/countries", requestOptions)
      .then((response) => response.text())
      .then((result) => setMobileFilterCountry(JSON.parse(result)))
      .catch((error) => console.log("error", error));
  }, []);

  useEffect(async () => {
    await fetch(
      `https://api.countrystatecity.in/v1/countries/${mobileFilterStateCode}/states`,
      requestOptions
    )
      .then((response) => response.text())
      .then((result) => setMobileFilterState(JSON.parse(result)))
      .catch((error) => console.log("error", error));
  }, [mobileFilterStateCode]);

  useEffect(async () => {
    await fetch(
      `https://api.countrystatecity.in/v1/countries/${mobileFilterStateCode}/states/${mobileFilterCityCode}/cities`,
      requestOptions
    )
      .then((response) => response.text())
      .then((result) => setMobileFilterCity(JSON.parse(result)))
      .catch((error) => console.log("error", error));
  }, [mobileFilterCityCode]);

  useEffect(() => {
    dispatch(listTags());
  }, []);

  function questionSubmitHandler(e) {
    e.preventDefault();
    if (questionInput) {
      dispatch(userQuestion(questionInput, "Nigeria", "Edo", "Benin"));
      setQuestionInput("");
      setUnansweredQuestionsChecker(false);
      setRecentQuestionsChecker(false);
      setTopQuestionsChecker(false);
      setGeneralQuestionsChecker(true);
    }
  }

  function handleMobileFilterSubmit(e) {
    e.preventDefault();
    return (
      mobileFilterCountryName &&
      mobileFilterStateName &&
      mobileFilterCityName &&
      mobileDate &&
      mainHistory.push(
        `/community/?search=${mobileFilterCountryName}+${mobileFilterStateName}+${mobileFilterCityName}+${mobileDate}`
      )
    );
  }

  function handleDesktopFilterSubmit(e) {
    e.preventDefault();
    return (
      desktopFilterCountryName &&
      desktopFilterStateName &&
      desktopFilterCityName &&
      date &&
      mainHistory.push(
        `/community/?search=${desktopFilterCountryName}+${desktopFilterStateName}+${desktopFilterCityName}+${date}`
      )
    );
  }

  function filterHandler() {
    let element = document.querySelector(".filter-tags-mobile-container");
    element.classList.toggle("hidden");
  }

  function tagSearchSubmitHandler(value) {
    history.push(`/community?search=${value}`);
  }

  let regionNamesInEnglish = new Intl.DisplayNames(["en"], { type: "region" });

  return (
    <div>
      <LazyLoadComponent>
        <div
          className="bg-cover bg-top relative main-container"
          style={{ backgroundImage: `url(${headerimg})`, height: "500px" }}
        >
          <Navbar height="500px" />
          <div className="overlay h-full w-full bg-black absolute opacity-30 top-0 left-0"></div>
          <h1 className="text-center text-5xl font-bold text-white mb-8 relative z-20">
            COMMUNITY
          </h1>
          <p className="text-white text-base font-roboto text-center relative z-20 px-3">
            The only thing better than a lawyer is an entire community of
            lawyers
          </p>
        </div>
      </LazyLoadComponent>

      <h1 className="text-2xl font-bold font-roboto text-center mt-12 mb-2">
        ASK LAWYERS ON OUR COMMUNITY
      </h1>
      <p
        style={{ width: "490px" }}
        className="caption-text text-center px-3 mx-auto font-roboto"
      >
        Ask a brief question on your legal troubles and get answers from the
        worlds most elite community of lawyers. Specify your country, state and
        city to get answers from lawyers in your jurisdiction
      </p>
      <div className="question-section relative">
        <form onSubmit={questionSubmitHandler}>
          <textarea
            style={{ width: "525px" }}
            type="text"
            required
            className="question-section border border-gray-400 outline-none mx-auto block px-5 pt-2 font-roboto h-28 rounded mt-6"
            placeholder="Type a question"
            value={questionInput}
            onChange={(e) => setQuestionInput(e.target.value)}
          ></textarea>

          <div
            style={{ width: " 525px" }}
            className="select-container text-center mx-auto my-5 flex justify-between"
          >
            <select
              style={{ width: "160px" }}
              className="h-9 rounded-md shadow-lg border-gray-500 border pl-3 pb-1"
              name="country"
              id="country"
              required
              onChange={(e) => (
                setStateCode(e.target.value),
                e.target.value == ""
                  ? ""
                  : setCountryName(regionNamesInEnglish.of(e.target.value))
              )}
            >
              <option value="country" disabled selected>
                Country
              </option>
              <option value="">{countryName}</option>

              {country.map((data) => (
                <option
                  className="country-code"
                  value={data.iso2}
                  placeholder={data.name}
                >
                  {data.name}
                </option>
              ))}
            </select>
            <select
              style={{ width: "160px" }}
              className="h-9 rounded-md shadow-lg border border-gray-500 pl-3 pb-1"
              name="state"
              id="state"
              required
              onChange={(e) => (
                setCityCode(e.target.value), setStateName(e.target.value)
              )}
            >
              <option value="state" selected disabled>
                State
              </option>
              <option value="">{stateName}</option>
              {state != "" &&
                Array.from(state).map((data) => (
                  <option value={data.iso2}>{data.name}</option>
                ))}
            </select>
            <select
              style={{ width: "160px" }}
              className="h-9 w-40 rounded-md shadow-lg border border-gray-500 pl-3 pb-1"
              name="city"
              id="city"
              required
              onChange={(e) => setCityName(e.target.value)}
            >
              <option value="city" selected disabled>
                City
              </option>
              {city != "" &&
                Array.from(city).map((data) => (
                  <option value={data.iso2}>{data.name}</option>
                ))}
            </select>
          </div>
          <input
            className="w-36 cursor-pointer font-semibold h-8 rounded block mx-auto adder bg-black text-white border border-white"
            type="submit"
            value="Ask"
          />
        </form>
        <div className="answer-section"></div>
      </div>

      <div
        style={{ width: "520px" }}
        className="mobile-filter-btn-container flex justify-end mt-5 mx-auto relative top-8"
      >
        <button
          onClick={filterHandler}
          className="filter-btn bg-black hidden text-white px-6 rounded py-1 font-roboto"
        >
          Filter
        </button>
      </div>

      <div className="filter-tags-mobile-container hidden">
        <div className="filter-tag-mobile ml-4 flex justify-evenly mt-16">
          <div
            style={{ width: "237px" }}
            className="filter-container rounded-lg border border-gray-400 h-80 mb-3"
          >
            <h1 className="font-roboto bg-black text-white w-full text-center h-8 font-semibold pt-1 rounded-t-md">
              Filter
            </h1>
            <form
              onSubmit={handleMobileFilterSubmit}
              style={{ height: "85%" }}
              className="flex flex-col items-center justify-around"
            >
              <select
                className="h-9 rounded-md shadow-lg border-gray-400 border pl-3 pb-1 w-3/4"
                name="country"
                id="country"
                required
                onChange={(e) => (
                  setMobileFilterStateCode(e.target.value),
                  e.target.value == ""
                    ? ""
                    : setMobileFilterCountryName(
                        regionNamesInEnglish.of(e.target.value)
                      )
                )}
              >
                <option value="country" disabled selected>
                  Country
                </option>

                <option value="">{mobileFilterCountryName}</option>

                {mobileFilterCountry.map((data) => (
                  <option
                    className="country-code"
                    value={data.iso2}
                    placeholder={data.name}
                  >
                    {data.name}
                  </option>
                ))}
              </select>
              <select
                className="h-9 rounded-md shadow-lg border border-gray-400 pl-3 pb-1 w-3/4"
                name="state"
                id="state"
                required
                onChange={(e) => (
                  setMobileFilterCityCode(e.target.value),
                  setMobileFilterStateName(e.target.value)
                )}
              >
                <option value="" selected disabled>
                  your state
                </option>

                <option value="">{mobileFilterStateName}</option>
                {mobileFilterState != "" &&
                  Array.from(mobileFilterState).map((data) => (
                    <option value={data.iso2}>{data.name}</option>
                  ))}
              </select>
              <select
                className="h-9 rounded-md shadow-lg border border-gray-400 pl-3 pb-1 w-3/4"
                name="city"
                id="city"
                required
                onChange={(e) => setMobileFilterCityName(e.target.value)}
              >
                <option value="your city" selected disabled>
                  your city
                </option>

                {mobileFilterCity != "" &&
                  Array.from(mobileFilterCity).map((data) => (
                    <option value={data.iso2}>{data.name}</option>
                  ))}
              </select>

              <label
                className="bg-black text-white text-center w-3/4 rounded py-1 text-sm font-roboto"
                htmlFor="mobiledate"
              >
                Date
              </label>

              <input
                className="border border-gray-400 rounded shadow-lg"
                id="mobiledate"
                type="date"
                value={mobileDate}
                onChange={(e) => setMobileDate(e.target.value)}
                placeholder="Date"
              />

              <input
                className="bg-black text-white font-roboto rounded h-9 w-24 font-semibold"
                type="submit"
                value="Apply Filter"
              />
            </form>
          </div>
        </div>
      </div>

      <div className="content flex flex-wrap mt-12 justify-center mb-64">
        <div className="filter-tags ml-4 flex flex-col">
          <div
            style={{ width: "237px" }}
            className="filter rounded-lg border border-gray-400 h-80 mb-3"
          >
            <h1 className="font-roboto bg-black text-white w-full text-center h-8 font-semibold pt-1 rounded-t-md">
              Filter
            </h1>
            <form
              onSubmit={handleDesktopFilterSubmit}
              style={{ height: "85%" }}
              className="flex flex-col items-center justify-around"
            >
              <select
                className="h-9 rounded-md shadow-lg border-gray-400 border pl-3 pb-1 w-3/4"
                name="country"
                id="country"
                required
                onChange={(e) => (
                  setDesktopFilterStateCode(e.target.value),
                  e.target.value == ""
                    ? ""
                    : setDesktopFilterCountryName(
                        regionNamesInEnglish.of(e.target.value)
                      )
                )}
              >
                <option value="country" disabled selected>
                  Country
                </option>

                <option value="">{desktopFilterCountryName}</option>

                {desktopFilterCountry.map((data) => (
                  <option
                    className="country-code"
                    value={data.iso2}
                    placeholder={data.name}
                  >
                    {data.name}
                  </option>
                ))}
              </select>
              <select
                className="h-9 rounded-md shadow-lg border border-gray-400 pl-3 pb-1 w-3/4"
                name="state"
                id="state"
                required
                onChange={(e) => (
                  setDesktopFilterCityCode(e.target.value),
                  setDesktopFilterStateName(e.target.value)
                )}
              >
                <option value="" selected disabled>
                  your state
                </option>

                <option value="">{desktopFilterStateName}</option>
                {desktopFilterState != "" &&
                  Array.from(desktopFilterState).map((data) => (
                    <option value={data.iso2}>{data.name}</option>
                  ))}
              </select>

              <select
                className="h-9 rounded-md shadow-lg border border-gray-400 pl-3 pb-1 w-3/4"
                name="city"
                id="city"
                required
                onChange={(e) => setDesktopFilterCityName(e.target.value)}
              >
                <option value="your city" selected disabled>
                  your city
                </option>

                {desktopFilterCity != "" &&
                  Array.from(desktopFilterCity).map((data) => (
                    <option value={data.iso2}>{data.name}</option>
                  ))}
              </select>

              <label
                className="bg-black text-white text-center w-3/4 rounded py-1 text-sm font-roboto"
                htmlFor="date"
              >
                Date
              </label>

              <input
                className="border border-gray-400 rounded w-3/4 shadow-lg"
                id="date"
                type="date"
                value={date}
                onChange={(e) => setDate(e.target.value)}
                placeholder="Date"
              />

              <input
                className="bg-black text-white font-roboto rounded h-9 w-24 font-semibold"
                type="submit"
                value="Apply Filter"
              />
            </form>
          </div>
          <div
            id="tags"
            className="popular-tags border h-auto w-60 rounded-lg border-gray-400 shadow-2xl"
          >
            <h1 className="font-roboto text-white bg-black text-center rounded-t-md h-9 font-semibold pt-1">
              Popular Tags
            </h1>
            <div className="tags grid grid-cols-2 justify-items-center gap-y-6 content-around px-4 mt-7">
              {tagsSuccess &&
                tags.slice(0, tagSize).map((data) => (
                  <button
                    style={{ fontSize: "9px" }}
                    className="bg-black text-white h-10 w-20 font-semibold rounded"
                    onClick={() =>
                      tagsSuccess && tagSearchSubmitHandler(data.tag)
                    }
                  >
                    {data.tag}
                  </button>
                ))}
            </div>
            {tagsSuccess &&
            tags &&
            tagSize !== tags.length &&
            tags.length > 1 ? (
              <button
                style={{ fontSize: "9px" }}
                className="bg-black text-white font-roboto h-10 w-20 rounded font-semibold mx-auto block mt-5 mb-7"
                onClick={() => setTagSize(tags.length)}
              >
                See all tags
              </button>
            ) : tagsSuccess && !tags && !tagSize === tags.length ? (
              <button
                style={{ fontSize: "9px" }}
                className="bg-black text-white font-roboto h-10 w-20 rounded font-semibold mx-auto block mt-5 mb-7"
                onClick={() => setTagSize(4)}
              >
                See less
              </button>
            ) : (
              ""
            )}
          </div>
        </div>

        <div className="Q-A">
          <div
            style={{ width: "520px" }}
            className="question-options bg-transparent border border-gray-500 mx-auto flex h-8 rounded font-semibold justify-between"
          >
            <input
              type="radio"
              id="recent"
              name="radio-options"
              className="radio-button"
              hidden
            />

            <label
              onClick={() => (
                setRecentQuestionsChecker(true),
                setGeneralQuestionsChecker(false),
                setUnansweredQuestionsChecker(false),
                setTopQuestionsChecker(false),
                setTagsChecker(false)
              )}
              className={`${
                recentQuestionsChecker && "bg-black text-white"
              } rounded font-roboto text-sm h-full py-1 w-3/6 text-center`}
              htmlFor="recent"
            >
              Recent
            </label>

            <input
              type="radio"
              id="unanswered"
              name="radio-options"
              className="radio-button"
              hidden
            />
            <label
              onClick={() => (
                setRecentQuestionsChecker(false),
                setGeneralQuestionsChecker(false),
                setUnansweredQuestionsChecker(true),
                setTopQuestionsChecker(false),
                setTagsChecker(false)
              )}
              className={`${
                unansweredQuestionsChecker && "bg-black text-white"
              } rounded font-roboto text-sm h-full py-1 w-3/6 text-center`}
              htmlFor="unanswered"
            >
              Unanswered
            </label>

            <input
              type="radio"
              id="top"
              name="radio-options"
              hidden
              className="radio-button"
            />
            <label
              onClick={() => (
                setRecentQuestionsChecker(false),
                setGeneralQuestionsChecker(false),
                setUnansweredQuestionsChecker(false),
                setTopQuestionsChecker(true),
                setTagsChecker(false)
              )}
              className={`${
                topQuestionsChecker && "bg-black text-white"
              } rounded font-roboto text-sm py-1 h-full w-3/6 text-center`}
              htmlFor="top"
            >
              Top
            </label>

            <input
              type="radio"
              id="tags"
              name="radio-options"
              hidden
              className="radio-button"
            />
            <label
              className="font-roboto text-sm radio-label h-full py-1 w-3/6 px-1 text-center"
              htmlFor="tags"
            >
              <span
                className="w-full h-full block"
                onClick={() => {
                  const element = document.getElementById("tags");
                  element.scrollIntoView();
                }}
              >
                Tags
              </span>
            </label>
          </div>

          {generalQuestionsChecker && (
            <Questions
              questionLoading={loading}
              questionSuccess={questionsListSuccess}
              questionType={userquestions}
            />
          )}
          {recentQuestionsChecker && (
            <Questions
              questionLoading={recentQuestionsLoading}
              questionSuccess={recentQuestionsSuccess}
              questionType={recentquestions}
            />
          )}
          {unansweredQuestionsChecker && (
            <Questions
              questionLoading={unansweredQuestionsLoading}
              questionSuccess={unansweredQuestionsSuccess}
              questionType={unansweredquestions}
            />
          )}
          {topQuestionsChecker && (
            <Questions
              questionLoading={topQuestionsLoading}
              questionSuccess={topQuestionsSuccess}
              questionType={topquestions}
            />
          )}
        </div>
      </div>
    </div>
  );
}

export default Community;
