import React from "react";
import "./Category.css";
import img1 from "../../../images/lw.jpg";
import img2 from "../../../images/hm.jpg";
import img3 from "../../../images/jb.jpg";
import { LazyLoadImage } from "react-lazy-load-image-component";
import { useHistory } from "react-router-dom";

function Category() {
  const history = useHistory();

  return (
    <div className="bg-gray-300 main-category-container text-gray-600">
      <h1 className="font-semibold text-center text-3xl py-16 bg-gray-300 text-gray-600">
        POPULAR CATEGORIES
      </h1>
      <div
        style={{ width: "550px" }}
        className="category-container flex justify-around mx-auto"
      >
        <div className="">
          <LazyLoadImage
            alt="lw"
            effect="blur"
            src={img1}
            className="h-36 w-36 object-cover rounded"
          />

          <span className="text-center block font-semibold mt-2">Startup</span>
        </div>
        <div>
          <LazyLoadImage
            alt="hm"
            effect="blur"
            src={img2}
            className="h-36 w-36 object-cover rounded"
          />

          <span className="text-center block font-semibold mt-2">
            Trademark
          </span>
        </div>
        <div>
          <LazyLoadImage
            alt="jb"
            effect="blur"
            src={img3}
            className="h-36 w-36 object-cover rounded"
          />

          <span className="text-center block font-semibold mt-2">
            Litigation
          </span>
        </div>
      </div>
      <button
        onClick={() => history.push("/member-categories")}
        className="block mx-auto text-base font-bold my-10 pb-4 bg-transparent text-gray-700"
      >
        See All
      </button>
    </div>
  );
}

export default Category;
