import React, { useState, useEffect } from "react";
import ReactLoading from "react-loading";
import { useHistory } from "react-router-dom";
import { useSelector, useDispatch } from "react-redux";
import ReactPaginate from "react-paginate";
import {
  FacebookShareButton,
  FacebookIcon,
  TwitterShareButton,
  TwitterIcon,
  WhatsappShareButton,
  WhatsappIcon,
} from "react-share";
import {
  listQuestions,
  listUnansweredQuestions,
  listRecentQuestions,
  listTopQuestions,
  updateUserQuestion,
  deleteUserQuestion,
  userAnswer,
  updateUserAnswer,
  deleteUserAnswer,
  userUpvote,
  userDownvote,
  userComment,
  updateUserComment,
  deleteUserComment,
} from "../actions/communityActions";

import { getUserDetails } from "../actions/userActions";
import {
  CREATE_QUESTION_RESET,
  UPDATE_QUESTION_RESET,
  DELETE_QUESTION_RESET,
  CREATE_ANSWER_RESET,
  UPDATE_ANSWER_RESET,
  DELETE_ANSWER_RESET,
  CREATE_DOWNVOTE_RESET,
  CREATE_UPVOTE_RESET,
  CREATE_COMMENT_RESET,
  UPDATE_COMMENT_RESET,
  DELETE_COMMENT_RESET,
} from "../constants/communityConstants";
import userimg from "../images/profile-pic.jpg";

function Questions({ questionLoading, questionSuccess, questionType }) {
  const [tagInput, setTagInput] = useState("");
  const [updateQuestionInput, setUpdateQuestionInput] = useState("");
  const [answerInput, setAnswerInput] = useState("");
  const [updateAnswerInput, setUpdateAnswerInput] = useState("");
  const [questionID, setQuestionID] = useState("");
  const [updateQuestionID, setUpdateQuestionID] = useState("");
  const [backAnswerID, setBackAnswerID] = useState("");
  const [updateAnswerID, setUpdateAnswerID] = useState("");
  const [answersID, setAnswersID] = useState("");
  const [commentID, setCommentID] = useState("");
  const [commentsID, setCommentsID] = useState("");
  const [updateCommentID, setUpdateCommentID] = useState("");
  const [backCommentID, setBackCommentID] = useState("");
  const [tagID, setTagID] = useState("");
  const [shareID, setShareID] = useState("");
  const [commentInput, setCommentInput] = useState("");
  const [updateCommentInput, setUpdateCommentInput] = useState("");
  const [commentBox, setCommentBox] = useState(false);
  const [updateCommentBox, setUpdateCommentBox] = useState(false);
  const [answerBox, setAnswerBox] = useState(false);
  const [updateAnswerBox, setUpdateAnswerBox] = useState(false);
  const [updateQuestionBox, setUpdateQuestionBox] = useState(false);
  const [numUpvotes, setNumUpvotes] = useState("");
  const [numDownvotes, setNumDownvotes] = useState("");
  const [numComments, setNumComments] = useState("");
  const [pageNumber, setPageNumber] = useState(0);

  const mainHistory = useHistory();

  const dispatch = useDispatch();
  let kw = mainHistory.location.search;

  const questionCreated = useSelector((state) => state.questionCreate);

  const { success } = questionCreated;

  const questionUpdated = useSelector((state) => state.questionUpdate);

  const { success: questionUpdatedSuccess } = questionUpdated;

  const questionDeleted = useSelector((state) => state.questionDelete);

  const { success: questionDeletedSuccess } = questionDeleted;

  const answerCreated = useSelector((state) => state.answerCreate);

  const { success: answerCreatedSuccess } = answerCreated;

  const answerUpdated = useSelector((state) => state.answerUpdate);

  const { success: answerUpdatedSuccess } = answerUpdated;

  const answerDeleted = useSelector((state) => state.answerDelete);

  const { success: answerDeletedSuccess } = answerDeleted;

  const upvoteCreated = useSelector((state) => state.upvoteCreate);

  const { success: upvoteCreatedSuccess } = upvoteCreated;

  const downvoteCreated = useSelector((state) => state.downvoteCreate);

  const { success: downvoteCreatedSuccess } = downvoteCreated;

  const commentCreated = useSelector((state) => state.commentCreate);

  const { success: commentCreatedSuccess } = commentCreated;

  const commentUpdated = useSelector((state) => state.commentUpdate);

  const { success: commentUpdatedSuccess } = commentUpdated;

  const commentDeleted = useSelector((state) => state.commentDelete);

  const { success: commentDeletedSuccess } = commentDeleted;

  const userDetail = useSelector((state) => state.userDetails);

  const { user, error: detailError } = userDetail;

  const userLogin = useSelector((state) => state.userLogin);

  const { userInfo, error: userInfoError, success: loginSuccess } = userLogin;

  useEffect(() => {
    if (
      success ||
      questionUpdatedSuccess ||
      answerCreatedSuccess ||
      answerUpdatedSuccess ||
      upvoteCreatedSuccess ||
      downvoteCreatedSuccess ||
      commentCreatedSuccess ||
      commentUpdatedSuccess ||
      questionDeletedSuccess ||
      commentDeletedSuccess ||
      answerDeletedSuccess
    ) {
      dispatch({ type: CREATE_QUESTION_RESET });
      dispatch({ type: UPDATE_QUESTION_RESET });
      dispatch({ type: DELETE_QUESTION_RESET });
      dispatch({ type: CREATE_ANSWER_RESET });
      dispatch({ type: UPDATE_ANSWER_RESET });
      dispatch({ type: DELETE_ANSWER_RESET });
      dispatch({ type: CREATE_UPVOTE_RESET });
      dispatch({ type: CREATE_DOWNVOTE_RESET });
      dispatch({ type: CREATE_COMMENT_RESET });
      dispatch({ type: UPDATE_COMMENT_RESET });
      dispatch({ type: DELETE_COMMENT_RESET });
    } else {
      dispatch(listQuestions(kw));
      dispatch(listUnansweredQuestions(kw));
      dispatch(listRecentQuestions(kw));
      dispatch(listTopQuestions(kw));
      dispatch(getUserDetails("profile"));
    }
  }, [
    dispatch,
    success,
    questionUpdatedSuccess,
    questionDeletedSuccess,
    answerCreatedSuccess,
    answerUpdatedSuccess,
    upvoteCreatedSuccess,
    downvoteCreatedSuccess,
    commentCreatedSuccess,
    commentUpdatedSuccess,
    commentDeletedSuccess,
    answerDeletedSuccess,
    kw,
  ]);

  function updateQuestionSubmitHandler(e) {
    e.preventDefault();
    dispatch(updateUserQuestion(updateQuestionID, updateQuestionInput));
    setUpdateQuestionBox(false);
    setUpdateQuestionID("");
  }

  function deleteQuestionSubmitHandler(id) {
    dispatch(deleteUserQuestion(id));
  }

  function answerSubmitHandler(e) {
    e.preventDefault();
    dispatch(
      userAnswer(
        questionID,
        answerInput,
        numUpvotes,
        numDownvotes,
        numComments,
        tagInput
      )
    );

    setAnswersID(questionID);
    setBackAnswerID(questionID);
    setAnswerInput("");
    setQuestionID("");
    setTagID("");
  }

  function updateAnswerSubmitHandler(e) {
    e.preventDefault();
    dispatch(updateUserAnswer(updateAnswerID, updateAnswerInput));
    setUpdateAnswerInput("");
    setUpdateAnswerID("");
    setCommentsID("");
  }

  function deleteAnswerSubmitHandler(id) {
    dispatch(deleteUserAnswer(id));
  }

  function addUpvoteSubmitHandler(qid, id) {
    dispatch(userUpvote(qid, id, 1));
  }

  function addDownvoteSubmitHandler(qid, id) {
    dispatch(userDownvote(qid, id, 1));
  }

  function addCommentSubmitHandler(qid, id) {
    dispatch(userComment(qid, id, commentInput));
    setCommentInput("");
    setCommentsID(commentID);
    setBackCommentID(commentID);
    setCommentID("");
  }

  function updateCommentSubmitHandler(e) {
    e.preventDefault();
    dispatch(updateUserComment(updateCommentID, updateCommentInput));
    setUpdateCommentID("");
  }

  function deleteCommentSubmitHandler(id) {
    dispatch(deleteUserComment(id));
  }

  const questionsPerPage = 2;
  const pageCount =
    questionSuccess && Math.ceil(questionType.length / questionsPerPage);
  const pagesVisited = pageNumber * questionsPerPage;

  const changePage = ({ selected }) => {
    setPageNumber(selected);
  };

  return (
    <div>
      <div className="cont">
        {questionLoading ? (
          <div className="flex justify-center items-center mt-12">
            <ReactLoading type="spin" color="#000" height={40} width={40} />
          </div>
        ) : (
          questionSuccess &&
          !questionLoading &&
          questionType
            .slice(0, questionType.length)
            .slice(pagesVisited, pagesVisited + questionsPerPage)
            .map((data) => {
              return (
                <div className="parent">
                  <div
                    key={data._id}
                    style={{ width: "510px" }}
                    className="answer1-container border1 border-gray-500 rounded-xl shadow-2xl mb-5 pt-3"
                  >
                    <div className="answer1-content flex mt-2">
                      <img
                        style={{ height: "50px", width: "50px" }}
                        className="object-cover rounded-full mx-5"
                        src={
                          data.userImage == null || data.userImage == ""
                            ? userimg
                            : data.userImage
                        }
                        alt="icon"
                      />
                      <div
                        style={{ width: "345px" }}
                        className="font-roboto answer1-header"
                      >
                        <h1 className="question-text font-semibold">
                          {data.question}
                        </h1>
                        <div className="question-details relative -top-2">
                          <h3 className="text-xs text-gray-500 inline-block">
                            asked by {data.name}
                          </h3>
                          <div className="date-header text-xs text-gray-500 relative -top-1">
                            <h5 className="mr-1 inline-block">
                              {new Date(
                                data.askedAt.substring(0, 10)
                              ).toLocaleString("default", {
                                month: "long",
                              })}
                            </h5>
                            <h5 className="inline-block">
                              {new Date(
                                data.askedAt.substring(0, 10)
                              ).getDate()}
                            </h5>
                          </div>
                        </div>
                      </div>

                      {localStorage.getItem("userInfo")
                        ? JSON.parse(localStorage.getItem("userInfo")).id ==
                            data.user && (
                            <div className="more-container mt-1">
                              <ul className="buttons border border-gray-400 rounded shadow-xl flex">
                                <li className="text-xs mx-2 my-1 cursor-pointer">
                                  <i
                                    class="fas fa-pen text-gray-600"
                                    onClick={() => (
                                      setUpdateQuestionID(data._id),
                                      setUpdateQuestionBox(true),
                                      setQuestionID(""),
                                      setUpdateQuestionInput(data.question)
                                    )}
                                  ></i>
                                </li>

                                <li className="text-xs mx-2 my-1 cursor-pointer">
                                  <i
                                    onClick={() =>
                                      deleteQuestionSubmitHandler(data._id)
                                    }
                                    class="fas fa-trash-alt text-gray-600"
                                  ></i>
                                </li>
                              </ul>
                            </div>
                          )
                        : ""}
                    </div>
                    <div className="flex justify-end mr-4">
                      {questionID != data._id &&
                      !detailError &&
                      localStorage.getItem("userInfo")
                        ? JSON.parse(localStorage.getItem("userInfo")).id !=
                            data.user &&
                          user.id != data.user && (
                            <button
                              style={{ left: "85%" }}
                              className="answer font-roboto py-1 px-2 text-xs border rounded border-gray-500 outline-none bg-gray-600 text-white"
                              onClick={() =>
                                localStorage.getItem("userInfo")
                                  ? JSON.parse(localStorage.getItem("userInfo"))
                                      .id != data.user &&
                                    (setQuestionID(data._id),
                                    setAnswerBox(true),
                                    setUpdateQuestionID(""),
                                    setNumComments(data.comments.length),
                                    setNumUpvotes(data.upvotes.length),
                                    setNumDownvotes(data.downvotes.length))
                                  : mainHistory.push("/signin")
                              }
                            >
                              Answer
                            </button>
                          )
                        : !JSON.parse(localStorage.getItem("userInfo")) && (
                            <div className="flex justify-end mr-12">
                              <button
                                style={{ left: "85%" }}
                                className="answer relative font-roboto py-1 px-2 text-xs border rounded border-gray-500 outline-none bg-gray-600 text-white"
                                onClick={() => mainHistory.push("/signin")}
                              >
                                Answer
                              </button>
                            </div>
                          )}
                    </div>
                    {questionID == data._id && answerBox && (
                      <div>
                        <form
                          onSubmit={answerSubmitHandler}
                          className="relative rounded m-3"
                        >
                          <div className="answer-cancel-icon flex justify-end">
                            <i
                              class="fas fa-times text-lg text-gray-600 cursor-pointer"
                              onClick={() => setQuestionID("")}
                            ></i>
                          </div>

                          <textarea
                            rows="8"
                            className="answer-1 border border-gray-400 rounded outline-none w-full px-3 py-3 mt-3 font-roboto text-sm"
                            onChange={(e) => setAnswerInput(e.target.value)}
                          ></textarea>

                          {tagID == data._id && (
                            <div className="relative rounded m-3">
                              <div className="tags-cancel-icon flex">
                                <i
                                  onClick={() => setTagID("")}
                                  class="fas fa-times text-lg text-gray-600 cursor-pointer"
                                ></i>
                              </div>

                              <textarea
                                rows="2"
                                value={tagInput}
                                onChange={(e) => setTagInput(e.target.value)}
                                placeholder="seperate each tag with a comma"
                                className="answer-1 border border-gray-400 rounded outline-none w-full px-3 py-3 mt-3 font-roboto text-xs"
                              ></textarea>
                            </div>
                          )}

                          {tagID != data._id && (
                            <button
                              onClick={() => setTagID(data._id)}
                              className="answer relative font-roboto py-1 mb-3 px-2 text-xs border rounded border-gray-500 outline-none bg-gray-600 text-white"
                            >
                              Add Tags <i class="fas fa-tags"></i>
                            </button>
                          )}
                          <div className="answer-submit-btn flex justify-end">
                            <input
                              type="submit"
                              className="bg-black text-white text-xs px-3 py-1 rounded mb-2"
                              value="Submit"
                            />
                          </div>
                        </form>
                      </div>
                    )}

                    {answersID != data._id &&
                      questionID != data._id &&
                      updateQuestionID != data._id && (
                        <button
                          className="bg-gray-600 text-center text-sm font-roboto text-white font-semibold h-8 py-1 w-full rounded-b-lg mt-3"
                          onClick={() => (
                            data.answers.length >setBackAnswerID(data._id), setAnswersID(data._id)
                          )}
                        >
                          <span className="text-sm mr-1">
                            {data.answers.length}
                          </span>
                          Answers
                        </button>
                      )}

                    {backAnswerID == data._id &&
                      questionID != data._id &&
                      updateQuestionID != data._id && (
                        <button
                          className="bg-gray-600 text-center font-roboto text-white font-semibold h-8 py-1 w-full rounded-b-lg mt-3"
                          onClick={() => (
                            setBackAnswerID(""), setAnswersID("")
                          )}
                        >
                          <i class="fas fa-arrow-left mr-2"></i>
                          back
                        </button>
                      )}

                    {updateQuestionID == data._id && updateQuestionBox && (
                      <form
                        onSubmit={updateQuestionSubmitHandler}
                        className="update-box rounded m-3 relative"
                      >
                        <div className="icon-frame flex justify-end">
                          <i
                            class="fas fa-times text-lg text-gray-600 cursor-pointer update-cancel-icon"
                            onClick={() => (
                              setUpdateQuestionID(""),
                              setUpdateQuestionBox(false)
                            )}
                          ></i>
                        </div>

                        <textarea
                          rows="8"
                          className="answer-1 border border-gray-400 rounded outline-none w-full px-3 py-3 mt-3 font-roboto text-sm"
                          value={updateQuestionInput}
                          onChange={(e) =>
                            setUpdateQuestionInput(e.target.value)
                          }
                        ></textarea>
                        <div className="update-question-btn flex justify-end">
                          <input
                            type="submit"
                            className="bg-black text-white text-xs px-3 py-1 rounded mb-2 cursor-pointe"
                            value="Update"
                          />
                        </div>
                      </form>
                    )}
                  </div>

                  {answersID == data._id &&
                    backAnswerID == data._id &&
                    data.answers.map((response) => {
                      return (
                        <div
                          style={{ width: "510px" }}
                          className="answer1-container  border-gray-500 rounded-xl shadow-2xl mb-1 pt-3"
                        >
                          <div className="answer1-content flex mt-2">
                            <img
                              style={{ height: "35px", width: "35px" }}
                              className="object-cover rounded-full ml-6 mr-4"
                              src={
                                response.userImage == null ||
                                response.userImage == ""
                                  ? userimg
                                  : response.userImage
                              }
                              alt="icon"
                            />
                            <div
                              style={{ width: "350px" }}
                              className="font-roboto"
                            >
                              <h1 className="font-semibold mr-1">
                                Answered by {response.name}
                              </h1>
                              <h3 className="answer-date-header inline-block text-xs text-gray-500 relative -top-2">
                                <span className="mr-1">
                                  {new Date(
                                    response.answeredAt.substring(0, 10)
                                  ).toLocaleString("default", {
                                    month: "long",
                                  })}
                                </span>
                                <span>
                                  {new Date(
                                    response.answeredAt.substring(0, 10)
                                  ).getDate()}
                                </span>
                              </h3>
                            </div>
                            {localStorage.getItem("userInfo")
                              ? JSON.parse(localStorage.getItem("userInfo"))
                                  .id == response.user && (
                                  <div className="icons mx-2">
                                    <ul className="buttons border border-gray-400 rounded shadow-xl flex ">
                                      <li className="text-xs mx-2 my-1 cursor-pointer">
                                        <i
                                          class="fas fa-pen text-gray-600"
                                          onClick={() => (
                                            setUpdateAnswerID(response._id),
                                            setUpdateAnswerBox(true),
                                            setUpdateAnswerInput(
                                              response.answer
                                            ),
                                            setCommentID(""),
                                            setBackCommentID(""),
                                            setCommentsID(response._id)
                                          )}
                                        ></i>
                                      </li>

                                      <li className="text-xs mx-2 my-1 cursor-pointer">
                                        <i
                                          onClick={() =>
                                            deleteAnswerSubmitHandler(
                                              response._id
                                            )
                                          }
                                          class="fas fa-trash-alt text-gray-600"
                                        ></i>
                                      </li>
                                    </ul>
                                  </div>
                                )
                              : ""}
                          </div>

                          <div className="anwser-content">
                            <p className="font-roboto text-sm px-3 pt-3 relative left-4">
                              {response.answer}
                            </p>
                            <div className="icons-container flex justify-between">
                              <div className="icons flex justify-between w-56 ml-7 mt-2 ">
                                <div className="icon-arrow-up">
                                  <i
                                    onClick={() =>
                                      addUpvoteSubmitHandler(
                                        data._id,
                                        response._id
                                      )
                                    }
                                    class="fas fa-arrow-circle-up text-xl mr-1"
                                  ></i>
                                  <span className="text-xs text-gray-600 relative -top-1">
                                    {response.upvotes.length}
                                  </span>
                                </div>
                                <div className="icon-arrow-down">
                                  <i
                                    onClick={() =>
                                      addDownvoteSubmitHandler(
                                        data._id,
                                        response._id
                                      )
                                    }
                                    class="fas fa-arrow-circle-down text-xl mr-1"
                                  ></i>
                                  <span className="text-xs text-gray-600 relative -top-1">
                                    {response.downvotes.length}
                                  </span>
                                </div>
                                <div className="icon-chat">
                                  <i class="fas fa-comment-alt text-lg mr-1"></i>
                                  <span className="text-xs text-gray-600 relative -top-1">
                                    {response.comments.length}
                                  </span>
                                </div>
                                <div className="icon-share">
                                  {shareID == response._id && (
                                    <div className="all-icons flex justify-around w-14">
                                      <FacebookShareButton
                                        url={window.location.href}
                                      >
                                        <FacebookIcon
                                          className="rounded"
                                          size={17}
                                        />
                                      </FacebookShareButton>
                                      <TwitterShareButton
                                        url={window.location.href}
                                      >
                                        <TwitterIcon
                                          className="rounded"
                                          size={17}
                                        />
                                      </TwitterShareButton>
                                      <WhatsappShareButton
                                        url={window.location.href}
                                      >
                                        <WhatsappIcon
                                          className="rounded"
                                          size={17}
                                        />
                                      </WhatsappShareButton>
                                    </div>
                                  )}
                                  {shareID != response._id && (
                                    <i
                                      onClick={() => setShareID(response._id)}
                                      class="fas fa-share text-lg"
                                    ></i>
                                  )}

                                  {shareID == response._id && (
                                    <i
                                      onClick={() => setShareID("")}
                                      class="fas fa-times text-xs text-gray-600 cursor-pointer ml-6"
                                    ></i>
                                  )}
                                </div>
                              </div>

                              {commentID != response._id && (
                                <button
                                  onClick={() =>
                                    userInfo
                                      ? (setCommentID(response._id),
                                        setCommentBox(true),
                                        setUpdateAnswerID(""),
                                        setUpdateCommentID(""),
                                        setCommentsID(""),
                                        setUpdateCommentBox(false),
                                        setBackCommentID(""))
                                      : mainHistory.push("/signin")
                                  }
                                  className="comment-btn mr-3 mt-2 h-7 answer font-roboto py-1 px-2 text-xs border rounded border-gray-500 outline-none bg-gray-600 text-white"
                                >
                                  Comment
                                </button>
                              )}
                            </div>
                          </div>

                          {commentID == response._id && commentBox && (
                            <div className="comment-form">
                              <form
                                onSubmit={(e) => (
                                  e.preventDefault(),
                                  addCommentSubmitHandler(
                                    data._id,
                                    response._id
                                  )
                                )}
                                className="m-3"
                              >
                                <div className="comment-cancel-icon flex justify-end">
                                  <i
                                    onClick={() => (
                                      setCommentID(""), setCommentBox(false)
                                    )}
                                    class="fas fa-times text-lg text-gray-600 cursor-pointer"
                                  ></i>
                                </div>

                                <textarea
                                  rows="3"
                                  className="comment-field border border-gray-400 rounded outline-none w-full px-3 py-3 mt-3 font-roboto text-sm"
                                  value={commentInput}
                                  onChange={(e) =>
                                    setCommentInput(e.target.value)
                                  }
                                ></textarea>
                                <div className="comment-submit-btn flex justify-end">
                                  <input
                                    className="bg-black text-white text-xs font-roboto p-1 rounded mt-3 mb-2"
                                    type="submit"
                                    value="Comment"
                                  />
                                </div>
                              </form>
                            </div>
                          )}

                          {updateAnswerID == response._id &&
                            updateAnswerBox && (
                              <form
                                onSubmit={updateAnswerSubmitHandler}
                                className="relative rounded m-3"
                              >
                                <div className="update-answer-cancel-icon flex justify-end">
                                  <i
                                    class="fas fa-times text-lg text-gray-600 cursor-pointer relative"
                                    onClick={() => (
                                      setUpdateAnswerID(""),
                                      setUpdateAnswerBox(false),
                                      setCommentsID("")
                                    )}
                                  ></i>
                                </div>

                                <textarea
                                  rows="8"
                                  className="answer-1 border border-gray-400 rounded outline-none w-full px-3 py-3 mt-3 font-roboto text-sm"
                                  value={updateAnswerInput}
                                  onChange={(e) =>
                                    setUpdateAnswerInput(e.target.value)
                                  }
                                ></textarea>

                                <div className="update-answer-submit-btn flex justify-end">
                                  <input
                                    style={{ marginLeft: "425px" }}
                                    type="submit"
                                    className="bg-black text-white text-xs px-3 py-1 rounded mb-2 cursor-pointer"
                                    value="Update"
                                  />
                                </div>
                              </form>
                            )}

                          {commentsID != response._id &&
                            commentID != response._id &&
                            updateCommentID != response._id && (
                              <button
                                onClick={() => (
                                  setBackCommentID(response._id),
                                  setCommentsID(response._id)
                                )}
                                className="bg-gray-600 text-center font-roboto text-white font-semibold h-8 py-1 w-full rounded-b-lg mt-3 text-sm"
                              >
                                Comments
                              </button>
                            )}

                          {backCommentID == response._id &&
                            commentID != response._id &&
                            updateCommentID != response._id && (
                              <button
                                onClick={() => (
                                  setBackCommentID(""), setCommentsID("")
                                )}
                                className="bg-gray-600 text-center font-roboto text-white font-semibold h-8 py-1 w-full rounded-b-lg mt-3"
                              >
                                <i class="fas fa-arrow-left mr-1"></i> back
                              </button>
                            )}

                          {commentsID == response._id &&
                            backCommentID == response._id &&
                            response.comments.map((data) => (
                              <div className="mt-8">
                                <div className="answer1-content flex mt-2">
                                  <img
                                    style={{
                                      height: "30px",
                                      width: "30px",
                                    }}
                                    className="object-cover rounded-full mt-1 ml-8 mr-4"
                                    src={
                                      data.userImage == null ||
                                      data.userImage == ""
                                        ? userimg
                                        : data.userImage
                                    }
                                    alt="icon"
                                  />
                                  <div
                                    style={{ width: "356px" }}
                                    className="font-roboto answer1-header"
                                  >
                                    <h1 className="font-semibold">
                                      {data.comment}
                                    </h1>
                                    <h3 className="comment-user inline-block text-xs text-gray-500 relative -top-2">
                                      comment by {data.name}
                                    </h3>
                                    <h3 className="comment-date block text-xs text-gray-500 relative -top-3">
                                      <span className="mr-1">
                                        {new Date(
                                          data.commentAt.substring(0, 10)
                                        ).toLocaleString("default", {
                                          month: "long",
                                        })}
                                      </span>
                                      <span>
                                        {new Date(
                                          data.commentAt.substring(0, 10)
                                        ).getDate()}
                                      </span>
                                    </h3>
                                  </div>
                                  {localStorage.getItem("userInfo")
                                    ? JSON.parse(
                                        localStorage.getItem("userInfo")
                                      ).id == data.user && (
                                        <div className="more-container mt-2">
                                          <ul className="buttons border border-gray-400 rounded shadow-xl flex">
                                            <li className="text-xs mx-2 my-1 cursor-pointer">
                                              <i
                                                onClick={() => (
                                                  setUpdateCommentID(data._id),
                                                  setUpdateCommentBox(true),
                                                  setCommentID(data._id),
                                                  setUpdateCommentInput(
                                                    data.comment
                                                  )
                                                )}
                                                class="fas fa-pen text-gray-600"
                                              ></i>
                                            </li>

                                            <li className="text-xs mx-2 my-1 cursor-pointer">
                                              <i
                                                onClick={() =>
                                                  deleteCommentSubmitHandler(
                                                    data._id
                                                  )
                                                }
                                                class="fas fa-trash-alt text-gray-600"
                                              ></i>
                                            </li>
                                          </ul>
                                        </div>
                                      )
                                    : ""}
                                </div>
                                {updateCommentID != data._id && (
                                  <button className="bg-gray-600 text-center font-roboto text-white font-semibold h-8 py-2 w-full rounded-b-lg mt-3"></button>
                                )}

                                {updateCommentID == data._id &&
                                  updateCommentBox && (
                                    <form
                                      onSubmit={updateCommentSubmitHandler}
                                      className="relative rounded m-3"
                                    >
                                      <i
                                        onClick={() => (
                                          setUpdateCommentID(""),
                                          setCommentID("")
                                        )}
                                        style={{
                                          left: "470px",
                                          top: "10px",
                                        }}
                                        class="fas fa-times text-lg text-gray-600 cursor-pointer relative"
                                      ></i>
                                      <textarea
                                        value={updateCommentInput}
                                        onChange={(e) =>
                                          setUpdateCommentInput(e.target.value)
                                        }
                                        rows="3"
                                        className="answer-1 border border-gray-400 rounded outline-none w-full px-3 py-3 mt-3 font-roboto text-sm"
                                      ></textarea>

                                      <input
                                        style={{ marginLeft: "425px" }}
                                        type="submit"
                                        className="bg-black text-white text-xs px-3 py-1 rounded mb-2 cursor-pointer"
                                        value="Update"
                                      />
                                    </form>
                                  )}
                              </div>
                            ))}
                        </div>
                      );
                    })}
                </div>
              );
            })
        )}
      </div>
      {questionType && questionType.length != 0 && (
        <ReactPaginate
          previousLabel={"Previous"}
          nextLabel={"Next"}
          pageCount={pageCount}
          onPageChange={changePage}
          containerClassName={"pagination-buttons"}
          previousLinkClassName={"previousBttn"}
          nextLinkClassName={"nextBttn"}
          disabledClassName={"pagination-disabled"}
          activeClassName={"pagination-active"}
        />
      )}
    </div>
  );
}

export default Questions;
