import React, { useEffect, useRef } from "react";
import ReactLoading from "react-loading";

import "./Scroller.css";
import Card from "../Card";
import userimg from "../../images/profile-pic.jpg";
import { useDispatch, useSelector } from "react-redux";
import { membersList } from "../../actions/chamberActions";
import { Swiper, SwiperSlide } from "swiper/react";
import "swiper/swiper.min.css";
import { Link } from "react-router-dom";

import SwiperCore, { Autoplay } from "swiper";

SwiperCore.use([Autoplay]);

function Scroller(props) {
  const swiperRef = useRef(null);
  const dispatch = useDispatch();
  const membersListContainer = useSelector((state) => state.membersListing);
  const { error, loading, members, success } = membersListContainer;

  // useEffect(() => {
  //   members.map(
  //     (data) =>
  //       data.topLawyerOfTheMonth && setTopLawyersLength((prev) => prev + 1)
  //   );
  // }, [success]);

  useEffect(() => {
    dispatch(membersList());
  }, [dispatch]);

  return (
    <div>
      {loading ? (
        <div className="flex justify-center items-center mb-12">
          <ReactLoading type="spin" color="#000" height={50} width={50} />
        </div>
      ) : error ? (
        <h2 className=" w-1/2 mx-auto text-center text-sm font-roboto text-red-600 bg-red-50 my-5 p-3">
          {error}
        </h2>
      ) : (
        <div
          onMouseEnter={() => swiperRef.current.swiper.autoplay.stop()}
          onMouseLeave={() => swiperRef.current.swiper.autoplay.start()}
          className="cards-container flex overflow-x-scroll items-center"
        >
          <Swiper
            ref={swiperRef}
            spaceBetween={10}
            speed={500}
            slidesPerView={3}
            autoplay={{ delay: 1000 }}
            breakpoints={{
              150: {
                width: 150,
                slidesPerView: 1,
                spaceBetween: 10,
              },

              200: {
                width: 200,
                slidesPerView: 1,
                spaceBetween: 10,
              },
              300: {
                width: 300,
                slidesPerView: 1,
                spaceBetween: 0,
              },
              560: {
                width: 560,
                slidesPerView: 1,
                spaceBetween: 8,
              },

              760: {
                width: 760,
                slidesPerView: 1.3,
                spaceBetween: 6,
              },
              1150: {
                width: 1150,
                slidesPerView: 2,
                spaceBetween: -6,
              },
            }}
          >
            {success &&
              members.map(
                (response) =>
                  response.topLawyerOfTheMonth &&
                  response.is_verified && (
                    <SwiperSlide>
                      <Card
                        className="cards"
                        name={response.name}
                        category={response.category}
                        country={response.country}
                        state={response.state}
                        city={response.city}
                        image={
                          response.profile_image === null ||
                          response.profile_image === ""
                            ? userimg
                            : response.profile_image
                        }
                        id={response._id}
                        chamberId={response.member}
                        activeStatus={response.online}
                        areas={response.practiceareas}
                        about={response.description}
                        verifiedStatus={response.is_verified}
                      />
                    </SwiperSlide>
                  )
              )}
          </Swiper>
        </div>
      )}

      {members && members.length > 1 && (
        <button className="mx-auto block text-base font-bold my-20 bg-transparent">
          <Link to="/findmembers">See All</Link>
        </button>
      )}
    </div>
  );
}

export default Scroller;
