import React, { useState, useEffect } from "react";
import { Link, useHistory } from "react-router-dom";
import OutsideClickHandler from "react-outside-click-handler";
import "./Navbar.css";
import { useSelector, useDispatch } from "react-redux";
import { logout } from "../../actions/userActions";
import {
  getLawyerAlerts,
  getQuestionAlerts,
  getRecentQuestionAlerts,
  deleteLawyerAlerts,
  deleteQuestionAlerts,
  deleteRecentQuestionAlerts,
  getPlanAlerts,
  deletePlanAlerts,
} from "../../actions/notifyActions";

import { membersList } from "../../actions/chamberActions";
import { listRecentQuestions } from "../../actions/communityActions";
import {
  DELETE_LAWYER_NOTIFY_RESET,
  DELETE_QUESTION_NOTIFY_RESET,
  DELETE_RECENT_QUESTION_NOTIFY_RESET,
  DELETE_PLAN_NOTIFY_RESET,
} from "../../constants/notifyConstants";

import { MEMBER_LOGOUT_RESET } from "../../constants/userConstants";
import logoimg from "../../images/logos/llogo.png";
import userpic from "../../images/profile-pic.jpg";

function Navbar() {
  const [communityNum, setCommunityNum] = useState(0);
  const [visible, setVisible] = useState(false);
  const [notifyVisible, setNotifyVisible] = useState(false);
  const [memberVerified, setMemberVerified] = useState(false);
  const [alertList, setAlertList] = useState([]);

  function handleClick() {
    let hamburger = document.querySelector(".hamburger-menu");
    let nav = document.querySelector(".nav-container");
    hamburger.classList.toggle("active");
    nav.classList.toggle("move");
  }

  const dispatch = useDispatch();
  const history = useHistory();

  const userLogin = useSelector((state) => state.userLogin);
  const { userInfo, success } = userLogin;

  const memberListing = useSelector((state) => state.membersListing);
  const { members, success: memberListingSuccess } = memberListing;

  const memberLoggedout = useSelector((state) => state.memberLogout);
  const { success: memberLoggedOutSuccess } = memberLoggedout;

  const lawyerAlerts = useSelector((state) => state.lawyerNotifications);
  const { notifications: lawyerfications, success: lawyerficationSuccess } =
    lawyerAlerts;

  const removedLawyerAlerts = useSelector(
    (state) => state.deleteLawyerNotifications
  );
  const { success: removedLawyerficationSuccess } = removedLawyerAlerts;

  const planAlerts = useSelector((state) => state.planNotifications);
  const { notifications: planfications, success: planficationSuccess } =
    planAlerts;

  const removedPlanAlerts = useSelector(
    (state) => state.deletePlanNotifications
  );
  const { success: removedPlanficationSuccess } = removedPlanAlerts;

  const questionAlerts = useSelector((state) => state.questionNotifications);
  const { notifications: questionfications, success: questionficationSuccess } =
    questionAlerts;

  const recentQuestionAlerts = useSelector(
    (state) => state.recentQuestionNotifications
  );
  const {
    notifications: recentQuestionfications,
    success: recentQuestionficationSuccess,
  } = recentQuestionAlerts;

  const removedQuestionAlerts = useSelector(
    (state) => state.deleteQuestionNotifications
  );
  const { success: removedQuestionficationSuccess } = removedQuestionAlerts;

  const removedRecentQuestionAlerts = useSelector(
    (state) => state.deleteRecentQuestionNotifications
  );
  const { success: removedRecentQuestionficationSuccess } =
    removedRecentQuestionAlerts;

  const logoutHandler = () => {
    dispatch(logout());
    history.push("/");
  };

  useEffect(() => {
    if (
      removedLawyerficationSuccess ||
      removedPlanficationSuccess ||
      removedQuestionficationSuccess ||
      removedRecentQuestionficationSuccess ||
      memberLoggedOutSuccess
    ) {
      dispatch({ type: DELETE_LAWYER_NOTIFY_RESET });
      dispatch({ type: DELETE_QUESTION_NOTIFY_RESET });
      dispatch({ type: DELETE_RECENT_QUESTION_NOTIFY_RESET });
      dispatch({ type: MEMBER_LOGOUT_RESET });
      dispatch({ type: DELETE_PLAN_NOTIFY_RESET });
    } else {
      try {
        dispatch(getLawyerAlerts());
        dispatch(getPlanAlerts());
        dispatch(membersList());
        dispatch(getQuestionAlerts(userInfo?.id));
        dispatch(listRecentQuestions());
        dispatch(getRecentQuestionAlerts(userInfo?.id));
      } catch (e) {
        console.log(e);
      }
    }
  }, [
    removedLawyerficationSuccess,
    removedPlanficationSuccess,
    removedQuestionficationSuccess,
    removedRecentQuestionficationSuccess,
    memberLoggedOutSuccess,
  ]);

  useEffect(() => {
    try {
      questionficationSuccess &&
        lawyerficationSuccess &&
        planficationSuccess &&
        recentQuestionficationSuccess &&
        setCommunityNum(alertList.length);
    } catch (e) {
      return;
    }
  }, [
    questionficationSuccess,
    lawyerficationSuccess,
    planficationSuccess,
    recentQuestionficationSuccess,
    communityNum,
  ]);

  const removedAlertsHandler = () => {
    try {
      dispatch(deleteLawyerAlerts());
      dispatch(deletePlanAlerts());
      dispatch(deleteQuestionAlerts(userInfo.id));
      dispatch(deleteRecentQuestionAlerts(userInfo.id));
      setNotifyVisible(false);
    } catch (e) {
      return;
    }
  };

  useEffect(() => {
    try {
      members.find(
        (data) =>
          data.member == userInfo.id &&
          data.is_verified &&
          setMemberVerified(true)
      );
    } catch (e) {
      return;
    }
  }, [memberListingSuccess, success]);

  useEffect(() => {
    try {
      setAlertList([
        ...lawyerfications,
        ...planfications,
        ...recentQuestionfications,
        ...questionfications,
      ]);
    } catch (e) {
      return e;
    }
  }, [
    lawyerficationSuccess,
    planficationSuccess,
    questionficationSuccess,
    recentQuestionficationSuccess,
  ]);

  return (
    <div className="relative z-50 main-nav-container">
      <div className="">
        <img
          onClick={() => history.push("/")}
          src={logoimg}
          alt="logo"
          className="hidden object-contain cursor-pointer h-36 w-36 mobile"
        />
      </div>

      <div
        className="absolute z-40 hidden hamburger-menu right-4 top-2"
        onClick={handleClick}
      >
        <svg
          xmlns="http://www.w3.org/2000/svg"
          width="50"
          height="50"
          viewBox="0 0 200 200"
        >
          <g stroke-width="6.5" stroke-linecap="round">
            <path
              d="M72 82.286h28.75"
              fill="#009100"
              fill-rule="evenodd"
              stroke="#fff"
            />
            <path
              d="M100.75 103.714l72.482-.143c.043 39.398-32.284 71.434-72.16 71.434-39.878 0-72.204-32.036-72.204-71.554"
              fill="none"
              stroke="#fff"
            />
            <path
              d="M72 125.143h28.75"
              fill="#009100"
              fill-rule="evenodd"
              stroke="#fff"
            />
            <path
              d="M100.75 103.714l-71.908-.143c.026-39.638 32.352-71.674 72.23-71.674 39.876 0 72.203 32.036 72.203 71.554"
              fill="none"
              stroke="#fff"
            />
            <path
              d="M100.75 82.286h28.75"
              fill="#009100"
              fill-rule="evenodd"
              stroke="#fff"
            />
            <path
              d="M100.75 125.143h28.75"
              fill="#009100"
              fill-rule="evenodd"
              stroke="#fff"
            />
          </g>
        </svg>
      </div>
      <div
        style={{ width: "1300px" }}
        className="flex items-center pb-4 mx-auto nav-container"
      >
        <Link to="/">
          <img
            className="relative h-36 left-10 desktop"
            src={logoimg}
            alt="logo"
          />
        </Link>

        <ul
          style={{ width: "80%" }}
          className="relative flex justify-around font-semibold text-white nav-items"
        >
          <li className="">
            <Link to="/">Home</Link>
          </li>

          <li className="">
            <Link to="/findmembers">Find lawyers</Link>
          </li>

          <li className="">
            <Link to="/community">Ask the Community</Link>
          </li>

          <li className="">
            <Link to="/member-categories">Categories</Link>
          </li>

          <li className="">
            <Link to="/lawbooks">Law Books</Link>
          </li>

          <li>
            <Link to="/blog">Blog</Link>
          </li>

          <li>
            <Link to="/pricing">Pricing</Link>
          </li>

          {userInfo && (
            <OutsideClickHandler
              onOutsideClick={() => {
                setNotifyVisible(false);
              }}
            >
              <li className="relative">
                {communityNum != 0 && (
                  <h4
                    onClick={() =>
                      notifyVisible
                        ? setNotifyVisible(false)
                        : setNotifyVisible(true)
                    }
                    style={{ fontSize: ".5rem" }}
                    className="inline h-3 p-1 bg-brandred rounded-full text-white absolute top-0.5 cursor-pointer"
                  >
                    <span className="relative bottom-1">
                      {questionficationSuccess &&
                      lawyerficationSuccess &&
                      recentQuestionficationSuccess &&
                      communityNum >= 1000
                        ? "1k..."
                        : communityNum}
                    </span>
                  </h4>
                )}

                <i
                  onClick={() =>
                    notifyVisible
                      ? setNotifyVisible(false)
                      : setNotifyVisible(true)
                  }
                  className="text-lg cursor-pointer fas fa-bell bell-icon"
                ></i>

                {notifyVisible && (
                  <div className="w-full main-content">
                    <div className="absolute pt-3 text-sm bg-gray-100 rounded notification-list w-72 -left-36">
                      <div className="notify-heading">
                        <div className="flex justify-between notify-sub-heading">
                          <h4 className="pb-1 mx-3 text-xs text-black">
                            NOTIFICATIONS
                          </h4>
                          {communityNum != 0 && (
                            <h4
                              onClick={removedAlertsHandler}
                              className="mx-3 text-xs text-blue-700 cursor-pointer"
                            >
                              Mark all as read
                            </h4>
                          )}
                        </div>
                        {communityNum == 0 && (
                          <h3 className="py-4 text-center text-gray-600 bg-gray-300 rounded-b text-md">
                            No Alerts!
                          </h3>
                        )}
                      </div>

                      {alertList.length > 4
                        ? alertList.slice(0, 4).map((data) => (
                            <div className="notify-sub-container">
                              <div className="flex justify-between mb-2"></div>

                              <div className="flex w-full p-3 bg-green-200 rounded-b">
                                {!data.notificationImage ||
                                data.notificationImage === undefined ? (
                                  <i class="fa fa-balance-scale text-black bg-white rounded-sm text-xl px-2 py-1 ml-1 mr-2 h-9"></i>
                                ) : data.notificationImage == null ||
                                  data.notificationImage == "" ? (
                                  <img
                                    className="w-8 h-8"
                                    src={userpic}
                                    alt=""
                                  />
                                ) : (
                                  <img
                                    className="object-cover h-9 w-9"
                                    src={data.notificationImage}
                                    alt=""
                                  />
                                )}

                                <p className="text-black text-xs pt-0.5">
                                  {data.text}
                                </p>
                              </div>
                            </div>
                          ))
                        : alertList.map((data) => (
                            <div className="notify-sub-container">
                              <div className="flex justify-between mb-2"></div>

                              <div className="flex w-full p-3 bg-green-200 rounded-b">
                                {!data.notificationImage ||
                                data.notificationImage === undefined ? (
                                  <i class="fa fa-balance-scale text-black bg-white rounded-sm text-xl px-2 py-1 ml-1 mr-2 h-9"></i>
                                ) : data.notificationImage == null ||
                                  data.notificationImage == "" ? (
                                  <img
                                    className="w-8 h-8"
                                    src={userpic}
                                    alt=""
                                  />
                                ) : (
                                  <img
                                    className="object-cover h-9 w-9"
                                    src={data.notificationImage}
                                    alt=""
                                  />
                                )}

                                <p className="text-black text-xs pt-0.5">
                                  {data.text}
                                </p>
                              </div>
                            </div>
                          ))}

                      {communityNum > 4 && (
                        <h4
                          onClick={() => history.push("/notifications")}
                          className="w-full text-center bg-blue-600 cursor-pointer"
                        >
                          See all
                        </h4>
                      )}
                    </div>
                  </div>
                )}
              </li>
            </OutsideClickHandler>
          )}

          {userInfo ? (
            <OutsideClickHandler
              onOutsideClick={() => {
                setVisible(false);
              }}
            >
              <div className="w-0 h-0">
                <li
                  onClick={() =>
                    visible ? setVisible(false) : setVisible(true)
                  }
                  className="relative cursor-pointer user-icon w-7 h-7 right-1"
                >
                  <img
                    className="object-cover w-full h-full rounded-full"
                    src={
                      userInfo.profile_pic == null || userInfo.profile_pic == ""
                        ? userpic
                        : userInfo.profile_pic
                    }
                    alt="user-pic"
                  />
                </li>

                {visible && (
                  <div
                    style={{ width: "100px" }}
                    className="relative z-40 text-xs text-gray-700 bg-gray-100 border rounded cursor-pointer sub-menu font-roboto"
                  >
                    <li className="w-full pt-1 pl-2 mb-1 bg-gray-200 border-b shadow-lg">
                      <Link to="/profile">Profile</Link>
                    </li>

                    <li className="w-full pt-1 pl-2 mb-1 bg-gray-200 border-b shadow-lg">
                      <Link to="/bmk">Bookmarks</Link>
                    </li>
                    {!memberVerified &&
                      userInfo &&
                      (userInfo.is_lawyer || userInfo.is_lawfirm) && (
                        <li className="w-full pl-2 my-2 bg-gray-200 border-b shadow-lg">
                          <Link to="/member-verification">
                            Request Verification
                          </Link>
                        </li>
                      )}

                    <li
                      className="w-full pl-2 mt-2 mb-1 bg-gray-200 border-b shadow-lg"
                      onClick={logoutHandler}
                    >
                      Logout
                    </li>
                  </div>
                )}
              </div>
            </OutsideClickHandler>
          ) : (
            <li className="text-right sign-up ml-30 w-36">
              <Link to="/signup">Sign Up!</Link>
            </li>
          )}
        </ul>
      </div>
    </div>
  );
}

export default Navbar;
