import React, { useEffect, useState } from "react";
import Navbar from "./HOME/Navbar";
import "./Notifications.css";
import {
  getLawyerAlerts,
  getQuestionAlerts,
  getRecentQuestionAlerts,
  deleteLawyerAlerts,
  deleteQuestionAlerts,
  deleteRecentQuestionAlerts,
  deletePlanAlerts,
  getPlanAlerts,
} from "../actions/notifyActions";
import { useDispatch, useSelector } from "react-redux";
import { membersList } from "../actions/chamberActions";
import { listRecentQuestions } from "../actions/communityActions";
import {
  DELETE_LAWYER_NOTIFY_RESET,
  DELETE_QUESTION_NOTIFY_RESET,
  DELETE_RECENT_QUESTION_NOTIFY_RESET,
  DELETE_PLAN_NOTIFY_RESET,
} from "../constants/notifyConstants";
import { MEMBER_LOGOUT_RESET } from "../constants/userConstants";
import { LazyLoadComponent } from "react-lazy-load-image-component";
import headerimg from "../images/read.jpg";
import userpic from "../images/profile-pic.jpg";

function Notifications() {
  const [notifyLength, setNotifyLength] = useState(0);
  const dispatch = useDispatch();

  const userLogin = useSelector((state) => state.userLogin);
  const { userInfo, success } = userLogin;

  const memberListing = useSelector((state) => state.membersListing);
  const { members, success: memberListingSuccess } = memberListing;

  const memberLoggedout = useSelector((state) => state.memberLogout);
  const { success: memberLoggedOutSuccess } = memberLoggedout;

  const lawyerAlerts = useSelector((state) => state.lawyerNotifications);
  const { notifications: lawyerfications, success: lawyerficationSuccess } =
    lawyerAlerts;

  const removedLawyerAlerts = useSelector(
    (state) => state.deleteLawyerNotifications
  );
  const { success: removedLawyerficationSuccess } = removedLawyerAlerts;

  const planAlerts = useSelector((state) => state.planNotifications);
  const { notifications: planfications, success: planficationSuccess } =
    planAlerts;

  const removedPlanAlerts = useSelector(
    (state) => state.deletePlanNotifications
  );
  const { success: removedPlanficationSuccess } = removedPlanAlerts;

  const questionAlerts = useSelector((state) => state.questionNotifications);
  const { notifications: questionfications, success: questionficationSuccess } =
    questionAlerts;

  const recentQuestionAlerts = useSelector(
    (state) => state.recentQuestionNotifications
  );
  const {
    notifications: recentQuestionfications,
    success: recentQuestionficationSuccess,
  } = recentQuestionAlerts;

  const removedQuestionAlerts = useSelector(
    (state) => state.deleteQuestionNotifications
  );
  const { success: removedQuestionficationSuccess } = removedQuestionAlerts;

  const removedRecentQuestionAlerts = useSelector(
    (state) => state.deleteRecentQuestionNotifications
  );
  const { success: removedRecentQuestionficationSuccess } =
    removedRecentQuestionAlerts;

  useEffect(() => {
    if (
      removedLawyerficationSuccess ||
      removedPlanficationSuccess ||
      removedQuestionficationSuccess ||
      removedRecentQuestionficationSuccess ||
      memberLoggedOutSuccess
    ) {
      dispatch({ type: DELETE_LAWYER_NOTIFY_RESET });
      dispatch({ type: DELETE_QUESTION_NOTIFY_RESET });
      dispatch({ type: DELETE_RECENT_QUESTION_NOTIFY_RESET });
      dispatch({ type: MEMBER_LOGOUT_RESET });
      dispatch({ type: DELETE_PLAN_NOTIFY_RESET });
    } else {
      try {
        dispatch(getLawyerAlerts());
        dispatch(getQuestionAlerts(userInfo?.id));
        dispatch(getRecentQuestionAlerts(userInfo?.id));
        dispatch(getPlanAlerts());
        dispatch(listRecentQuestions());
        dispatch(membersList());
      } catch (e) {
        return;
      }
    }
  }, [
    removedLawyerficationSuccess,
    removedPlanficationSuccess,
    removedQuestionficationSuccess,
    removedRecentQuestionficationSuccess,
    memberLoggedOutSuccess,
  ]);

  useEffect(() => {
    try {
      questionficationSuccess &&
      lawyerficationSuccess &&
      planficationSuccess &&
      recentQuestionficationSuccess &&
      recentQuestionfications.length > 0
        ? setNotifyLength(
            questionfications.length +
              lawyerfications.length +
              1 +
              planfications.length
          )
        : setNotifyLength(
            questionfications.length +
              lawyerfications.length +
              0 +
              planfications.length +
              0
          );
    } catch (e) {
      return e;
    }
  }, [
    questionficationSuccess,
    lawyerficationSuccess,
    planficationSuccess,
    recentQuestionficationSuccess,
    notifyLength,
  ]);

  const removeNotifications = () => {
    try {
      dispatch(deleteLawyerAlerts());
      dispatch(deletePlanAlerts());
      dispatch(deleteQuestionAlerts(userInfo?.id));
      dispatch(deleteRecentQuestionAlerts(userInfo?.id));
    } catch (e) {
      return e;
    }
  };

  return (
    <div>
      <LazyLoadComponent>
        <div
          style={{ backgroundImage: `url(${headerimg})` }}
          className="bg-cover h-96 bg-center relative"
        >
          <div className="overlay h-full w-full bg-black absolute opacity-20 top-0 left-0"></div>
          <Navbar />
          <h1 className="main-text text-5xl font-bold font-roboto text-white relative z-20 text-center mb-4">
            NOTIFICATIONS
          </h1>
          <p className="small-text text-white text-center font-roboto relative z-20">
            Things you may have missed while away
          </p>
        </div>
      </LazyLoadComponent>

      {notifyLength > 0 && (
        <button
          onClick={removeNotifications}
          className="absolute right-6 mt-4 bg-black text-white text-sm px-2 py-1 rounded"
        >
          remove all
        </button>
      )}

      {planficationSuccess &&
        planfications.map((data) => (
          <div
            style={{ width: "620px" }}
            className="notice-cards mx-auto mt-20 bg-transparent border border-gray-700 rounded bg-green-200"
          >
            <div
              style={{ width: "620px" }}
              className="card-content flex pt-5 px-3 mx-auto"
            >
              <img
                src={
                  userInfo.profile_pic == null || userInfo.profile_pic == ""
                    ? userpic
                    : userInfo.profile_pic
                }
                alt=""
                className="h-12 w-12 rounded-full mr-1"
              />
              <p
                style={{ width: "84%" }}
                className="notice-text font-roboto text-sm px-2 pt-2"
              >
                {data.text}
              </p>

              <div className="more-container relative"></div>
            </div>

            <p className="font-roboto text-sm text-right mr-8 pb-5 pt-2">
              {`${new Date(data.createdAt).getMinutes()}min`}
            </p>
          </div>
        ))}

      {lawyerficationSuccess &&
        lawyerfications.map((data) => (
          <div
            style={{ width: "620px" }}
            className="notice-cards mx-auto mt-20 bg-transparent border border-gray-700 rounded bg-green-200"
          >
            <div
              style={{ width: "620px" }}
              className="card-content  flex justify-around pt-5 px-3 mx-auto"
            >
              <i class="fa fa-balance-scale text-black rounded-sm text-2xl px-2 py-1 ml-1 mr-2 h-9 bg-white"></i>
              <p
                style={{ width: "84%" }}
                className="notice-text font-roboto text-sm px-2"
              >
                {data.text}
              </p>

              <div className="more-container relative"></div>
            </div>

            <p className="font-roboto text-sm text-right mr-8 pb-5 pt-2">
              {`${new Date(data.createdAt).getMinutes()}min`}
            </p>
          </div>
        ))}

      {recentQuestionficationSuccess &&
        recentQuestionfications.length != 0 &&
        recentQuestionfications.map((data) => (
          <div
            style={{ width: "620px" }}
            className="notice-cards mx-auto mt-20 bg-transparent border border-gray-700 rounded bg-blue-200"
          >
            <div
              style={{ width: "620px" }}
              className="card-content  flex justify-around pt-5 px-3 mx-auto"
            >
              <i class="fa fa-balance-scale text-black rounded-sm text-2xl px-2 py-1 ml-1 mr-2 h-9 bg-white"></i>
              <p
                style={{ width: "84%" }}
                className="notice-text font-roboto text-sm px-2"
              >
                {data.text}
              </p>

              <div className="more-container relative"></div>
            </div>

            <p className="font-roboto text-sm text-right mr-8 pb-5 pt-2">
              {`${new Date(data.createdAt).getMinutes()}min`}
            </p>
          </div>
        ))}

      {questionficationSuccess &&
        questionfications.map((data) => (
          <div
            style={{ width: "620px" }}
            className="notice-cards mx-auto mt-20 bg-transparent border border-gray-700 rounded bg-blue-200"
          >
            <div
              style={{ width: "620px" }}
              className="card-content  flex justify-around pt-5 px-3 mx-auto"
            >
              <img
                className="w-9 h-9 rounded-sm ml-1 mr-2"
                src={
                  data.notificationImage == null || data.notificationImage == ""
                    ? userpic
                    : data.notificationImage
                }
                alt="image-content"
              />
              <p
                style={{ width: "84%" }}
                className="notice-text font-roboto text-sm px-2"
              >
                {data.text}
              </p>

              <div className="more-container relative"></div>
            </div>

            <p className="font-roboto text-sm text-right mr-8 pb-5 pt-2">
              {`${new Date(data.createdAt).getMinutes()}min`}
            </p>
          </div>
        ))}
    </div>
  );
}

export default Notifications;
