import React, { useState, useEffect } from "react";
import "./Blogcontent.css";
import { Link } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import {
  listPosts,
  addPostComment,
  updatePostComment,
  deletePostComment,
} from "../../actions/BlogActions";
import parse from "html-react-parser";
import {
  CREATE_COMMENT_RESET,
  UPDATE_COMMENT_RESET,
  DELETE_COMMENT_RESET,
} from "../../constants/blogConstants";
import {
  FacebookShareButton,
  FacebookIcon,
  TwitterShareButton,
  TwitterIcon,
  WhatsappShareButton,
  WhatsappIcon,
  RedditIcon,
  RedditShareButton,
} from "react-share";

function Blogcontent({ match }) {
  const [postBody, setPostBody] = useState("");
  const [postAuthor, setPostAuthor] = useState("");
  const [postTitle, setPostTitle] = useState("");
  const [postComments, setPostComments] = useState([]);
  const [editCommentID, setEditCommentID] = useState("");
  const [input, setInput] = useState("");
  const [editInput, setEditInput] = useState("");
  const [numComments, setNumComments] = useState(4);

  const dispatch = useDispatch();

  const postsList = useSelector((state) => state.blogPosts);
  const { success, posts } = postsList;

  const postCommentCreated = useSelector((state) => state.postComment);

  const { success: postCommentSuccess } = postCommentCreated;

  const updateComment = useSelector((state) => state.updatePostComment);

  const { success: updatePostCommentSuccess } = updateComment;

  const deleteComment = useSelector((state) => state.deletePostComment);

  const { success: deletePostCommentSuccess } = deleteComment;

  const userLoggedIn = useSelector((state) => state.userLogin);

  const { userInfo } = userLoggedIn;

  useEffect(() => {
    if (
      postCommentSuccess ||
      updatePostCommentSuccess ||
      deletePostCommentSuccess
    ) {
      dispatch({ type: CREATE_COMMENT_RESET });
      dispatch({ type: UPDATE_COMMENT_RESET });
      dispatch({ type: DELETE_COMMENT_RESET });
    } else {
      dispatch(listPosts());
    }
  }, [postCommentSuccess, updatePostCommentSuccess, deletePostCommentSuccess]);

  useEffect(() => {
    success &&
      posts.find(
        (post) =>
          post._id == match.params.id &&
          (setPostBody(post.body),
          setPostAuthor(post.authorName),
          setPostTitle(post.title),
          setPostComments(post.comments))
      );
  }, [match.params.id, success]);

  const commentSubmitHandler = (e) => {
    e.preventDefault();
    dispatch(addPostComment(input, match.params.id));
    setInput("");
  };

  const updateCommentSubmitHandler = (commentData, id) => {
    dispatch(updatePostComment(commentData, id));
    setEditCommentID("");
  };

  const deleteCommentSubmitHandler = (id) => {
    dispatch(deletePostComment(id));
  };

  const loadMoreCommentsHandler = () => {
    Math.ceil(postComments.length / numComments) * numComments != numComments &&
      setNumComments((prev) => prev + 4);
    console.log(
      Math.ceil(postComments.length / numComments) * numComments,
      numComments
    );
  };

  const URL = window.location.href;

  return (
    <div>
      <Link to="/blog">
        <div className="mt-10 mx-6">
          <i className="fas fa-arrow-left text-blue-500 mr-2"></i>
          <span className="text-blue-500">Back To Home</span>
        </div>
      </Link>
      <div className="content-container flex justify-center">
        <div className="post-content mx-6 mt-12">
          <div className="post-heading-container">
            <h1
              style={{ maxWidth: "700px" }}
              className="post-heading font-bold text-3xl ml-2"
            >
              {postTitle}
            </h1>
          </div>
          <div className="share-icons flex justify-evenly w-28 ml-1 mt-1">
            <FacebookShareButton url={URL}>
              <FacebookIcon className="rounded" size={20} />
            </FacebookShareButton>

            <WhatsappShareButton url={URL}>
              <WhatsappIcon className="rounded" size={20} />
            </WhatsappShareButton>

            <RedditShareButton url={URL}>
              <RedditIcon className="rounded" size={20} />
            </RedditShareButton>

            <TwitterShareButton url={URL}>
              <TwitterIcon className="rounded" size={20} />
            </TwitterShareButton>
          </div>

          <p style={{ maxWidth: "700px" }} className="post-body p-2">
            {parse(postBody)}
          </p>
          <div className="caption-section ml-2">
            <h5 className="ml-2">Posted by {postAuthor}</h5>
            <h5 className="">-Thanks for reading</h5>
          </div>

          <div className="comment-form ml-2">
            {/* <h3 className="">LEAVE A REPLY</h3> */}
            {/* <h3 className="">
              Your email address will not be published. Required fields are
              marked *
            </h3> */}

            <form
              onSubmit={commentSubmitHandler}
              className="flex flex-col mt-4"
            >
              <label htmlFor="comment">Comment</label>
              <textarea
                value={input}
                rows={2}
                cols={1}
                onChange={(e) => setInput(e.target.value)}
                style={{ maxWidth: "700px" }}
                id="comment"
                className="border border-gray-600 h-20 rounded p-2"
              ></textarea>

              <input
                className="w-32 text-white bg-black p-1 rounded mt-4"
                type="submit"
                value="Post Comment"
              />
            </form>
          </div>

          <div className="comments-section mt-10 mb-16 ml-2">
            <h2 className="font-bold">COMMENTS</h2>
            {postComments.slice(0, numComments).map((postcomment) => (
              <div className="my-4">
                <div style={{ maxWidth: "350px" }} className="text-sm">
                  <img
                    className="h-10 w-10 inline mr-1 rounded-full"
                    src={postcomment.userImage}
                  />
                  <h2 className="comment-text w-full">{postcomment.comment}</h2>
                </div>
                <div className="ml-1">
                  {postcomment.user == userInfo.id && (
                    <div className="comment-icons">
                      <i
                        onClick={() =>
                          deleteCommentSubmitHandler(postcomment._id)
                        }
                        className="cursor-pointer fas fa-trash text-xs mr-2"
                      ></i>
                      <i
                        onClick={() =>
                          editCommentID == postcomment._id
                            ? setEditCommentID("")
                            : (setEditCommentID(postcomment._id),
                              setEditInput(postcomment.comment))
                        }
                        className="cursor-pointer fas fa-pen text-xs"
                      ></i>
                    </div>
                  )}

                  {editCommentID == postcomment._id && (
                    <form
                      onSubmit={(e) => {
                        e.preventDefault();

                        updateCommentSubmitHandler(editInput, postcomment._id);
                      }}
                      className="flex flex-col mt-4 mb-6"
                    >
                      <textarea
                        style={{ maxWidth: "700px" }}
                        className="border border-gray-600 h-20 rounded"
                        value={editInput}
                        onChange={(e) => setEditInput(e.target.value)}
                      ></textarea>

                      <input
                        className="w-20 text-white bg-black px-1 rounded mt-2"
                        type="submit"
                        value="Update"
                      />
                    </form>
                  )}
                </div>

                {console.log(numComments, postComments.length)}
              </div>
            ))}
            {postComments.length != 0 &&
              Math.ceil(postComments.length / numComments) * numComments !=
                numComments && (
                <button
                  onClick={loadMoreCommentsHandler}
                  className="bg-black text-white text-sm px-2 py-1 rounded"
                >
                  Load more
                </button>
              )}
            {Math.ceil(postComments.length / numComments) * numComments ==
              numComments &&
              postComments.length > 4 && (
                <button
                  onClick={() => setNumComments(4)}
                  className="bg-black text-white text-sm px-2 py-1 rounded"
                >
                  See less
                </button>
              )}
          </div>
        </div>
        {console.log(
          Math.ceil(postComments.length / numComments) * numComments
        )}
        <div className="second-content mx-6 mt-14">
          <h2 className="font-bold ml-2">ABOUT US</h2>
          <p className="w-80 p-2">
            Lorem ipsum dolor sit amet, consectetur adipisicing elit. Asperiores
            iusto voluptas debitis sapiente incidunt labore ad a libero magni
            necessitatibus qui corporis, repellat distinctio voluptatem ipsam
            quam dolorum laboriosam sint molestiae ratione officia consequatur
            accusamus. Quas, nam quae voluptatibus ratione quam veritatis
            obcaecati? Nemo, pariatur laboriosam natus dolore mollitia dolor.
          </p>

          <h2 className="font-bold mt-14 ml-2">FOLLOW US</h2>
          <ul className="ml-2 socials">
            <li className="my-1">instagram</li>
            <li className="my-1">facebook</li>
            <li className="my-1">twitter</li>
          </ul>
        </div>
      </div>
    </div>
  );
}

export default Blogcontent;
