import {
  LAWYER_NOTIFY_REQUEST,
  LAWYER_NOTIFY_SUCCESS,
  LAWYER_NOTIFY_FAIL,
  DELETE_LAWYER_NOTIFY_REQUEST,
  DELETE_LAWYER_NOTIFY_SUCCESS,
  DELETE_LAWYER_NOTIFY_FAIL,
  PLAN_NOTIFY_REQUEST,
  PLAN_NOTIFY_SUCCESS,
  PLAN_NOTIFY_FAIL,
  DELETE_PLAN_NOTIFY_REQUEST,
  DELETE_PLAN_NOTIFY_SUCCESS,
  DELETE_PLAN_NOTIFY_FAIL,
  QUESTION_NOTIFY_REQUEST,
  QUESTION_NOTIFY_SUCCESS,
  QUESTION_NOTIFY_FAIL,
  RECENT_QUESTION_NOTIFY_REQUEST,
  RECENT_QUESTION_NOTIFY_SUCCESS,
  RECENT_QUESTION_NOTIFY_FAIL,
  DELETE_QUESTION_NOTIFY_REQUEST,
  DELETE_QUESTION_NOTIFY_SUCCESS,
  DELETE_QUESTION_NOTIFY_FAIL,
  DELETE_RECENT_QUESTION_NOTIFY_REQUEST,
  DELETE_RECENT_QUESTION_NOTIFY_SUCCESS,
  DELETE_RECENT_QUESTION_NOTIFY_FAIL,
} from "../constants/notifyConstants";

import axios from "axios";

import useCustomInterceptor from "../customInterceptor/useCustomInterceptor";

export const getLawyerAlerts = () => async (dispatch, getState) => {
  try {
    dispatch({
      type: LAWYER_NOTIFY_REQUEST,
    });

    const api = useCustomInterceptor();

    const userInfo = JSON.parse(localStorage.getItem("userInfo"));

    const config = {
      headers: {
        "Content-type": "application/json",
        Authorization: `Bearer ${userInfo.token}`,
      },
    };

    const { data } = await api.get("/alerts/lawyer-notify/", config);

    dispatch({
      type: LAWYER_NOTIFY_SUCCESS,
      payload: data,
    });
  } catch (error) {
    dispatch({
      type: LAWYER_NOTIFY_FAIL,
      payload:
        error.response && error.response.data.detail
          ? error.response.data.detail
          : error.message,
    });
  }
};

