import React, { useEffect, useState } from "react";
import Navbar from "../components/HOME/Navbar";
import "./Signup.css";
import headerimg from "../images/loginimage.jpg";
import { Link } from "react-router-dom";
import { useSelector, useDispatch } from "react-redux";
import { register } from "../actions/userActions";
import ReactLoading from "react-loading";
import "react-phone-number-input/style.css";
import PhoneInput from "react-phone-number-input";
import useInputValidator from "../validatorHook/useInputValidator";
import FlashMessage from "react-flash-message";

function Signup({ location, history }) {
  const [value, setValue] = useState();
  const [email, setEmail] = useState("");
  const [name, setName] = useState("");
  const [phone, setPhone] = useState("");
  const [password, setPassword] = useState("");
  const [country, setCountry] = useState([]);
  const [state, setState] = useState([]);
  const [city, setCity] = useState([]);
  const [client, setClient] = useState(true);
  const [lawyer, setLawyer] = useState(false);
  const [lawfirm, setLawFirm] = useState(false);
  const [lawstudent, setLawStudent] = useState(false);
  const [countryName, setCountryName] = useState("");
  const [stateName, setStateName] = useState("");
  const [cityName, setCityName] = useState("");
  const [stateCode, setStateCode] = useState("");
  const [cityCode, setCityCode] = useState("");

  const dispatch = useDispatch();

  const redirect = location.search ? location.search.split("=")[1] : "/";

  const userRegister = useSelector((state) => state.userRegister);

  const { loading, userInfo } = userRegister;

  const { numberState, letterState, valueSize } = useInputValidator(password);

  useEffect(() => {
    if (userInfo) {
      history.push(redirect);
    }
  }, [history, userInfo, redirect]);

  function submitHandler(e) {
    e.preventDefault();

    numberState &&
      letterState &&
      valueSize &&
      countryName &&
      stateName &&
      cityName &&
      dispatch(
        register(
          name,
          email,
          value,
          countryName,
          stateName,
          cityName,
          password,
          lawyer,
          client,
          lawfirm,
          lawstudent
        )
      );
  }

  let regionNamesInEnglish = new Intl.DisplayNames(["en"], { type: "region" });

  var headers = new Headers();
  headers.append(
    "X-CSCAPI-KEY",
    "VU1VYXF3U25EMkxTNFF0YmtNU3BwSHI5RVQzS3hZTDI4czVvUFl5OQ=="
  );

  var requestOptions = {
    method: "GET",
    headers: headers,
    redirect: "follow",
  };

  useEffect(async () => {
    await fetch("https://api.countrystatecity.in/v1/countries", requestOptions)
      .then((response) => response.text())
      .then((result) => setCountry(JSON.parse(result)))
      .catch((error) => console.log("error", error));
  }, []);

  useEffect(async () => {
    await fetch(
      `https://api.countrystatecity.in/v1/countries/${stateCode}/states`,
      requestOptions
    )
      .then((response) => response.text())
      .then((result) => setState(JSON.parse(result)))
      .catch((error) => console.log("error", error));
  }, [stateCode]);

  useEffect(async () => {
    await fetch(
      `https://api.countrystatecity.in/v1/countries/${stateCode}/states/${cityCode}/cities`,
      requestOptions
    )
      .then((response) => response.text())
      .then((result) => setCity(JSON.parse(result)))
      .catch((error) => console.log("error", error));
  }, [cityCode]);

  return (
    <div className="main-container">
      <div className="mb-0">
        <div
          className="relative bg-center bg-cover"
          style={{
            backgroundImage: `url(${headerimg})`,
            height: "500px",
          }}
        >
          <Navbar height="500px" />
          <div className="absolute top-0 left-0 w-full h-full bg-black overlay opacity-30"></div>
          <h1 className="relative z-20 text-5xl font-bold text-center text-white bg-main-text mt-14">
            SIGN UP FOR FREE
          </h1>
          <p
            style={{ width: "500px" }}
            className="relative z-20 mx-auto mt-4 text-center text-white bg-text font-roboto"
          >
            Join the Largest Community of Legal Services Expert as someone in
            need of legal services, a lawyer, firm, or student.
          </p>
        </div>

        <h1 className="mt-10 mb-10 text-3xl font-bold text-center text-gray-800 font-roboto">
          SIGN UP AS
        </h1>

        <form onSubmit={submitHandler}>
          {loading && (
            <div className="flex items-center justify-center mb-12">
              <ReactLoading type="spin" color="#000" height={50} width={50} />
            </div>
          )}
          <div
            style={{ width: "520px" }}
            className="flex items-center justify-between h-10 mx-auto font-semibold bg-gray-200 rounded signup-options"
          >
            <input
              type="radio"
              id="client"
              name="radio-options"
              className="radio-button"
              onChange={(e) =>
                e.target.checked ? setClient(true) : setClient(false)
              }
              hidden
            />

            <label
              className={`client-label font-roboto text-sm radio-label h-full py-3 w-3/6 text-center ${
                client ? "bg-black text-white rounded" : "bg-gray-200"
              }`}
              htmlFor="client"
            >
              A Client
            </label>

            <input
              type="radio"
              id="lawyer"
              name="radio-options"
              className="radio-button"
              onChange={(e) =>
                e.target.checked && client == true
                  ? (setLawyer(true), setClient(false))
                  : setLawyer(false)
              }
              hidden
            />
            <label
              className="w-3/6 h-full py-3 text-sm text-center font-roboto radio-label"
              htmlFor="lawyer"
            >
              A Lawyer
            </label>

            <input
              type="radio"
              id="lawfirm"
              name="radio-options"
              onChange={(e) =>
                e.target.checked && client == true
                  ? (setLawFirm(true), setClient(false))
                  : setLawFirm(false)
              }
              hidden
              className="radio-button"
            />
            <label
              className="w-3/6 h-full py-3 text-sm text-center font-roboto radio-label"
              htmlFor="lawfirm"
            >
              A Law Firm
            </label>

            <input
              type="radio"
              id="student"
              name="radio-options"
              onChange={(e) =>
                e.target.checked && client == true
                  ? (setLawStudent(true), setClient(false))
                  : setLawStudent(false)
              }
              hidden
              className="radio-button"
            />
            <label
              className="w-3/6 h-full px-1 py-3 text-sm text-center font-roboto radio-label"
              htmlFor="student"
            >
              A Law Student
            </label>
          </div>

          <div
            style={{ width: "520px" }}
            className="mx-auto mt-5 input-container"
          >
            <input
              className="block w-full h-10 px-5 mt-5 border border-gray-500 rounded font-roboto"
              type="text"
              placeholder="Fullname"
              required
              pattern="^[A-Za-z].?{2,20}$"
              minLength="2"
              title="Name must be between 2 and 10 characters!"
              onChange={(e) => setName(e.target.value)}
            />
            <input
              className="block w-full h-10 px-5 mt-5 border border-gray-500 rounded font-roboto"
              type="email"
              placeholder="Email address"
              required
              onChange={(e) => setEmail(e.target.value)}
            />

            <PhoneInput
              defaultCountry="US"
              value={value}
              className="h-10 pl-2 mt-5 border border-gray-500 rounded"
              placeholder="Phone number"
              onChange={setValue}
              minLength="10"
            />

            <div
              style={{ width: "520px" }}
              className="flex justify-between mx-auto my-5 text-center country-select-container"
            >
              <select
                style={{ width: "31%" }}
                className="pb-1 pl-3 border border-gray-500 rounded-md shadow-lg h-9"
                name="country"
                id="country"
                required
                onChange={(e) => {
                  setStateCode(e.target.value);
                  try {
                    return e.target.value == ""
                      ? ""
                      : setCountryName(regionNamesInEnglish.of(e.target.value));
                  } catch (e) {
                    console.log(e.message);
                  }
                }}
              >
                <option value="" disabled selected>
                  Country
                </option>

                <option value="country">{countryName}</option>
                {country.map((data) => (
                  <option
                    className="country-code"
                    value={data.iso2}
                    placeholder={data.name}
                  >
                    {data.name}
                  </option>
                ))}
              </select>

              <select
                style={{ width: "31%" }}
                className="pb-1 pl-3 border border-gray-500 rounded-md shadow-lg h-9"
                name="state"
                id="state"
                required
                onChange={(e) => (
                  setCityCode(e.target.value), setStateName(e.target.value)
                )}
              >
                <option value="state" selected disabled>
                  State
                </option>
                <option value="state">{stateName}</option>
                {state != "" &&
                  Array.from(state).map((data) => (
                    <option value={data.iso2}>{data.name}</option>
                  ))}
              </select>

              <select
                style={{ width: "31%" }}
                className="w-40 pb-1 pl-3 border border-gray-500 rounded-md shadow-lg h-9"
                name="city"
                id="city"
                required
                onChange={(e) => setCityName(e.target.value)}
              >
                <option value="city" selected disabled>
                  City
                </option>
                {city != "" &&
                  Array.from(city).map((data) => (
                    <option value={data.iso2}>{data.name}</option>
                  ))}
              </select>
            </div>
            <input
              style={{ width: "520px" }}
              className="block h-10 px-5 mx-auto mt-5 border border-gray-500 rounded passwd-input font-roboto"
              type="password"
              placeholder="Password should contain letters and numbers!"
              pattern="^(?=.*[A-Za-z])(?=.*\d)[A-Za-z\d]{8,13}$"
              title="'Password must be between 8 and 13 characters in length and contain only letters and numbers"
              minLength="8"
              required
              onChange={(e) => setPassword(e.target.value)}
            />
          </div>
          <h1 className="mt-5 text-xs text-center text-gray-500 footer-text font-roboto">
            By clicking sign up, you agree to our
            <span className="font-bold text-black"> Terms of service</span> and
            <span className="font-bold text-black"> Privacy Policy</span>
          </h1>
          <input
            style={{ width: "170px" }}
            className="block h-10 mx-auto mt-5 font-bold text-white bg-black rounded font-roboto signup-btn"
            type="submit"
            value="Sign Up"
          />
        </form>
        {/* 
        <h1 className="my-4 text-lg italic font-bold text-center text-gray-800 font-roboto">
          OR
        </h1>
        <div style={{ width: "380px" }} className="flex mx-auto other-options">
          <button className="w-1/2 h-10 mr-4 text-sm font-bold text-white bg-red-600 rounded">
            Sign up with google
          </button>
          <button className="w-1/2 h-10 text-sm font-bold text-white bg-blue-900 rounded">
            Sign up with Facebook
          </button>
        </div> */}
        <h1 className="mt-4 italic text-center text-gray-700 font-roboto">
          already have an account?
          <Link to={redirect ? `/signin?redirect=${redirect}` : "/signin"}>
            <span className="font-bold text-black"> Sign in</span>
          </Link>
        </h1>
      </div>
    </div>
  );
}

export default Signup;
