import {
  POST_REQUEST,
  POST_SUCCESS,
  POST_FAIL,
  CREATE_COMMENT_REQUEST,
  CREATE_COMMENT_SUCCESS,
  CREATE_COMMENT_FAIL,
  CREATE_COMMENT_RESET,
  UPDATE_COMMENT_REQUEST,
  UPDATE_COMMENT_SUCCESS,
  UPDATE_COMMENT_FAIL,
  UPDATE_COMMENT_RESET,
  DELETE_COMMENT_REQUEST,
  DELETE_COMMENT_SUCCESS,
  DELETE_COMMENT_FAIL,
  DELETE_COMMENT_RESET,
} from "../constants/blogConstants";

export const blogPostReducers = (state = { posts: [] }, action) => {
  switch (action.type) {
    case POST_REQUEST:
      return { loading: true, posts: [] };

    case POST_SUCCESS:
      return { loading: false, success: true, posts: action.payload };

    case POST_FAIL:
      return { loading: false, error: action.payload };

    default:
      return state;
  }
};

export const createPostCommentReducer = (state = {}, action) => {
  switch (action.type) {
    case CREATE_COMMENT_REQUEST:
      return { loading: true };

    case CREATE_COMMENT_SUCCESS:
      return { loading: false, success: true, state: action.payload };

    case CREATE_COMMENT_FAIL:
      return { loading: false, error: action.payload };

    case CREATE_COMMENT_RESET:
      return {};

    default:
      return state;
  }
};

export const updatePostCommentReducer = (state = { comment: {} }, action) => {
  switch (action.type) {
    case UPDATE_COMMENT_REQUEST:
      return { loading: true };

    case UPDATE_COMMENT_SUCCESS:
      return {
        loading: false,
        success: true,
        message: "Updated successfully",
        comment: action.payload,
      };

    case UPDATE_COMMENT_FAIL:
      return { loading: false, error: action.payload };

    case UPDATE_COMMENT_RESET:
      return {};

    default:
      return state;
  }
};

export const deletePostCommentReducer = (state = {}, action) => {
  switch (action.type) {
    case DELETE_COMMENT_REQUEST:
      return { loading: true };

    case DELETE_COMMENT_SUCCESS:
      return {
        loading: false,
        success: true,
        message: action.payload,
      };

    case DELETE_COMMENT_FAIL:
      return { loading: false, error: action.payload };

    case DELETE_COMMENT_RESET:
      return {};

    default:
      return state;
  }
};
