import {
  BOOKMARK_LIST_REQUEST,
  BOOKMARK_LIST_SUCCESS,
  BOOKMARK_LIST_FAIL,
  CREATE_BOOKMARK_REQUEST,
  CREATE_BOOKMARK_SUCCESS,
  CREATE_BOOKMARK_FAIL,
  CREATE_BOOKMARK_RESET,
  DELETE_BOOKMARK_REQUEST,
  DELETE_BOOKMARK_SUCCESS,
  DELETE_BOOKMARK_FAIL,
  DELETE_BOOKMARK_RESET,

  BOOKMARK_LAWYER_LIST_REQUEST,
  BOOKMARK_LAWYER_LIST_SUCCESS,
  BOOKMARK_LAWYER_LIST_FAIL,
  CREATE_BOOKMARK_LAWYER_REQUEST,
  CREATE_BOOKMARK_LAWYER_SUCCESS,
  CREATE_BOOKMARK_LAWYER_FAIL,
  CREATE_BOOKMARK_LAWYER_RESET,
  DELETE_BOOKMARK_LAWYER_REQUEST,
  DELETE_BOOKMARK_LAWYER_SUCCESS,
  DELETE_BOOKMARK_LAWYER_FAIL,
  DELETE_BOOKMARK_LAWYER_RESET,
} from "../constants/bookmarkConstants";

export const bookmarkBookListReducer = (state = { books: [] }, action) => {
    switch (action.type) {
        case BOOKMARK_LIST_REQUEST:
          return { loading: true, books: [] };
    
        case BOOKMARK_LIST_SUCCESS:
          return { loading: false, success: true, books: action.payload };
    
        case BOOKMARK_LIST_FAIL:
          return { loading: false, error: action.payload };
    
        default:
          return state;
      }
};


export const bookmarkLawyerListReducer = (state = { lawyers: [] }, action) => {
    switch (action.type) {
        case BOOKMARK_LAWYER_LIST_REQUEST:
          return { loading: true, lawyers: [] };
    
        case BOOKMARK_LAWYER_LIST_SUCCESS:
          return { loading: false, success: true, lawyers: action.payload };
    
        case BOOKMARK_LAWYER_LIST_FAIL:
          return { loading: false, error: action.payload };

        
    
        default:
          return state;
      }
};


export const addBookmarkBookReducer = (state = {}, action) => {
    switch (action.type) {
      case CREATE_BOOKMARK_REQUEST:
        return { loading: true };
  
      case CREATE_BOOKMARK_SUCCESS:
        return { loading: false, success: true, state: action.payload };
  
      case CREATE_BOOKMARK_FAIL:
        return { loading: false, error: action.payload };
  
      case CREATE_BOOKMARK_RESET:
        return {};
  
      default:
        return state;
    }
  };


  export const addBookmarkLawyerReducer = (state = {}, action) => {
    switch (action.type) {
      case CREATE_BOOKMARK_LAWYER_REQUEST:
        return { loading: true };
  
      case CREATE_BOOKMARK_LAWYER_SUCCESS:
        return { loading: false, success: true , message: action.payload};
  
      case CREATE_BOOKMARK_LAWYER_FAIL:
        return { loading: false, error: action.payload };
  
      case CREATE_BOOKMARK_LAWYER_RESET:
        return {};
  
      default:
        return state;
    }
  };




  export const deleteBookmarkBookReducer = (state = {}, action) => {
    switch (action.type) {
      case DELETE_BOOKMARK_REQUEST:
        return { loading: true };
  
      case DELETE_BOOKMARK_SUCCESS:
        return {
          loading: false,
          success: true,
          message: action.payload,
        };
  
      case DELETE_BOOKMARK_FAIL:
        return { loading: false, error: action.payload };
  
      case DELETE_BOOKMARK_RESET:
        return {};
  
      default:
        return state;
    }
  };



  export const deleteBookmarkLawyerReducer = (state = {}, action) => {
    switch (action.type) {
      case DELETE_BOOKMARK_LAWYER_REQUEST:
        return { loading: true };
  
      case DELETE_BOOKMARK_LAWYER_SUCCESS:
        return {
          loading: false,
          success: true,
          message: action.payload,
        };
  
      case DELETE_BOOKMARK_LAWYER_FAIL:
        return { loading: false, error: action.payload };
  
      case DELETE_BOOKMARK_LAWYER_RESET:
        return {};
  
      default:
        return state;
    }
  };