import {
  BOOK_LIST_REQUEST,
  BOOK_LIST_SUCCESS,
  BOOK_LIST_FAIL,
  BOOK_DETAILS_REQUEST,
  BOOK_DETAILS_SUCCESS,
  BOOK_DETAILS_FAIL,
  UPLOAD_BOOK_REQUEST,
  UPLOAD_BOOK_SUCCESS,
  UPLOAD_BOOK_FAIL,
  UPLOAD_BOOK_RESET,
  ADD_REVIEW_REQUEST,
  ADD_REVIEW_SUCCESS,
  ADD_REVIEW_FAIL,
  ADD_REVIEW_RESET,
  UPDATE_REVIEW_REQUEST,
  UPDATE_REVIEW_SUCCESS,
  UPDATE_REVIEW_FAIL,
  UPDATE_REVIEW_RESET,
  DELETE_REVIEW_REQUEST,
  DELETE_REVIEW_SUCCESS,
  DELETE_REVIEW_FAIL,
  DELETE_REVIEW_RESET,
} from "../constants/bookConstants";

export const addBookReducer = (state = {}, action) => {
  switch (action.type) {
    case UPLOAD_BOOK_REQUEST:
      return { loading: true };

    case UPLOAD_BOOK_SUCCESS:
      return {
        loading: false,
        success: true,
        message: "Book uploaded successfully",
      };

    case UPLOAD_BOOK_FAIL:
      return { loading: false, error: action.payload };

    case UPLOAD_BOOK_RESET:
      return {};

    default:
      return state;
  }
};

export const addBookReviewReducer = (state = {}, action) => {
  switch (action.type) {
    case ADD_REVIEW_REQUEST:
      return { loading: true };

    case ADD_REVIEW_SUCCESS:
      return { loading: false, success: true };

    case ADD_REVIEW_FAIL:
      return { loading: false, error: action.payload };

    case ADD_REVIEW_RESET:
      return {};

    default:
      return state;
  }
};

export const updateBookReviewReducer = (state = { bookreview: {} }, action) => {
  switch (action.type) {
    case UPDATE_REVIEW_REQUEST:
      return { loading: true };

    case UPDATE_REVIEW_SUCCESS:
      return {
        loading: false,
        success: true,
        message: "Review updated successfully",
        bookreview: action.payload,
      };

    case UPDATE_REVIEW_FAIL:
      return { loading: false, error: action.payload };

    case UPDATE_REVIEW_RESET:
      return {};

    default:
      return state;
  }
};

export const deleteBookReviewReducer = (state = {}, action) => {
  switch (action.type) {
    case DELETE_REVIEW_REQUEST:
      return { loading: true };

    case DELETE_REVIEW_SUCCESS:
      return {
        loading: false,
        success: true,
        message: "Review deleted successfully",
      };

    case DELETE_REVIEW_FAIL:
      return { loading: false, error: action.payload };

    case DELETE_REVIEW_RESET:
      return {};

    default:
      return state;
  }
};

export const bookListReducers = (state = { books: [] }, action) => {
  switch (action.type) {
    case BOOK_LIST_REQUEST:
      return { loading: true, books: [] };

    case BOOK_LIST_SUCCESS:
      return { loading: false, success: true, books: action.payload };

    case BOOK_LIST_FAIL:
      return { loading: false, error: action.payload };

    default:
      return state;
  }
};

