import {
  LAWYER_NOTIFY_REQUEST,
  LAWYER_NOTIFY_SUCCESS,
  LAWYER_NOTIFY_FAIL,
  DELETE_LAWYER_NOTIFY_REQUEST,
  DELETE_LAWYER_NOTIFY_SUCCESS,
  DELETE_LAWYER_NOTIFY_FAIL,
  DELETE_LAWYER_NOTIFY_RESET,
  PLAN_NOTIFY_REQUEST,
  PLAN_NOTIFY_SUCCESS,
  PLAN_NOTIFY_FAIL,
  DELETE_PLAN_NOTIFY_REQUEST,
  DELETE_PLAN_NOTIFY_SUCCESS,
  DELETE_PLAN_NOTIFY_FAIL,
  DELETE_PLAN_NOTIFY_RESET,
  QUESTION_NOTIFY_REQUEST,
  QUESTION_NOTIFY_SUCCESS,
  QUESTION_NOTIFY_FAIL,
  RECENT_QUESTION_NOTIFY_REQUEST,
  RECENT_QUESTION_NOTIFY_SUCCESS,
  RECENT_QUESTION_NOTIFY_FAIL,
  DELETE_QUESTION_NOTIFY_REQUEST,
  DELETE_QUESTION_NOTIFY_SUCCESS,
  DELETE_QUESTION_NOTIFY_FAIL,
  DELETE_QUESTION_NOTIFY_RESET,
  DELETE_RECENT_QUESTION_NOTIFY_REQUEST,
  DELETE_RECENT_QUESTION_NOTIFY_SUCCESS,
  DELETE_RECENT_QUESTION_NOTIFY_FAIL,
  DELETE_RECENT_QUESTION_NOTIFY_RESET,
} from "../constants/notifyConstants";

export const lawyerNotifyReducers = (state = { notifications: [] }, action) => {
  switch (action.type) {
    case LAWYER_NOTIFY_REQUEST:
      return { loading: true, notifications: [] };

    case LAWYER_NOTIFY_SUCCESS:
      return { loading: false, success: true, notifications: action.payload };

    case LAWYER_NOTIFY_FAIL:
      return { loading: false, error: action.payload };

    default:
      return state;
  }
};

export const deleteLawyerNotifyReducers = (state = {}, action) => {
  switch (action.type) {
    case DELETE_LAWYER_NOTIFY_REQUEST:
      return { loading: true };

    case DELETE_LAWYER_NOTIFY_SUCCESS:
      return {
        loading: false,
        success: true,
      };

    case DELETE_LAWYER_NOTIFY_FAIL:
      return { loading: false, error: action.payload };

    case DELETE_LAWYER_NOTIFY_RESET:
      return {};

    default:
      return state;
  }
};

