import React, { useState, useEffect } from "react";
import "./Reviewmodal.css";
import { useDispatch, useSelector } from "react-redux";
import {
  addQualificationAction,
  updateQualificationAction,
  addMemberReviewAction,
  updateMemberReviewAction,
  updateContactAction,
  updateDescriptionAction,
  updateMemberBannerDetailsAction,
  updateMemberDpAction,
} from "../../actions/chamberActions";

function Reviewmodal({
  addQualification,
  editEducation,
  editMembership,
  editContact,
  editAbout,
  editBannerDetails,
  editProfileImage,
  aboutValue,
  position,
  sidePosition,
  size,
  degreeValue,
  institutionValue,
  dateValue,
  memInstitution,
  memPosition,
  memDate,
  submitReview,
  editReview,
  memberID,
  qlftID,
  reviewCommentValue,
  reviewRatingValue,
  reviewID,
  memberexperience,
  membersite,
  memberContactMod,
  memberEm,
  memberFacebook,
  memberTw,
  memberLo,
  memberNumber,
  memberAbout,
  width,
  color,
  rightPosition,

}) {
  const [degree, setDegree] = useState("");
  const [institution, setInstitution] = useState("");
  const [date, setDate] = useState("");
  const [membershipInstitution, setMembershipInstitution] = useState("");
  const [membershipPosition, setMembershipPosition] = useState("");
  const [membershipDate, setMembershipDate] = useState("");

  const [editDegree, setEditDegree] = useState(degreeValue);
  const [editInstitution, setEditInstitution] = useState(institutionValue);
  const [editDate, setEditDate] = useState(dateValue);

  const [editMembershipInstitution, setEditMembershipInstitution] =
    useState(memInstitution);
  const [editMembershipPosition, setEditMembershipPosition] =
    useState(memPosition);
  const [editMembershipDate, setEditMembershipDate] = useState(memDate);
  const [reviewComment, setReviewComment] = useState("");
  const [reviewRating, setReviewRating] = useState("");
  const [editReviewComment, setEditReviewComment] =
    useState(reviewCommentValue);
  const [editReviewRating, setEditReviewRating] = useState(reviewRatingValue);

  const [memberLocation, setMemberLocation] = useState(memberLo);
  const [memberEmail, setMemberEmail] = useState(memberEm);
  const [memberTwitter, setMemberTwitter] = useState(memberTw);
  const [memberFB, setMemberFB] = useState(memberFacebook);
  const [memberPhone, setMemberPhone] = useState(memberNumber);

  const [about, setAbout] = useState(aboutValue);
  const [memberExperience, setMemberExperience] = useState(memberexperience);
  const [memberWebsite, setMemberWebsite] = useState(membersite);
  const [memberDp, setMemberDp] = useState("");
  const [memberBp, setMemberBp] = useState("");

  const dispatch = useDispatch();

  const addQlftSubmitHandler = (e) => {
    e.preventDefault();
    dispatch(
      addQualificationAction(
        degree,
        institution,
        date,
        membershipInstitution,
        membershipPosition,
        membershipDate,
        memberID
      )
    );
  };

  const updateQlftSubmitHandler = (e) => {
    e.preventDefault();

    dispatch(
      updateQualificationAction(
        editDegree,
        editInstitution,
        editDate,
        editMembershipInstitution,
        editMembershipPosition,
        editMembershipDate,
        memberID,
        qlftID
      )
    );
  };

  const addMemberReviewSubmitHandler = (e) => {
    e.preventDefault();

    dispatch(addMemberReviewAction(reviewRating, reviewComment, memberID));
    setReviewComment("");
    setReviewRating("");
  };

  const updateMemberReviewSubmitHandler = (e) => {
    e.preventDefault();
    dispatch(
      updateMemberReviewAction(
        editReviewRating,
        editReviewComment,
        reviewID,
        memberID
      )
    );
  };

  const updateMemberContactSubmitHandler = (e) => {
    e.preventDefault();
    dispatch(
      updateContactAction(
        memberLocation,
        memberTwitter,
        memberFB,
        memberEmail,
        memberPhone,
        reviewID,
        memberID
      )
    );
    // setMemberEmail("");
    // setMemberFB("");
    // setMemberPhone("");
    // setMemberTwitter("");
    // setMemberLocation("");
  };

  const updateDescriptionSubmitHandler = (e) => {
    e.preventDefault();
    dispatch(updateDescriptionAction(about, memberID));
  };

  const updateBannerDetailsSubmitHandler = (e) => {
    e.preventDefault();
    dispatch(
      updateMemberBannerDetailsAction(
        memberBp,
        memberExperience,
        memberWebsite,
        memberID
      )
    );

    // setMemberBp("");
    // setMemberExperience("");
    // setMemberWebsite("");
  };

  const updateDpSubmitHandler = (e) => {
    e.preventDefault();
    dispatch(updateMemberDpAction(memberDp, memberID));
  };

  return (
    <div className={`container ${width}`}>
      <details>
        <summary>
          <div
            style={{ right: rightPosition }}
            className={`button ${position} ${sidePosition}`}
          >
            <i className={`fas fa-pen ${size} ${color} cursor-pointer`}></i>
          </div>
          <div className="details-modal-overlay"></div>
        </summary>

        <div className="details-modal">
          <div className="details-modal-close">
            <svg
              xmlns="http://www.w3.org/2000/svg"
              width="14"
              height="14"
              viewBox="0 0 14 14"
              fill="none"
            >
              <path
                fill-rule="evenodd"
                clip-rule="evenodd"
                d="M13.7071 1.70711C14.0976 1.31658 14.0976 0.683417 13.7071 0.292893C13.3166 -0.0976311 12.6834 -0.0976311 12.2929 0.292893L7 5.58579L1.70711 0.292893C1.31658 -0.0976311 0.683417 -0.0976311 0.292893 0.292893C-0.0976311 0.683417 -0.0976311 1.31658 0.292893 1.70711L5.58579 7L0.292893 12.2929C-0.0976311 12.6834 -0.0976311 13.3166 0.292893 13.7071C0.683417 14.0976 1.31658 14.0976 1.70711 13.7071L7 8.41421L12.2929 13.7071C12.6834 14.0976 13.3166 14.0976 13.7071 13.7071C14.0976 13.3166 14.0976 12.6834 13.7071 12.2929L8.41421 7L13.7071 1.70711Z"
                fill="black"
              />
            </svg>
          </div>
          <div className="details-modal-title">
            {!submitReview && !editReview && !memberContactMod && !memberAbout && <h1>Your Qualifications</h1>}
            {memberAbout && <h1>About Info</h1>}
            {memberContactMod && <h1>Contact Info</h1>}
          </div>
          {addQualification && (
            <form
              onSubmit={addQlftSubmitHandler}
              className="details-modal-content"
            >
              <div className="Education-container flex flex-col">
                <h4 className="font-roboto font-bold mb-2">Education</h4>
                <label htmlFor="deg">Degree</label>
                <input
                  id="deg"
                  className="border-b border-gray-700"
                  onChange={(e) => setDegree(e.target.value)}
                  type="text"
                />

                <label className="mt-3" htmlFor="eduInst">
                  Institution
                </label>
                <input
                  id="eduIns"
                  className="border-b border-gray-700"
                  type="text"
                  onChange={(e) => setInstitution(e.target.value)}
                />

                <label className="mt-3" htmlFor="eduDate">
                  Date
                </label>
                <input
                  id="eduDate"
                  className="border-b border-gray-700"
                  type="text"
                  maxLength={4}
                  onChange={(e) => setDate(e.target.value)}
                />
              </div>

              <div className="Membership-container flex flex-col">
                <h4 className="font-roboto font-bold mt-8 mb-3">Memberships</h4>
                <label htmlFor="deg">Institution</label>
                <input
                  id="deg"
                  className="border-b border-gray-700"
                  type="text"
                  onChange={(e) => setMembershipInstitution(e.target.value)}
                />

                <label className="mt-3" htmlFor="deg">
                  Position
                </label>
                <input
                  id="deg"
                  className="border-b border-gray-700"
                  type="text"
                  onChange={(e) => setMembershipPosition(e.target.value)}
                />

                <label className="mt-3" htmlFor="deg">
                  Date
                </label>
                <input
                  id="deg"
                  className="border-b border-gray-700"
                  type="text"
                  maxLength={4}
                  onChange={(e) => setMembershipDate(e.target.value)}
                />
              </div>
              <input
                className="bg-black text-white block mx-auto px-5 mt-6 rounded"
                type="submit"
                value="Add"
              />
            </form>
          )}

          {editEducation && (
            <form
              onSubmit={updateQlftSubmitHandler}
              className="flex flex-col px-5 details-modal-content"
            >
              <h4 className="font-roboto font-bold mb-2">Education</h4>
              <label className="mb-1 ml-2" htmlFor="deg">
                Degree
              </label>
              <input
                id="deg"
                className="border-b border-gray-700 pb-1"
                type="text"
                value={editDegree}
                onChange={(e) => setEditDegree(e.target.value)}
              />

              <label className="mt-3 mb-2 ml-2" htmlFor="eduInst">
                Institution
              </label>
              <input
                id="eduInst"
                className="border-b border-gray-700 pb-1"
                type="text"
                value={editInstitution}
                onChange={(e) => setEditInstitution(e.target.value)}
              />

              <label className="mt-3 mb-2 ml-2" htmlFor="eduDate">
                Date
              </label>
              <input
                id="eduDate"
                className="border-b border-gray-700 pb-1"
                type="text"
                maxLength={4}
                value={editDate}
                onChange={(e) => setEditDate(e.target.value)}
              />
              <input
                className="bg-black text-white block mx-auto px-5 my-6 rounded "
                type="submit"
                value="Update"
              />
            </form>
          )}

          {editMembership && (
            <form
              onSubmit={updateQlftSubmitHandler}
              className="flex flex-col details-modal-content"
            >
              <h4 className="font-roboto font-bold mb-2">Memberships</h4>
              <label htmlFor="memInst">Degree</label>
              <input
                id="memInst"
                className="border-b border-gray-700"
                type="text"
                value={editMembershipInstitution}
                onChange={(e) => setEditMembershipInstitution(e.target.value)}
              />

              <label className="mt-3" htmlFor="memPosition">
                Institution
              </label>
              <input
                id="memPosition"
                className="border-b border-gray-700"
                type="text"
                value={editMembershipPosition}
                onChange={(e) => setEditMembershipPosition(e.target.value)}
              />

              <label className="mt-3" htmlFor="memDate">
                Date
              </label>
              <input
                id="memDate"
                className="border-b border-gray-700"
                type="text"
                maxLength={4}
                value={editMembershipDate}
                onChange={(e) => setEditMembershipDate(e.target.value)}
              />
              <input
                className="bg-black text-white block mx-auto px-5 my-6 rounded"
                type="submit"
                value="Update"
              />
            </form>
          )}

          {submitReview && (
            <form
              onSubmit={addMemberReviewSubmitHandler}
              className="review-section details-modal-content flex flex-col h-80 items-center justify-evenly"
            >
              <label
                className="bg-red-600 w-3/4 text-white text-center rounded"
                htmlFor="ratings"
              >
                Rating
              </label>

              <select
                className="w-3/4 bg-gray-200 py-1 rounded px-2"
                id="ratings"
                value={reviewRating}
                onChange={(e) => setReviewRating(e.target.value)}
              >
                <option value="" selected disabled>
                  Select...
                </option>
                <option value="1">1 - Poor</option>
                <option value="2">2 - Fair</option>
                <option value="3">3 - Good</option>
                <option value="4">4 - Very Good</option>
                <option value="5">5 - Excellent</option>
              </select>

              <textarea
                className="w-3/4 border border-gray-400 h-32 rounded px-2 py-1"
                type="text"
                required
                placeholder="write a review"
                value={reviewComment}
                onChange={(e) => setReviewComment(e.target.value)}
              ></textarea>

              <input
                className="bg-black text-white px-4 rounded review-submit-btn"
                type="submit"
                value="Submit"
              />
            </form>
          )}

          {editReview && (
            <form
              onSubmit={updateMemberReviewSubmitHandler}
              className="review-section details-modal-content flex flex-col h-80 items-center justify-evenly"
            >
              <label
                className="bg-red-600 w-3/4 text-white text-center rounded"
                htmlFor="ratings"
              >
                Rating
              </label>

              <select
                className="w-3/4 bg-gray-200 py-1 rounded px-2"
                id="ratings"
                value={editReviewRating}
                onChange={(e) => setEditReviewRating(e.target.value)}
              >
                <option value="" selected disabled>
                  Select...
                </option>
                <option value="1">1 - Poor</option>
                <option value="2">2 - Fair</option>
                <option value="3">3 - Good</option>
                <option value="4">4 - Very Good</option>
                <option value="5">5 - Excellent</option>
              </select>

              <textarea
                className="w-3/4 border border-gray-400 h-32 rounded px-2 py-1"
                type="text"
                required
                placeholder="write a review"
                value={editReviewComment}
                onChange={(e) => setEditReviewComment(e.target.value)}
              ></textarea>

              <input
                className="bg-black text-white px-4 rounded"
                type="submit"
                value="Submit"
              />
            </form>
          )}

          {editContact && (
            <form
              onSubmit={updateMemberContactSubmitHandler}
              className="flex flex-col px-5 details-modal-content"
            >
              <h4 className="font-roboto font-bold mb-2">Contacts</h4>
              <label className="mb-1 ml-2" htmlFor="location">
                Location
              </label>
              <input
                id="location"
                className="border-b border-gray-700 pb-1"
                type="text"
                value={memberLocation}
                onChange={(e) => setMemberLocation(e.target.value)}
              />

              <label className="mt-3 mb-2 ml-2" htmlFor="phone">
                Mobile number
              </label>
              <input
                id="phone"
                className="border-b border-gray-700 pb-1"
                type="text"
                value={memberPhone}
                onChange={(e) => setMemberPhone(e.target.value)}
              />

              <label className="mt-3 mb-2 ml-2" htmlFor="lLink">
                Location Link
              </label>
              <input
                id="lLink"
                className="border-b border-gray-700 pb-1"
                type="text"
                value={memberLocation}
                onChange={(e) => setMemberLocation(e.target.value)}
              />

              <label className="mt-3 mb-2 ml-2" htmlFor="email">
                Email
              </label>
              <input
                id="email"
                className="border-b border-gray-700 pb-1"
                type="text"
                value={memberEmail}
                onChange={(e) => setMemberEmail(e.target.value)}
              />

              <label className="mt-3 mb-2 ml-2" htmlFor="TW">
                Twitter username
              </label>
              <input
                id="TW"
                className="border-b border-gray-700 pb-1"
                type="text"
                value={memberTwitter}
                onChange={(e) => setMemberTwitter(e.target.value)}
              />

              <label className="mt-3 mb-2 ml-2" htmlFor="FB">
                Facebook username
              </label>
              <input
                id="FB"
                className="border-b border-gray-700 pb-1"
                type="text"
                value={memberFB}
                onChange={(e) => setMemberFB(e.target.value)}
              />
              <input
                className="bg-black text-white block mx-auto px-5 my-6 rounded "
                type="submit"
                value="Update"
              />
            </form>
          )}

          {editAbout && (
            <form
              onSubmit={updateDescriptionSubmitHandler}
              className="flex flex-col items-center details-modal-content"
            >
              <textarea
                className="w-full border border-gray-400 h-32 rounded px-2 py-1"
                type="text"
                required
                placeholder="Describe yourself"
                value={about}
                onChange={(e) => setAbout(e.target.value)}
              ></textarea>

              <input
                className="bg-black text-white block mx-auto px-5 my-6 rounded"
                type="submit"
                value="Update"
              />
            </form>
          )}

          {editBannerDetails && (
            <form
              onSubmit={updateBannerDetailsSubmitHandler}
              className="flex flex-col items-center text-white pb-4 bg-gray-500 rounded-b-lg details-modal-content"
              encType="multipart/formdata"
            >
              <label htmlFor="BannerImage">Banner Image</label>
              <input
                id="bannerImage"
                onChange={(e) => setMemberBp(e.target.files)}
                type="file"
              />

              <input
                className="my-2 border-b-2 bg-transparent border-white "
                type="text"
                value={memberExperience}
                maxLength={2}
                placeholder="years of experience"
                onChange={(e) => setMemberExperience(e.target.value)}
              />

              <input
                className="my-2 border-b-2 bg-transparent border-white"
                type="text"
                value={memberWebsite}
                placeholder="your website"
                onChange={(e) => setMemberWebsite(e.target.value)}
              />

              <input
                className="bg-black text-white w-20 rounded mt-4"
                type="submit"
                value="Update"
              />
            </form>
          )}

          {editProfileImage && (
            <form
              onSubmit={updateDpSubmitHandler}
              className="flex flex-col items-center pb-4 bg-gray-500 rounded-b-lg details-modal-content"
              encType="multipart/formdata"
            >
              <input
                onChange={(e) => setMemberDp(e.target.files)}
                type="file"
              />
              <input
                className="bg-black text-white w-20 rounded mt-4"
                type="submit"
                value="Update"
              />
            </form>
          )}
        </div>
      </details>
    </div>
  );
}

export default Reviewmodal;
