import {
  USER_LOGIN_REQUEST,
  USER_LOGIN_SUCCESS,
  USER_LOGIN_FAIL,
  USER_LOGOUT,
  MEMBER_LOGOUT_REQUEST,
  MEMBER_LOGOUT_SUCCESS,
  MEMBER_LOGOUT_FAIL,
  MEMBER_LOGOUT_RESET,
  USER_REGISTER_REQUEST,
  USER_REGISTER_SUCCESS,
  USER_REGISTER_FAIL,
  USER_DETAILS_RESET,
  USER_DETAILS_REQUEST,
  USER_DETAILS_SUCCESS,
  USER_DETAILS_FAIL,
  USER_DETAILS_UPDATE_REQUEST,
  USER_DETAILS_UPDATE_SUCCESS,
  USER_DETAILS_UPDATE_FAIL,
  USER_DETAILS_UPDATE_RESET,
  USER_DELETE_REQUEST,
  USER_DELETE_SUCCESS,
  USER_DELETE_FAIL,
  USER_DELETE_RESET,
  UPDATE_USER_TOKEN_REQUEST,
  UPDATE_USER_TOKEN_SUCCESS,
  UPDATE_USER_TOKEN_FAIL,
} from "../constants/userConstants";

export const userRegisterReducer = (state = {}, action) => {
  switch (action.type) {
    case USER_REGISTER_REQUEST:
      return { loading: true };

    case USER_REGISTER_SUCCESS:
      return { loading: false, userInfo: action.payload };

    case USER_REGISTER_FAIL:
      return { loading: false, error: action.payload };

    case USER_LOGOUT:
      return {};

    default:
      return state;
  }
};

export const userLoginReducer = (state = {}, action) => {
  switch (action.type) {
    case USER_LOGIN_REQUEST:
      return { loading: true };

    case USER_LOGIN_SUCCESS:
      return { loading: false, success: true, userInfo: action.payload };

    case USER_LOGIN_FAIL:
      return { loading: false, error: action.payload };

    case USER_LOGOUT:
      return {};

    default:
      return state;
  }
};

export const memberLogoutReducer = (state = {}, action) => {
  switch (action.type) {
    case MEMBER_LOGOUT_REQUEST:
      return { loading: true };

    case MEMBER_LOGOUT_SUCCESS:
      return {
        loading: false,
        success: true,
        message: "Member logged out successfully",
      };

    case MEMBER_LOGOUT_FAIL:
      return { loading: false, error: action.payload };

    case MEMBER_LOGOUT_RESET:
      return {};

    default:
      return state;
  }
};

export const userDetailsReducer = (state = { user: {} }, action) => {
  switch (action.type) {
    case USER_DETAILS_REQUEST:
      return { ...state, loading: true };

    case USER_DETAILS_SUCCESS:
      return { loading: false, success: true, user: action.payload };

    case USER_DETAILS_FAIL:
      return { loading: false, error: action.payload };

    case USER_DETAILS_RESET:
      return { user: {} };

    default:
      return state;
  }
};

export const userUpdateReducer = (state = {}, action) => {
  switch (action.type) {
    case USER_DETAILS_UPDATE_REQUEST:
      return { loading: true };

    case USER_DETAILS_UPDATE_SUCCESS:
      return {
        loading: false,
        success: true,
        message: "Updated Successfully",
        userInfo: action.payload,
      };

    case USER_DETAILS_UPDATE_FAIL:
      return { loading: false, error: action.payload };

    case USER_DETAILS_UPDATE_RESET:
      return {};

    default:
      return state;
  }
};

export const userDeleteReducer = (state = {}, action) => {
  switch (action.type) {
    case USER_DELETE_REQUEST:
      return { loading: true };

    case USER_DELETE_SUCCESS:
      return { loading: false, success: true, message: "Deleted Successfully" };

    case USER_DELETE_FAIL:
      return { loading: false, error: action.payload };

    case USER_DELETE_RESET:
      return {};

    default:
      return state;
  }
};

export const updateUserTokenReducer = (state = {}, action) => {
  switch (action.type) {
    case UPDATE_USER_TOKEN_REQUEST:
      return { loading: true };

    case UPDATE_USER_TOKEN_SUCCESS:
      return { loading: false, success: true, authToken: action.payload };

    case UPDATE_USER_TOKEN_FAIL:
      return { loading: false, error: action.payload };

    default:
      return state;
  }
};
