import {
  PAYMENT_PLAN_REQUEST,
  PAYMENT_PLAN_SUCCESS,
  PAYMENT_PLAN_FAIL,
  PLAN_STATUS_REQUEST,
  PLAN_STATUS_SUCCESS,
  PLAN_STATUS_FAIL,
  PLAN_STATUS_RESET,
} from "../constants/paymentConstants";

export const paymentPlanReducer = (state = { paymentplan: {} }, action) => {
  switch (action.type) {
    case PAYMENT_PLAN_REQUEST:
      return { loading: true };

    case PAYMENT_PLAN_SUCCESS:
      return {
        loading: false,
        success: true,
        paymentplan: action.payload,
      };

    case PAYMENT_PLAN_FAIL:
      return { loading: false, error: action.payload };

    default:
      return state;
  }
};

export const planStatusReducer = (state = { planstatus: {} }, action) => {
  switch (action.type) {
    case PLAN_STATUS_REQUEST:
      return { loading: true };

    case PLAN_STATUS_SUCCESS:
      return {
        loading: false,
        success: true,
        planstatus: action.payload,
      };

    case PLAN_STATUS_FAIL:
      return { loading: false, error: action.payload };

    case PLAN_STATUS_RESET:
      return {};

    default:
      return state;
  }
};
