import React, { useState, useEffect } from "react";
import { usePaystackPayment } from "react-paystack";
import { useHistory } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { paymentPlanAction } from "../actions/paymentActions";
import { membersList } from "../actions/chamberActions";

function PaymentButton({ price, profile, name, memberType, email, id }) {
  const [memberPlan, setMemberPlan] = useState("");

  const convertCurrencyFormat = (amount) => {
    let convertedAmount = String(Number(amount) * 417).split(",");
    convertedAmount = Number(convertedAmount * 100);
    return convertedAmount;
  };

  let Config = {
    reference: new Date().getTime().toString(),
    email,
    amount: convertCurrencyFormat(price),
    publicKey: "pk_test_f70cd128896486bc7bf4ca00d302ea0573268231",
  };

  const history = useHistory();
  const dispatch = useDispatch();

  const userLogin = useSelector((state) => state.userLogin);

  const { userInfo } = userLogin;

  const membersListContainer = useSelector((state) => state.membersListing);
  const { members, success } = membersListContainer;

  const premiumMember = useSelector((state) => state.paymentPlan);
  const { success: planSuccess, plan: premiumPlan, loading } = premiumMember;

  useEffect(() => {
    try {
      members.find((member) => member.member == id && setMemberPlan(member));
    } catch (e) {
      return e;
    }
  }, [members]);

  useEffect(() => {
    if (planSuccess) {
      dispatch(membersList());
    }
  }, [planSuccess]);

  const onSuccess = (reference) => {
    // Implementation for whatever you want to do with reference and after success call.
    dispatch(paymentPlanAction(id, price));
  };

  const onClose = () => {
    // implementation for  whatever you want to do when the Paystack dialog closed.
  };

  const InitializePayment = usePaystackPayment(Config);

  return (
    <div
      style={{ height: "450px" }}
      className="mt-2 bg-gray-100 border-2 border-gray-300 standard w-80 rounded-2xl"
    >
      <h1 className="mt-4 text-xl font-bold text-center font-roboto">{name}</h1>
      <h1 className="text-3xl font-bold text-center font-roboto">
        <sup className="text-sm">$</sup>
        {price}/<sub className="relative text-sm bottom-1">mo</sub>
      </h1>
      <div
        style={{ height: "1px" }}
        className="w-64 mx-auto my-5 bg-gray-300 heading-line"
      ></div>
      <ul className="flex flex-col h-56 ml-6 text-sm benefits font-roboto justify-evenly">
        <li className="">
          <i class="fas fa-check mr-2 text-gray-400"></i>
          {profile}
        </li>
        <li className="">
          <i class="fas fa-check mr-2 text-gray-400"></i>
          Enhanced Firm Directory listing
        </li>

        <li className="">
          <i class="fas fa-check mr-2 text-gray-400"></i>Live Messaging
        </li>
        <li className="">
          <i class="fas fa-check mr-2 text-gray-400"></i>Voice Call
        </li>
        <li className="">
          <i class="fas fa-check mr-2 text-gray-400"></i>Video Call
        </li>
      </ul>

      <button
        onClick={() => {
          try {
            if (!userInfo) {
              return history.push("/signin");
            }

            if (
              (userInfo &&
                memberType.is_lawyer &&
                price == "30" &&
                memberPlan.is_verified &&
                !memberPlan.lexerati &&
                !memberPlan.firm &&
                !memberPlan.elite_firm) ||
              (userInfo &&
                memberType.is_lawfirm &&
                price == "50" &&
                memberPlan.is_verified &&
                !memberPlan.lexerati &&
                !memberPlan.firm &&
                !memberPlan.elite_firm) ||
              (userInfo &&
                memberType.is_lawfirm &&
                price == "150" &&
                memberPlan.is_verified &&
                !memberPlan.lexerati &&
                !memberPlan.firm &&
                !memberPlan.elite_firm)
            ) {
              InitializePayment(onSuccess, onClose);
            } else {
              alert("NO Plan");
            }

            if (
              (userInfo &&
                memberType.is_lawyer &&
                price == "30" &&
                memberPlan.is_verified &&
                (memberPlan.lexerati ||
                  memberPlan.firm ||
                  memberPlan.elite_firm)) ||
              (userInfo &&
                memberType.is_lawfirm &&
                price == "50" &&
                memberPlan.is_verified &&
                (memberPlan.lexerati ||
                  memberPlan.firm ||
                  memberPlan.elite_firm)) ||
              (userInfo &&
                memberType.is_lawfirm &&
                price == "150" &&
                memberPlan.is_verified &&
                (memberPlan.lexerati ||
                  memberPlan.firm ||
                  memberPlan.elite_firm))
            ) {
              return alert("You're on a plan already!");
            }
          } catch (e) {
            alert("No Internet Connection!");
          }

          if (userInfo && !memberPlan.is_verified) {
            alert("You' ve not been verified");
          }

          // if (userInfo && !memberType.is_lawyer) {
          //   alert("This plan is made for lawyers only!");
          // }
        }}
        style={{ width: "85%", isolation: "isolate" }}
        className="block w-3/4 h-12 mx-auto mt-8 font-semibold text-white border rounded-lg outline-none bg-brandred font-roboto"
      >
        Choose Plan
        <i class="fas fa-arrow-right ml-2 text-white text-xs"></i>
      </button>
    </div>
  );
}

export default PaymentButton;
