import React, { useState, useEffect } from "react";
import "./Blog.css";
import Navbar from "../components/HOME/Navbar";
import { useDispatch, useSelector } from "react-redux";
import { Link, useHistory } from "react-router-dom";
import { listPosts } from "../actions/BlogActions";
import ReactPaginate from "react-paginate";
import headerimg from "../images/read.jpg";
import { LazyLoadComponent } from "react-lazy-load-image-component";

function Blog({ history }) {
  const [keyword, setKeyword] = useState("");
  const [pageNumber, setPageNumber] = useState(0);

  const mainHistory = useHistory();

  let kw = history.location.search;

  const dispatch = useDispatch();
  const blogPosts = useSelector((state) => state.blogPosts);

  const { loading, success, posts } = blogPosts;

  const postsPerPage = 2;
  const pageCount = success && Math.ceil(posts.length / postsPerPage);
  const pagesVisited = pageNumber * postsPerPage;

  const changePage = ({ selected }) => {
    setPageNumber(selected);
  };

  useEffect(() => {
    dispatch(listPosts(kw));
  }, [dispatch, kw]);

  const searchSubmitHandler = (e) => {
    e.preventDefault();
    return keyword && mainHistory.push(`/blog/?search=${keyword}`);
  };
  return (
    <div>
      <LazyLoadComponent>
        <div
          style={{ backgroundImage: `url(${headerimg})`, height: "500px" }}
          className="bg-cover bg-center relative"
        >
          <div className="overlay h-full w-full bg-black absolute opacity-20 top-0 left-0"></div>
          <Navbar height="500px" />
          <h1 className="text-5xl font-bold font-roboto text-white relative z-20 text-center mb-4">
            BLOG
          </h1>
          <p className="text-white text-center font-roboto relative z-20 px-6">
            Learn more about the latest wins, hits and tips in the legal indusry
          </p>
        </div>
      </LazyLoadComponent>

      <div
        style={{ width: "350px" }}
        className="form-container mt-5 relative border rounded-md border-gray-400 mx-auto mb-12 h-12"
      >
        <form onSubmit={searchSubmitHandler} className="search-form h-full">
          <input
            style={{ width: "300px" }}
            type="text"
            className="search-field h-full border-0 outline-none px-4 pb-1 font-roboto text-md"
            placeholder="search"
            onChange={(e) => setKeyword(e.target.value)}
          />

          <button className="bg-black h-full w-12 absolute right-0 rounded-r-md">
            <i class="fas fa-search text-white search-icon"></i>
          </button>
        </form>
      </div>
      <h1 className="font-roboto text-3xl font-bold text-center text-black mb-2 px-4">
        Check out our Latest Posts
      </h1>
      <p className="text-center font-roboto text-gray-600 mb-16 px-4">
        Lorem ipsum dolor sit amet, consectetur adipisicing elit. Minima,
        commodi consectetur.
      </p>
      <div className="image-container mx-auto px-10 justify-center gap-x-12 gap-y-14">
        {success &&
          posts
            .slice(0, posts.length)
            .slice(pagesVisited, pagesVisited + postsPerPage)
            .map((post) => (
              <div style={{ maxWidth: "350px" }} className="">
                <img
                  className="h-52 w-full object-cover"
                  src={post.thumbnail}
                />
                <h1 className="text-black text-xl w-full font-semibold font-roboto my-2">
                  {/* LAWBOOK: THE NEW LEGAL ERA */}
                  {post.title}
                </h1>
                <p className="text-gray-500 font-roboto text-sm mb-3">
                  {post.caption}
                </p>

                <h1 className="text-gray-500 font-roboto text-xs my-2">
                  By: {post.authorName}
                </h1>
                <Link to={`/post/${post._id}/`}>
                  <button className="bg-black text-white py-1 px-2">
                    Read more
                  </button>
                </Link>
              </div>
            ))}
      </div>
      {posts && posts.length != 0 && (
        <ReactPaginate
          previousLabel={"Previous"}
          nextLabel={"Next"}
          pageCount={pageCount}
          pageClassName={"page-bttn"}
          onPageChange={changePage}
          containerClassName={"pagination-buttons"}
          previousLinkClassName={"previousBttn"}
          nextLinkClassName={"nextBttn"}
          disabledClassName={"pagination-disabled"}
          activeClassName={"pagination-active"}
        />
      )}
    </div>
  );
}

export default Blog;
