import React, { useState, useEffect } from "react";
import { useHistory } from "react-router-dom";
import "./Uploadmodal.css";
import countrydata from "../../countrydata.json";
import langdata from "../../langdata.json";
import { useSelector, useDispatch } from "react-redux";
import { addBook, listBooks } from "../../actions/bookActions";
import { listCategories } from "../../actions/categoryActions";
import { UPLOAD_BOOK_RESET } from "../../constants/bookConstants";
import FlashMessage from "react-flash-message";

function Uploadmodal() {
  const [title, setTitle] = useState("");
  const [description, setDescription] = useState("");
  const [country, setCountry] = useState("Country");
  const [category, setCategory] = useState("Category");
  const [language, setLanguage] = useState("Language");
  const [author, setAuthor] = useState("");
  const [price, setPrice] = useState("");
  const [file, setFile] = useState("");
  const [coverImage, setCoverImage] = useState("");

  const dispatch = useDispatch();
  const history = useHistory();

  const addbook = useSelector((state) => state.uploadBook);
  const { error, success } = addbook;

  const userLogin = useSelector((state) => state.userLogin);
  const { userInfo } = userLogin;

  const bookCategories = useSelector((state) => state.categoriesList);

  const { categoryList, success: categoryListSuccess } = bookCategories;

  useEffect(() => {
    if (success) {
      dispatch({ type: UPLOAD_BOOK_RESET });
    } else {
      dispatch(listBooks());
      dispatch(listCategories());
    }
  }, [success, dispatch]);

  function submitHandler(e) {
    e.preventDefault();
    return userInfo
      ? (dispatch(
          addBook(
            title,
            coverImage,
            file,
            category,
            description,
            country,
            language,
            author,
            price
          )
        ),
        setCountry(""),
        setTitle(""),
        setFile(""),
        setDescription(""),
        setCoverImage(""),
        setLanguage(""),
        setPrice(""),
        setAuthor(""),
        setCategory(""))
      : history.push("signin");
  }

  return (
    <div className="upload-parent-container">
      <div className="section">
        <input
          className="modal-btn"
          type="checkbox"
          id="modal-btn"
          name="modal-btn"
        />

        <label
          className="upload-label font-semibold bg-brandred"
          for="modal-btn"
        >
          Upload your book <i class="uil uil-expand-arrows"></i>
        </label>
        <div className="modal">
          <div className="modal-wrap p-3">
            {!success && (
              <FlashMessage duration={3000}>
                <h3 className="font-roboto bg-green-400 h-8 text-white w-full rounded text-center mx-auto pt-1 my-2">
                  Book uploaded successfully!
                </h3>
              </FlashMessage>
            )}

            {error && (
              <FlashMessage duration={2000}>
                <h3 className="text-xs font-roboto bg-red-100 h-6 text-red-900 w-full rounded text-center mx-auto pt-1 mt-5">
                  {error}
                </h3>
              </FlashMessage>
            )}
            <form
              onSubmit={submitHandler}
              className="upload-form flex flex-col"
            >
              <label className="" htmlFor="title">
                Title
              </label>
              <input
                className="border border-gray-900 mb-4 rounded"
                id="title"
                type="text"
                required
                value={title}
                onChange={(e) => setTitle(e.target.value)}
              />

              <label htmlFor="description">Description</label>
              <textarea
                id="description"
                type="text"
                required
                value={description}
                className="w-full description-section border border-gray-400 outline-none mx-auto block pt-2 mb-4  h-28 rounded"
                placeholder="Type here"
                onChange={(e) => setDescription(e.target.value)}
              ></textarea>

              <select
                className="h-7 rounded-md shadow-lg border-gray-400 border pl-2 pb-1 w-full my-1"
                name="country"
                id="country"
                required
                value={country}
                onChange={(e) => setCountry(e.target.value)}
              >
                <option value="Country" selected disabled hidden>
                  Country
                </option>
                {countrydata.map(({ name, code }) => {
                  return <option value={code}>{name}</option>;
                })}
              </select>

              <select
                className="h-7 rounded-md shadow-lg border border-gray-400 pl-2 pb-1 w-full my-1"
                name="state"
                id="state"
                value={category}
                onChange={(e) => setCategory(e.target.value)}
                required
              >
                <option value="Category" disabled selected hidden>
                  Category
                </option>
                {categoryListSuccess &&
                  categoryList.map((data) => {
                    return data.names.map((e) => <option>{e.name}</option>);
                  })}
              </select>

              <select
                className="h-7 rounded-md shadow-lg border border-gray-400 pl-2 pb-1 w-full my-1"
                name="city"
                id="city"
                required
                value={language}
                onChange={(e) => setLanguage(e.target.value)}
              >
                <option value="Language" selected disabled hidden>
                  Language
                </option>
                {langdata.map((e) =>
                  Object.keys(e).map((data) => {
                    return <option value={data}>{data}</option>;
                  })
                )}
              </select>

              <label
                className=" text-sm mt-3 font-semibold"
                htmlFor="coverImage"
              >
                Book Image
              </label>
              <input
                id="coverImage"
                className="text-sm w-full relative right-2 mb-4 cursor-pointer"
                type="file"
                required
                accept="image/png, image/jpeg"
                onChange={(e) => setCoverImage(e.target.files)}
              />

              <label htmlFor="author">Author</label>
              <input
                className="border border-gray-900 mb-2 rounded"
                id="author"
                required
                value={author}
                type="text"
                onChange={(e) => setAuthor(e.target.value)}
              />

              <label className="" htmlFor="price">
                Price
              </label>
              <div className="price-container relative">
                <input
                  className="border border-gray-900 mb-4 rounded h-7 pl-8 w-full"
                  id="price"
                  type="text"
                  required
                  value={price}
                  placeholder="e.g 10.00"
                  onChange={(e) => setPrice(e.target.value)}
                />
                <span className="absolute top-0 left-0 bg-black text-white text-sm pt-1 w-6 h-7 text-center rounded-l">
                  $
                </span>
              </div>

              <label className=" text-sm font-semibold " htmlFor="bookFile">
                Book File
              </label>

              <input
                id="bookFile"
                className="text-sm w-full relative right-2 mb-4 cursor-pointer"
                type="file"
                name="file"
                required
                accept=".pdf, .doc"
                onChange={(e) => setFile(e.target.files)}
              />

              <input
                className=" text-md text-white bg-black w-1/3 mx-auto rounded cursor-pointer"
                type="submit"
                value="Add Book"
              />
            </form>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Uploadmodal;
