import { useState, useEffect } from "react";

function useInputValidator(value) {
  const [numberState, setNumberState] = useState(false);
  const [letterState, setLetterState] = useState(false);
  const [valueSize, setValueSize] = useState(false);
  

  const numberExpression = /[0-9]/;

  const letterExpression = /[a-zA-Z]/;

  useEffect(() => {
    if (value === "" || value === null) {
      return;
    }

    if (numberExpression.test(value)) {
      setNumberState(true);
    } else {
      return setNumberState(false)
    }

    if (letterExpression.test(value)) {
      setLetterState(true);
    } else {
      return setLetterState(false);
    }
    if (value.length >= 8) {
      setValueSize(true);
    } else {
      return setValueSize(false);
    }
  }, [value]);

  return { numberState, letterState, valueSize };
}

export default useInputValidator;
