import React, { useState, useEffect } from "react";
import Navbar from "../components/HOME/Navbar";
import Rating from "./Rating";
import { Link, useHistory } from "react-router-dom";
import countrydata from "../countrydata.json";
import langdata from "../langdata.json";
import "./Lawbooks.css";
import { useDispatch, useSelector } from "react-redux";
import { listBooks } from "../actions/bookActions";
import { listCategories } from "../actions/categoryActions";
import { planStatusAction } from "../actions/paymentActions";
import { membersList } from "../actions/chamberActions";
import Uploadmodal from "./UploadModal/Uploadmodal";
import ReactLoading from "react-loading";
import ReactPaginate from "react-paginate";
import headerimg from "../images/Education.jpg";
import {
  LazyLoadComponent,
  LazyLoadImage,
} from "react-lazy-load-image-component";

function Lawbooks({ history }) {
  const [country, setCountry] = useState("");
  const [category, setCategory] = useState("");
  const [language, setLanguage] = useState("");
  const [keyword, setKeyword] = useState("");
  const [pageNumber, setPageNumber] = useState(0);

  const dispatch = useDispatch();

  const userLogin = useSelector((state) => state.userLogin);
  const { userInfo, success: userLoginSuccess } = userLogin;

  const memberListing = useSelector((state) => state.membersListing);
  const { members, success: membersSuccess } = memberListing;

  const bookList = useSelector((state) => state.bookList);
  const { error, loading, books, success } = bookList;

  const booksPerPage = 3;
  const pageCount = success && Math.ceil(books.length / booksPerPage);
  const pagesVisited = pageNumber * booksPerPage;

  const changePage = ({ selected }) => {
    setPageNumber(selected);
  };

  //BOOKS PAGINATION

  const bookCategories = useSelector((state) => state.categoriesList);

  const {
    loading: categotyListLoading,
    categoryList,
    success: categoryListSuccess,
  } = bookCategories;

  const mainHistory = useHistory();

  let kw = history.location.search;

  useEffect(() => {
    dispatch(listBooks(kw));
  }, [dispatch, kw]);

  useEffect(() => {
    dispatch(membersList());
    dispatch(listCategories());
  }, []);

  useEffect(() => {
    userInfo &&
      success &&
      members.find(
        (member) =>
          member.member == userInfo.id && dispatch(planStatusAction(member._id))
      );
  }, [membersSuccess]);

  function submitSearchHandler(e) {
    e.preventDefault();

    return keyword && mainHistory.push(`/lawbooks/?search=${keyword}`);
  }

  function handleSubmit(e) {
    e.preventDefault();

    return (
      country &&
      category &&
      language &&
      mainHistory.push(`/lawbooks/?search=${country}+${category}+${language}`)
    );
  }

  function handleFilter() {
    let display = document.querySelector(".filter-mobile");
    display.classList.toggle("hidden");
  }

  return (
    <div>
      <LazyLoadComponent>
        <div
          className="bg-cover bg-center relative main-container"
          style={{
            backgroundImage: `url(${headerimg})`,
            height: "500px",
          }}
        >
          <Navbar />
          <div className="overlay h-full w-full bg-black absolute opacity-30 top-0 left-0"></div>
          <h1 className="text-center text-5xl font-bold text-white mb-8 relative z-20 bg-transparent">
            LAW BOOKS
          </h1>
          <p className="text-white text-base font-roboto text-center relative z-20 px-4 bg-transparent">
            Mind your manners. Tell the truth, Know the law.
          </p>
        </div>
      </LazyLoadComponent>

      <h1 className="search-manual text-center mt-14 bg-transparent">
        Search our archives for legal resources to aid your research
      </h1>

      <div
        style={{ width: "350px" }}
        className="form-container mt-5 relative border rounded-md border-gray-400 mx-auto mb-12 h-12"
      >
        <form onSubmit={submitSearchHandler} className="search-form h-full">
          <input
            style={{ width: "300px" }}
            type="text"
            className="search-field h-full border-0 outline-none px-4 pb-1 font-roboto text-md"
            placeholder="search"
            onChange={(e) => setKeyword(e.target.value)}
          />

          <button className="search-button h-full w-12 absolute right-0 rounded-r-md bg-black">
            <i class="fas fa-search search-icon text-white"></i>
          </button>
        </form>
      </div>
      <div className="filter-container flex flex-wrap justify-center mb-72">
        <div className="filter-books">
          <div
            style={{ width: "237px" }}
            className="filter rounded-lg border border-gray-400 h-80 mb-3"
          >
            <h1 className="font-roboto text-white bg-black w-full text-center h-8 font-semibold pt-1 rounded-t-md">
              Filter
            </h1>
            <form
              onSubmit={handleSubmit}
              style={{ height: "83%" }}
              className="flex flex-col items-center justify-around text-gray-800"
            >
              <select
                className="h-9 rounded-md shadow-lg border-gray-400 border pl-3 pb-1 w-3/4"
                name="country"
                id="country"
                onChange={(e) => setCountry(e.target.value)}
              >
                <option value="country" selected disabled hidden>
                  Country
                </option>
                {countrydata.map(({ name, code }) => {
                  return <option value={code}>{name}</option>;
                })}
              </select>

              <select
                className="h-9 rounded-md shadow-lg border border-gray-400 pl-3 pb-1 w-3/4"
                name="state"
                id="state"
                onChange={(e) => setCategory(e.target.value)}
              >
                <option value="Category" disabled selected hidden>
                  Category
                </option>
                {!categotyListLoading &&
                  categoryListSuccess &&
                  categoryList.map((data) => {
                    return data.names.map((e) => <option>{e.name}</option>);
                  })}

                {/* <option value="Comedy">Comedy</option>
                <option value="Action">Action</option> */}
              </select>

              <select
                className="h-9 rounded-md shadow-lg border border-gray-400 pl-3 pb-1 w-3/4"
                name="city"
                id="city"
                onChange={(e) => setLanguage(e.target.value)}
              >
                <option value="Language" selected hidden disabled>
                  Language
                </option>
                {langdata.map((e) =>
                  Object.keys(e).map((data) => {
                    return <option value={data}>{data}</option>;
                  })
                )}
              </select>

              <input
                className=" filter-btn text-white rounded h-9 w-24 font-semibold bg-black"
                type="submit"
                value="Apply Filter"
              />
            </form>
          </div>
          <div className="upload-parent-container">
            <div className="upload-button m-0">
              <Uploadmodal />
            </div>

            <ul className="upload-section font-roboto text-gray-700 text-sm">
              <li>Free books</li>
              <li>Top books</li>
              <li>Categories</li>
              <li>Audio books</li>
              <li>Law Reports</li>
            </ul>
          </div>
        </div>

        <div className="books-container ml-8">
          <h1 className="heading-text font-roboto font-semibold text-xl bg-transparent">
            Check out our Latest Posts
          </h1>
          <h3 className="sub-heading font-roboto text-xs text-gray-500 mb-4">
            Lorem ipsum dolor sit amet consectetur adipisicing elit. Eligendi,
            iste!
          </h3>
          <div
            onClick={handleFilter}
            style={{ width: "70px" }}
            className="filter-controller bg-black text-white px-2 rounded font-roboto mx-auto hidden"
          >
            <span className="mr-1">Filter</span>
            <i class="text-sm fas fa-filter"></i>
          </div>
          <div
            style={{ width: "237px" }}
            className="filter-mobile rounded-lg border border-gray-400 h-80 my-3 hidden mx-auto"
          >
            <h1 className="font-roboto bg-black text-white w-full text-center h-8 font-semibold pt-1 rounded-t-md">
              Filter
            </h1>
            <form
              onSubmit={handleSubmit}
              style={{ height: "83%" }}
              className="flex flex-col items-center justify-around"
            >
              <select
                className="h-9 rounded-md shadow-lg border-gray-400 border pl-3 pb-1 w-3/4"
                name="country"
                id="country"
                onChange={(e) => setCountry(e.target.value)}
              >
                <option value="country" selected disabled hidden>
                  Country
                </option>
                {countrydata.map(({ name, code }) => {
                  return <option value={code}>{name}</option>;
                })}
              </select>

              <select
                className="h-9 rounded-md shadow-lg border border-gray-400 pl-3 pb-1 w-3/4"
                name="state"
                id="state"
                onChange={(e) => setCategory(e.target.value)}
              >
                <option value="Category" disabled selected hidden>
                  Category
                </option>
                {categoryListSuccess &&
                  categoryList.map((data) => {
                    return data.names.map((e) => <option>{e.name}</option>);
                  })}
              </select>

              <select
                className="h-9 rounded-md shadow-lg border border-gray-400 pl-3 pb-1 w-3/4"
                name="city"
                id="city"
                onChange={(e) => setLanguage(e.target.value)}
              >
                <option value="Language" selected hidden disabled>
                  Language
                </option>
                {langdata.map((e) =>
                  Object.keys(e).map((data) => {
                    return <option value={data}>{data}</option>;
                  })
                )}
              </select>

              <input
                className="bg-black filter-btn text-white font-roboto rounded h-9 w-24 font-semibold"
                type="submit"
                value="Apply Filter"
              />
            </form>
          </div>

          {loading ? (
            <div className="flex justify-center items-center mt-8">
              <ReactLoading type="spin" color="#000" height={50} width={50} />
            </div>
          ) : error ? (
            <h2 className="text-sm text-red-600 bg-red-50 p-3">{error}</h2>
          ) : success && books.count == 0 ? (
            <h2 className="sorry-text text-red-600">sorry nothing found!</h2>
          ) : (
            <div className="books-shelf grid grid-cols-4 gap-6">
              {success &&
                books
                  .slice(0, 4)
                  .slice(pagesVisited, pagesVisited + booksPerPage)
                  .map((response) => {
                    return (
                      <div className="lawbook my-4">
                        {success && (
                          <Link
                            to={`/bookdetails/${response.category}/${response._id}`}
                          >
                            <img
                              className="h-32 w-24 border-gray-400 border rounded-sm"
                              src={response.coverImage}
                              alt="ch"
                            />
                            <h1
                              style={{ fontSize: "11px" }}
                              className="font-roboto font-bold bg-transparent"
                            >
                              {response.title}
                            </h1>
                            <h3
                              style={{ fontSize: "11px" }}
                              className="font-roboto"
                            >
                              {response.author}
                            </h3>
                          </Link>
                        )}

                        <div
                          style={{ isolation: "isolate" }}
                          className="ratings-container"
                        >
                          <Rating
                            value={response.rating}
                            color={"text-yellow-500"}
                          />
                        </div>

                        <h3 className="font-roboto text-gray-600 text-xs">
                          {response.category}
                        </h3>
                        <button
                          style={{ isolation: "isolate" }}
                          className="font-roboto w-16 rounded bg-brandred text-white my-1 font-semibold text-sm h-6"
                        >
                          {response.price === 0 || response.price === null
                            ? "Free"
                            : `$${response.price}`}
                        </button>
                      </div>
                    );
                  })}
            </div>
          )}

          {books && books.length != 0 && (
            <ReactPaginate
              previousLabel={"Previous"}
              nextLabel={"Next"}
              pageCount={pageCount}
              onPageChange={changePage}
              containerClassName={"pagination-buttons"}
              previousLinkClassName={"previousBttn"}
              nextLinkClassName={"nextBttn"}
              disabledClassName={"pagination-disabled"}
              activeClassName={"pagination-active"}
            />
          )}
        </div>
      </div>
    </div>
  );
}

export default Lawbooks;
