import React, { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import "./Newsletter.css";
import FlashMessage from "react-flash-message";
import ReactLoading from "react-loading";
import { addEmail } from "../actions/mailActions";

function Newsletter() {
  const [input, setInput] = useState("");

  const dispatch = useDispatch();

  const addedEmail = useSelector((state) => state.addSubscriberEmail);

  const { success, loading, error, message } = addedEmail;

  const formSubmitHandler = (e) => {
    e.preventDefault();

    dispatch(addEmail(input));

    loading == false && success == true && setInput("");
  };

  return (
    <div>
      <h1 className="text-center font-bold mt-32 text-lg mb-6 px-4 bg-transparent">
        Subscribe to our newsletter for updates
      </h1>
      {loading ? (
        <div className="flex justify-center items-center mt-8">
          <ReactLoading type="spin" color="#000" height={30} width={30} />
        </div>
      ) : success ? (
        <FlashMessage duration={2000}>
          <h1
            style={{ maxWidth: "450px" }}
            className="text-center mt-6 text-lg mb-6 mx-auto px-4 py-1 rounded text-white bg-green-400"
          >
            {message.detail}
          </h1>
        </FlashMessage>
      ) : error ? (
        <FlashMessage duration={2000}>
          <h1
            style={{ maxWidth: "450px" }}
            className="text-center mt-6 text-lg mb-6 mx-auto px-4 py-1 rounded text-white bg-red-400"
          >
            {error}
          </h1>
        </FlashMessage>
      ) : (
        ""
      )}

      <div
        style={{ width: "450px" }}
        className="form-container mt-6 relative border rounded-md  mx-auto"
      >
        <form onSubmit={formSubmitHandler} className="w-full m-0">
          <input
            style={{ width: "75%" }}
            type="email"
            className="h-10 border border-gray-300 outline-none pl-3 pb-1 text-lg bg-transparent"
            placeholder="your email address"
            value={input}
            onChange={(e) => setInput(e.target.value)}
          />
          <input
            style={{ width: "25%" }}
            type="submit"
            value="subscribe"
            className="newsletter-btn bg-gray-700 text-white absolute right-0 h-10 rounded cursor-pointer"
          />
        </form>
      </div>
    </div>
  );
}

export default Newsletter;
