import {
  ADD_EMAIL_REQUEST,
  ADD_EMAIL_SUCCESS,
  ADD_EMAIL_FAIL,

  SEND_EMAIL_REQUEST,
  SEND_EMAIL_SUCCESS,
  SEND_EMAIL_FAIL,


} from "../constants/mailConstants";





export const addEmailReducer = (state = {}, action) => {
  switch (action.type) {
    case ADD_EMAIL_REQUEST:
      return { loading: true };

    case ADD_EMAIL_SUCCESS:
      return { loading: false, success: true, message: action.payload };

    case ADD_EMAIL_FAIL:
      return { loading: false, error: action.payload };

    default:
      return state;
  }
};


export const sendEmailReducer = (state = {}, action) => {
  switch (action.type) {
    case SEND_EMAIL_REQUEST:
      return { loading: true };

    case SEND_EMAIL_SUCCESS:
      return { loading: false, success: true, state: action.payload };

    case SEND_EMAIL_FAIL:
      return { loading: false, error: action.payload };

    default:
      return state;
  }
};
