import {
  BOOKMARK_LIST_REQUEST,
  BOOKMARK_LIST_SUCCESS,
  BOOKMARK_LIST_FAIL,
  CREATE_BOOKMARK_REQUEST,
  CREATE_BOOKMARK_SUCCESS,
  CREATE_BOOKMARK_FAIL,
  DELETE_BOOKMARK_REQUEST,
  DELETE_BOOKMARK_SUCCESS,
  DELETE_BOOKMARK_FAIL,
  BOOKMARK_LAWYER_LIST_REQUEST,
  BOOKMARK_LAWYER_LIST_SUCCESS,
  BOOKMARK_LAWYER_LIST_FAIL,
  CREATE_BOOKMARK_LAWYER_REQUEST,
  CREATE_BOOKMARK_LAWYER_SUCCESS,
  CREATE_BOOKMARK_LAWYER_FAIL,
  DELETE_BOOKMARK_LAWYER_REQUEST,
  DELETE_BOOKMARK_LAWYER_SUCCESS,
  DELETE_BOOKMARK_LAWYER_FAIL,
} from "../constants/bookmarkConstants";

import axios from "axios";

import useCustomInterceptor from "../customInterceptor/useCustomInterceptor";

export const listBookmarkBooks = () => async (dispatch, getState) => {
  try {
    dispatch({ type: BOOKMARK_LIST_REQUEST });

    const api = useCustomInterceptor();

    const userInfo = JSON.parse(localStorage.getItem("userInfo"));

    const config = {
      headers: {
        "Content-type": "application/json",
        Authorization: `Bearer ${userInfo.token}`,
      },
    };

    const { data } = await api.get(`/bookmarkings/getbookmark-books/`, config);

    console.log(data);

    dispatch({ type: BOOKMARK_LIST_SUCCESS, payload: data });
  } catch (error) {
    dispatch({
      type: BOOKMARK_LIST_FAIL,
      payload:
        error.response && error.response.data.detail
          ? error.response.data.detail
          : error.message,
    });
  }
};

