export const createBookBookmark = (id) => async (dispatch, getState) => {
  try {
    dispatch({
      type: CREATE_BOOKMARK_REQUEST,
    });

    const api = useCustomInterceptor();

    const userInfo = JSON.parse(localStorage.getItem("userInfo"));

    const config = {
      headers: {
        "Content-type": "application/json",
        Authorization: `Bearer ${userInfo.token}`,
      },
    };

    const { data } = await api.post(
      `/bookmarkings/addbookmark-book/${id}/`,
      "",
      config
    );

    dispatch({
      type: CREATE_BOOKMARK_SUCCESS,
      payload: data,
    });
  } catch (error) {
    dispatch({
      type: CREATE_BOOKMARK_FAIL,
      payload:
        error.response && error.response.data.detail
          ? error.response.data.detail
          : error.message,
    });
  }
};

