import React from "react";
import "./Testimonial.css";
import img1 from "../../images/christina.jpg";
import img2 from "../../images/charles.jpg";
import img3 from "../../images/joel.jpg";
import { LazyLoadImage } from "react-lazy-load-image-component";

function Testimonial(props) {
  return (
    <div>
      <h1 className="text-center text-3xl font-semibold my-14 main-heading bg-transparent">
        {props.title}
      </h1>
      <div className="container flex w-3/5 justify-around mx-auto">
        <div>
          <LazyLoadImage
            alt="img1"
            effect="blur"
            src={img1}
            className="w-32 h-32 object-cover rounded-full block mx-auto mb-5"
          />

       

          <span className="inline-block w-64 text-center font-semibold text-gray-500 text-xs px-2">
            Lexquiry offers a viable market place for lawyers - giving them the
            much needed visibility they deserve.
          </span>
        </div>
        <div>
          <LazyLoadImage
            alt="img2"
            effect="blur"
            src={img2}
            className="w-32 h-32 object-cover rounded-full block mx-auto mb-5"
          />

          <span className="inline-block w-52 text-center font-semibold text-gray-500 text-xs px-2">
            This is a handy office for the millenial lawyer. i love it.
          </span>
        </div>
        <div>
          <LazyLoadImage
            alt="img3"
            effect="blur"
            src={img3}
            className="w-32 h-32 object-cover rounded-full block mx-auto mb-5"
          />

          <span className="inline-block text-center w-52  font-semibold text-gray-500 text-xs px-2">
            When we talk of the future of the legal profession, we are referring
            to things like this.
          </span>
        </div>
      </div>
    </div>
  );
}

export default Testimonial;
