import React from "react";
import "./Footer.css";
import { Link } from "react-router-dom";

function Footer() {
  return (
    <div className="big-container">
      <div className="bg-gray-300 main-container">
        <div className="scroll-up h-12 w-10 bg-brandred text-gray-300 absolute right-0 rounded-b">
          <span
            onClick={() => {
              document.getElementById("top-navigator").scrollIntoView();
            }}
            className="block w-full h-full"
          >
            <i class="fas fa-long-arrow-alt-up text-2xl block text-center pt-2"></i>
          </span>
        </div>

        <div style={{ maxWidth: "500px" }} className="container mx-auto">
          <ul className="container-child-1 flex flex-wrap justify-evenly mb-1 pt-10 text-base font-bold text-gray-700 mx-auto">
            <li>
              <Link to="/about">About us</Link>
            </li>
            <li>
              <Link to="/blog">Blog</Link>
            </li>
            <li>
              <Link to="/community">Community</Link>
            </li>
          </ul>
          <ul className="container-child-2 flex w-96 flex-wrap justify-around text-sm font-semibold text-gray-700 mx-auto">
            <li>Terms of service</li>
            <li>
              <Link to="/privacypolicy">Privacy Policy</Link>
            </li>
            <li>
              <Link to="/faq">FAQ</Link>
            </li>
            <li>
              <a href="mailto:lexquiry.com">Help</a>
            </li>
          </ul>

          <ul className="socials-btn flex w-28 justify-around mx-auto text-2xl mt-3">
            <li className="">
              <i class="fab fa-instagram-square"></i>
            </li>
            <li className="">
              <i class="fab fa-facebook-square"></i>
            </li>
            <li className="">
              <i class="fab fa-youtube"></i>
            </li>
            <li className="">
              <i class="fab fa-twitter-square"></i>
            </li>
          </ul>
          <span className="footer-text block text-center text-xs text-gray-700 font-bold py-3">
            Copyright Metalex Ltd 2021
          </span>
        </div>
        <div className="pb-4"></div>
      </div>
    </div>
  );
}

export default Footer;
