import React from "react";
import "./App.css";
import Home from "./components/HOME/Home";
import Scroller from "./components/scroller/Scroller";
import Category from "./components/HOME/categories/Category";
import Testimonial from "./components/HOME/Testimonial";
import Newsletter from "./components/Newsletter";
import Footer from "./components/Footer";
import About from "./components/ABOUT/About";
import Blog from "./components/Blog";
import Signup from "./components/Signup";
import Signin from "./components/Signin";
import Community from "./components/Community";
import Lawbooks from "./components/Lawbooks";
import Bookdetails from "./components/Bookdetails";
import Memberprofile from "./components/Memberprofile";
import Findmembers from "./components/Findmembers";
import Bypractice from "./components/Bypractice";
import Profile from "./components/Profile";
import Notifications from "./components/Notifications";
import Privacypolicy from "./components/Privacypolicy";
import Faq from "./components/FAQ/Faq";
import Membercategories from "./components/Membercategories";
import Pricing from "./components/Pricing";
import Blogcontent from "./components/Blog/Blogcontent";
import Pagenotfound from "./components/PageNotFound/Pagenotfound";
import Chamberverification from "./components/Chamberverification";
import Bookmarks from "./components/Bookmarks";
import { HashRouter as Router, Route, Switch } from "react-router-dom";
import Darkmode from "darkmode-js";

function App() {
  const options = {
    label: "", // default: ''
  };
  const darkmode = new Darkmode(options);
  darkmode.showWidget();

  

  

  return (
    <Router>
      <div id="top-navigator">
        <Switch>
          <Route
            exact
            path="/"
            render={() => (
              <div>
                <Home />

                <Scroller />
                <Category />

                <Testimonial title="TESTIMONIALS" />

                <Newsletter title="Subscribe to our newsletter for updates" />
              </div>
            )}
          />

          <Route exact path="/about">
            <About />
          </Route>

          <Route path="/blog" component={Blog} exact />

          <Route path="/post/:id" component={Blogcontent} exact />

          <Route path="/signup" component={Signup} title="SIGN UP AS" exact />
          <Route path="/notifications" component={Notifications} />

          <Route path="/signin" component={Signin} />
          <Route path="/profile" component={Profile} />

          <Route
            path="/community"
            component={Community}
            exact
            title="COMMUNITY"
          />

          <Route path="/lawbooks" component={Lawbooks} exact />

          <Route
            path="/bookdetails/:category/:id"
            component={Bookdetails}
            exact
          />
          <Route
            path="/member-profile/:id/:category"
            component={Memberprofile}
            exact
          />
          <Route path="/by-practice" component={Bypractice} exact />
          <Route path="/findmembers" component={Findmembers} exact />
          <Route path="/member-categories" component={Membercategories} exact />
          <Route path="/pricing" component={Pricing} exact />
          <Route path="/privacypolicy" component={Privacypolicy} exact />
          <Route
            path="/member-verification"
            component={Chamberverification}
            exact
          />
          <Route path="/faq" component={Faq} exact />
          <Route path="/bmk" component={Bookmarks} exact />
          <Route component={Pagenotfound} />
        </Switch>
        <Route component={Footer} />
      </div>
    </Router>
  );
}

export default App;
