import axios from "axios";
import {
  BOOK_LIST_REQUEST,
  BOOK_LIST_SUCCESS,
  BOOK_LIST_FAIL,
  BOOK_DETAILS_REQUEST,
  BOOK_DETAILS_SUCCESS,
  BOOK_DETAILS_FAIL,
  UPLOAD_BOOK_REQUEST,
  UPLOAD_BOOK_SUCCESS,
  UPLOAD_BOOK_FAIL,
  ADD_REVIEW_REQUEST,
  ADD_REVIEW_SUCCESS,
  ADD_REVIEW_FAIL,
  UPDATE_REVIEW_REQUEST,
  UPDATE_REVIEW_SUCCESS,
  UPDATE_REVIEW_FAIL,
  DELETE_REVIEW_REQUEST,
  DELETE_REVIEW_SUCCESS,
  DELETE_REVIEW_FAIL,
} from "../constants/bookConstants";

import useCustomInterceptor from "../customInterceptor/useCustomInterceptor";

export const addBook =
  (
    title,
    bookImage,
    bookfile,
    category,
    description,
    country,
    language,
    author,
    price
  ) =>
  async (dispatch, getState) => {
    try {
      dispatch({
        type: UPLOAD_BOOK_REQUEST,
      });

      const api = useCustomInterceptor();

      const {
        userLogin: { userInfo },
      } = getState();

      const config = {
        headers: {
          "Content-type": "multipart/form-data",
          Authorization: `Bearer ${userInfo.token}`,
        },
      };

      let formData = new FormData();
      formData.append("title", title);
      formData.append("author", author);
      formData.append("category", category);
      formData.append("language", language);
      formData.append("country", country);
      formData.append("description", description);
      formData.append("price", price);
      formData.append("coverImage", bookImage[0]);
      formData.append("file", bookfile[0]);

      const { data } = await api.post("/api/addbook/", formData, config);

      dispatch({
        type: UPLOAD_BOOK_SUCCESS,
        payload: data,
      });
    } catch (error) {
      dispatch({
        type: UPLOAD_BOOK_FAIL,
        payload:
          error.response && error.response.data.detail
            ? error.response.data.detail
            : error.message,
      });
    }
  };

export const listBooks =
  (keyword = "") =>
  async (dispatch) => {
    try {
      dispatch({ type: BOOK_LIST_REQUEST });
      const { data } = await axios.get(`/api/lawbooks${keyword}`);

      dispatch({ type: BOOK_LIST_SUCCESS, payload: data });
    } catch (error) {
      dispatch({
        type: BOOK_LIST_FAIL,
        payload:
          error.response && error.response.data.detail
            ? error.response.data.detail
            : error.message,
      });
    }
  };

export const listBookDetails = (id, category) => async (dispatch) => {
  try {
    dispatch({ type: BOOK_DETAILS_REQUEST });
    const { data } = await axios.get(`/api/bookdetails/${id}/${category}/`);

   

    dispatch({ type: BOOK_DETAILS_SUCCESS, payload: data });
  } catch (error) {
    dispatch({
      type: BOOK_DETAILS_FAIL,
      payload:
        error.response && error.response.data.message
          ? error.response.data.message
          : error.message,
    });
  }
};

export const addReview = (bookId, review) => async (dispatch, getState) => {
  try {
    dispatch({
      type: ADD_REVIEW_REQUEST,
    });

    const api = useCustomInterceptor();

    const {
      userLogin: { userInfo },
    } = getState();

    const config = {
      headers: {
        "Content-type": "application/json",
        Authorization: `Bearer ${userInfo.token}`,
      },
    };

    const { data } = await api.post(
      `/api/${bookId}/addreview/`,
      review,

      config
    );

    dispatch({
      type: ADD_REVIEW_SUCCESS,
      payload: data,
    });
  } catch (error) {
    dispatch({
      type: ADD_REVIEW_FAIL,
      payload:
        error.response && error.response.data.detail
          ? error.response.data.detail
          : error.message,
    });
  }
};

export const updateReview =
  (reviewId, bookId, rating, comment) => async (dispatch, getState) => {
    try {
      dispatch({
        type: UPDATE_REVIEW_REQUEST,
      });

      const api = useCustomInterceptor();

      const {
        userLogin: { userInfo },
      } = getState();

      const config = {
        headers: {
          "Content-type": "application/json",
          Authorization: `Bearer ${userInfo.token}`,
        },
      };

      const { data } = await api.put(
        `/api/${reviewId}/${bookId}/updatereview/`,
        { rating, comment },

        config
      );

      dispatch({
        type: UPDATE_REVIEW_SUCCESS,
        payload: data,
      });
    } catch (error) {
      dispatch({
        type: UPDATE_REVIEW_FAIL,
        payload:
          error.response && error.response.data.detail
            ? error.response.data.detail
            : error.message,
      });
    }
  };

export const deleteReview =
  (reviewId, bookId) => async (dispatch, getState) => {
    try {
      dispatch({
        type: DELETE_REVIEW_REQUEST,
      });

      const api = useCustomInterceptor();

      const {
        userLogin: { userInfo },
      } = getState();

      const config = {
        headers: {
          "Content-type": "application/json",
          Authorization: `Bearer ${userInfo.token}`,
        },
      };

      const { data } = await api.delete(
        `/api/${reviewId}/${bookId}/removereview/`,

        config
      );

      dispatch({
        type: DELETE_REVIEW_SUCCESS,
        payload: data,
      });
    } catch (error) {
      dispatch({
        type: DELETE_REVIEW_FAIL,
        payload:
          error.response && error.response.data.detail
            ? error.response.data.detail
            : error.message,
      });
    }
  };
