import React, { useState, useEffect } from "react";
import Navbar from "./HOME/Navbar";
import Rating from "./Rating";
import { Link, useHistory } from "react-router-dom";
import "./Bookdetails.css";
import ReactLoading from "react-loading";
import { useDispatch, useSelector } from "react-redux";
import {
  addReview,
  listBookDetails,
  listBooks,
  updateReview,
  deleteReview,
} from "../actions/bookActions";
import {
  createBookBookmark,
  listBookmarkBooks,
} from "../actions/bookmarkActions";
import {
  ADD_REVIEW_RESET,
  UPDATE_REVIEW_RESET,
  DELETE_REVIEW_RESET,
} from "../constants/bookConstants";
import { CREATE_BOOKMARK_RESET } from "../constants/bookmarkConstants";
import FlashMessage from "react-flash-message";
import { usePaystackPayment } from "react-paystack";

function Bookdetails({ match, history }) {
  const [keyword, setKeyword] = useState("");
  const [rating, setRating] = useState(0);
  const [editRating, setEditRating] = useState(0);
  const [comment, setComment] = useState("");
  const [editComment, setEditComment] = useState("");
  const [reviewID, setReviewID] = useState("");
  const [bookID, setBookID] = useState("");
  const [reviewBox, setReviewBox] = useState(false);
  const [editReviewBox, setEditReviewBox] = useState(false);

  const dispatch = useDispatch();

  const bookList = useSelector((state) => state.bookList);
  const { books, loading: loadingBooks, error: errorBooks } = bookList;

  const bookDetail = useSelector((state) => state.bookDetails);
  const { success, book } = bookDetail;

  const userLogin = useSelector((state) => state.userLogin);
  const { userInfo } = userLogin;

  const reviewUpdate = useSelector((state) => state.updateBookReview);
  const { success: reviewUpdateSuccess } = reviewUpdate;

  const reviewDelete = useSelector((state) => state.deleteBookReview);
  const { success: reviewDeleteSuccess } = reviewDelete;

  const bookBookmarkList = useSelector((state) => state.bookBookmarks);
  const { books: bookmarkedBooks, success: bookmarkedBooksSuccess } =
    bookBookmarkList;

  const addedBookmark = useSelector((state) => state.createBookBookmark);

  const { success: addedBookmarkSuccess } = addedBookmark;

  const subHistory = useHistory();

  let kw = history.location.search;

  const addedReview = useSelector((state) => state.bookReview);

  const {
    loading: loadingBookReview,
    error: errorBookReview,
    success: successBookReview,
  } = addedReview;

  useEffect(() => {
    if (addedBookmarkSuccess) {
      dispatch({ type: CREATE_BOOKMARK_RESET });
    } else {
      dispatch(listBookmarkBooks());
    }
  }, [addedBookmarkSuccess]);

  useEffect(() => {
    if (
      successBookReview ||
      reviewUpdateSuccess ||
      reviewDeleteSuccess ||
      addedBookmarkSuccess
    ) {
      setComment("");
      setRating(0);
      dispatch({ type: ADD_REVIEW_RESET });
      dispatch({ type: UPDATE_REVIEW_RESET });
      dispatch({ type: DELETE_REVIEW_RESET });
    }
    dispatch(listBookDetails(match.params.id, match.params.category));
    dispatch(listBooks(kw));
  }, [
    dispatch,
    match,
    successBookReview,
    reviewUpdateSuccess,
    reviewDeleteSuccess,
  ]);

  function submitSearchHandler(e) {
    e.preventDefault();

    return keyword && subHistory.push(`/lawbooks/?search=${keyword}`);
  }

  function submitReviewHandler(e) {
    e.preventDefault();
    dispatch(addReview(match.params.id, { rating, comment }));
  }

  function updateReviewSubmitHandler(e) {
    e.preventDefault();
    dispatch(updateReview(reviewID, bookID, editRating, editComment));
  }

  function deleteReviewSubmitHandler(rID, bID) {
    dispatch(deleteReview(rID, bID));
  }

  function addBookmarkSubmitHandler(id) {
    dispatch(createBookBookmark(id));
  }
  const convertCurrencyFormat = (amount) => {
    let convertedAmount = String(Number(amount) * 417).split(",");
    convertedAmount = Number(convertedAmount * 100);
    return convertedAmount;
  };

  // const bookDownloadUrl = "http://localhost:8000";

  let Config = {
    reference: new Date().getTime().toString(),
    email: userInfo?.email,
    amount: convertCurrencyFormat(book.price),
    publicKey: "pk_test_f70cd128896486bc7bf4ca00d302ea0573268231",
  };

  const onSuccess = (reference) => {
    // Implementation for whatever you want to do with reference and after success call.
    document.location.href = book.file;
  };

  const onClose = () => {
    // implementation for  whatever you want to do when the Paystack dialog closed.
  };

  const InitializePayment = usePaystackPayment(Config);

  return (
    <div>
      <div
        className="bg-cover bg-center relative main-container"
        style={{
          backgroundImage: `url('/images/Education.jpg')`,
          height: "500px",
        }}
      >
        <Navbar />
        <div className="overlay h-full w-full bg-black absolute opacity-30 top-0 left-0"></div>
        <h1 className="text-center text-5xl font-bold text-white mb-8 relative z-20">
          LAW BOOKS
        </h1>
        <p className="background-text text-white text-base font-roboto text-center relative z-20">
          Mind your manners. Tell the truth, Know the law.
        </p>
      </div>
      <h1 className="header-text font-roboto text-gray-600 text-center mt-14">
        Search our archives for legal resources to aid your research
      </h1>
      <div
        style={{ width: "350px" }}
        className="form-container mt-5 relative border rounded-md border-gray-400 mx-auto mb-12 h-12"
      >
        <form onSubmit={submitSearchHandler} className="search-form h-full">
          <input
            style={{ width: "300px" }}
            type="text"
            className="search-field h-full border-0 outline-none px-4 pb-1 font-roboto text-md"
            placeholder="search"
            onChange={(e) => setKeyword(e.target.value)}
          />

          <button className="bg-black h-full w-12 absolute right-0 rounded-r-md">
            <i class="fas fa-search text-white search-icon"></i>
          </button>
        </form>
      </div>

      <div style={{ width: "400px" }} className="cover-container flex ml-44">
        <img
          src={book.coverImage}
          alt="b1"
          className="mr-10 w-48 h-72 block border border-gray-400 rounded-sm"
        />

        <div className="cover-text w-3/4">
          <h1 className="title-text font-roboto text-3xl font-semibold">
            {book.title}
          </h1>
          <h3 className="author-text font-roboto text-sm text-left">
            {book.author}
          </h3>

          <div style={{ isolation: "isolate" }} className="main-rating">
            <Rating value={book.rating} color={"text-yellow-500 text-2xl"} />
          </div>

          <div className="caption-1 font-roboto text-xs my-1">
            <span className="font-bold mr-5">Publisher</span>
            <span className="text-gray-600">Law Publishers</span>
          </div>
          <div className="caption-2 text-xs mb-3">
            <span className="font-bold mr-1">ISBN</span>
            <span className="text-gray-600 ml-10">1234567890</span>
          </div>

          <div className="buttons">
            <button
              style={{ isolation: "isolate" }}
              className="w-16 h-8 font-roboto text-white bg-brandred rounded-sm mr-2"
            >
              {(book.price == 0) || (book.price == null)
                ? "Free"
                : `$${book.price}`}
            </button>
            <button
              onClick={() =>
                userInfo
                  ? (book.price == 0.0) ||
                    (book.price == 0) ||
                    (book.price == null)
                    ? (document.location.href = book.file)
                    : InitializePayment(onSuccess, onClose)
                  : subHistory.push("/signin")
              }
              style={{ isolation: "isolate" }}
              className="w-16 h-8 bg-gray-600 font-roboto text-white rounded-sm"
            >
              Read
            </button>
          </div>
          <div className="mark-icon mt-2 relative -left-2">
            <i
              onClick={() => addBookmarkSubmitHandler(book._id)}
              class={`fas fa-bookmark text-3xl text-gray-400 mx-2 bk-icon ${
                bookmarkedBooksSuccess &&
                bookmarkedBooks.find((bk) => bk.book == book._id) &&
                "text-brandred"
              }`}
            ></i>
            <span
              style={{ fontSize: "9px" }}
              className="search-icon-text font-roboto text-gray-600 w-14 inline-block absolute top-1"
            >
              Bookmark this
            </span>
          </div>
        </div>
      </div>
      <div className="about-description flex">
        <div
          style={{ width: "180px" }}
          className="heading font-roboto ml-44 mr-11"
        >
          <h1 className="about-header font-bold mt-4 mb-2 w-44">
            ABOUT THIS BOOK
          </h1>
          <p className="text-sm text-gray-600">{book.description}</p>

          {!reviewBox && (
            <button
              style={{ isolation: "isolate" }}
              className="review block font-roboto mt-10 mb-2 mx-auto text-sm w-3/5 h-7 p-1 rounded bg-brandred text-white"
              onClick={() => (setReviewBox(true), setEditReviewBox(false))}
            >
              Write a review
            </button>
          )}

          {/* <Rating value={0} color={"text-gray-500"} other={"mx-10"} /> */}

          {reviewBox && !editReviewBox && (
            <div className="">
              <button
                style={{ isolation: "isolate" }}
                className="review block font-roboto mt-10 mb-2 mx-auto text-sm w-3/5 h-7 p-1 rounded text-white bg-brandred"
                onClick={() => setReviewBox(false)}
              >
                Cancel
              </button>
              <form onSubmit={submitReviewHandler}>
                {loadingBookReview && (
                  <div className="flex justify-center mt-10">
                    <ReactLoading
                      type="spin"
                      color="#000"
                      height={20}
                      width={20}
                    />
                  </div>
                )}
                {errorBookReview && (
                  <FlashMessage duration={1000}>
                    <h3 className="text-xs font-roboto bg-red-100 h-6 text-red-900 w-full rounded text-center mx-auto pt-1 mt-5">
                      {errorBookReview}
                    </h3>
                  </FlashMessage>
                )}
                <label className="font-roboto block mb-1 mt-5" htmlFor="rating">
                  Rating
                </label>
                <select
                  style={{ isolation: "isolate" }}
                  className="h-8 w-full rounded-sm shadow-lg border-gray-500 border pl-3 pb-1"
                  name="rating"
                  id="rating"
                  onChange={(e) => setRating(e.target.value)}
                >
                  <option value="" selected disabled>
                    Select...
                  </option>
                  <option value="1">1 - Poor</option>
                  <option value="2">2 - Fair</option>
                  <option value="3">3 - Good</option>
                  <option value="4">4 - Very Good</option>
                  <option value="5">5 - Excellent</option>
                </select>

                <textarea
                  type="text"
                  required
                  className="w-full text-sm review-section border border-gray-400 outline-none mx-auto block px-3 pt-2 font-roboto h-28 rounded mt-6"
                  placeholder="write a review"
                  onChange={(e) => setComment(e.target.value)}
                ></textarea>
                <input
                  className="bg-black text-white mt-3 rounded-sm text-sm h-7 w-full cursor-pointer"
                  type="submit"
                  value="Submit"
                />
              </form>
            </div>
          )}

          {editReviewBox && !reviewBox && (
            <div className="">
              <button
                style={{ isolation: "isolate" }}
                className="review block font-roboto mt-10 mb-2 mx-auto text-sm w-3/5 h-7 p-1 bg-red-600 rounded text-white"
                onClick={() => setEditReviewBox(false)}
              >
                Cancel
              </button>
              <form onSubmit={updateReviewSubmitHandler}>
                {loadingBookReview && (
                  <div className="flex justify-center mt-10">
                    <ReactLoading
                      type="spin"
                      color="#000"
                      height={20}
                      width={20}
                    />
                  </div>
                )}
                {errorBookReview && (
                  <FlashMessage duration={1000}>
                    <h3 className="text-xs font-roboto bg-red-100 h-6 text-red-900 w-full rounded text-center mx-auto pt-1 mt-5">
                      {errorBookReview}
                    </h3>
                  </FlashMessage>
                )}
                <label className="font-roboto block mb-1 mt-5" htmlFor="rating">
                  Rating
                </label>
                <select
                  style={{ isolation: "isolate" }}
                  className="h-8 w-full rounded-sm shadow-lg border-gray-500 border pl-3 pb-1"
                  name="rating"
                  id="rating"
                  value={editRating}
                  onChange={(e) => setEditRating(e.target.value)}
                >
                  <option value="" selected disabled>
                    Select...
                  </option>
                  <option value="1">1 - Poor</option>
                  <option value="2">2 - Fair</option>
                  <option value="3">3 - Good</option>
                  <option value="4">4 - Very Good</option>
                  <option value="5">5 - Excellent</option>
                </select>

                <textarea
                  type="text"
                  required
                  className="w-full text-sm review-section border border-gray-400 outline-none mx-auto block px-3 pt-2 font-roboto h-28 rounded mt-6"
                  placeholder="write a review"
                  value={editComment}
                  onChange={(e) => setEditComment(e.target.value)}
                ></textarea>
                <input
                  className="bg-black text-white mt-3 rounded-sm text-sm h-7 w-full cursor-pointer"
                  type="submit"
                  value="Update"
                />
              </form>
            </div>
          )}

          <h1 className="font-roboto text-lg font-semibold mt-7 mb-2">
            Reviews
          </h1>

          {success && book.reviews.length === 0 ? (
            <h3
              style={{ isolation: "isolate" }}
              className="px-3 py-1 mx-auto w-full bg-blue-200 text-sm font-roboto"
            >
              No Reviews
            </h3>
          ) : (
            success &&
            book.reviews.map((review) => (
              <ul className="reviews-container mt-8 font-roboto text-sm text-gray-700">
                <li className="mb-1">{review.name}</li>
                <li style={{ isolation: "isolate" }} className="">
                  <Rating value={review.rating} color={"text-yellow-500"} />
                </li>
                <li className="">{review.createdAt.substring(0, 10)}</li>
                <li className="mt-2">{review.comment}</li>
                <li className="mt-3">
                  <i
                    onClick={() => (
                      setEditComment(review.comment),
                      setEditRating(review.rating),
                      setEditReviewBox(true),
                      setReviewID(review._id),
                      setBookID(review.book),
                      setReviewBox(false)
                    )}
                    className="fas fa-pen mr-2 text-xs"
                  ></i>
                  <i
                    onClick={() =>
                      deleteReviewSubmitHandler(review._id, review.book)
                    }
                    className="fas fa-trash text-xs"
                  ></i>
                </li>
                <li
                  style={{ height: ".3px" }}
                  className="w-full bg-gray-400 mt-3"
                ></li>
              </ul>
            ))
          )}
        </div>

        {/* SIMILAR LAWBOOKS */}

        <div style={{ marginTop: "40px", width: "500px" }} className="similar">
          <h1 className="more-header font-roboto text-lg font-bold mb-3">
            MORE LIKE THIS
          </h1>
          <div className="similar-lawbooks flex flex-wrap w-full">
            {loadingBooks ? (
              <div className="mx-auto mt-8">
                <ReactLoading type="spin" color="#000" height={40} width={40} />
              </div>
            ) : errorBooks ? (
              <h3
                style={{ width: "400px" }}
                className="px-3 py-1 bg-red-200 text-sm font-roboto"
              >
                {errorBooks}
              </h3>
            ) : (
              success &&
              books.map((data) => {
                return (
                  data.category == match.params.category &&
                  match.params.id != data._id && (
                    <div className="first-bk">
                      <Link to={`/bookdetails/${data.category}/${data._id}`}>
                        <img
                          className="h-32 w-24 mr-5 border border-gray-400 rounded-sm"
                          src={data.coverImage}
                          alt="jb"
                        />
                        <h1
                          style={{ fontSize: "10px" }}
                          className="similar-title-text font-roboto font-bold"
                        >
                          {data.title}
                        </h1>
                        <h3
                          style={{ fontSize: "11px" }}
                          className="font-roboto similar-author-text"
                        >
                          {data.author}
                        </h3>
                      </Link>
                      <div style={{ isolation: "isolate" }}>
                        <Rating value={data.rating} color={"text-yellow-500"} />
                      </div>

                      <h3 className="category font-roboto text-gray-600 text-xs">
                        {data.category}
                      </h3>
                      <button
                        style={{ isolation: "isolate" }}
                        className="price-button font-roboto  w-16 rounded bg-brandred text-white my-1 font-semibold text-sm h-6"
                      >
                        {data.price == 0 ? "Free" : `$${data.price}`}
                      </button>
                    </div>
                  )
                );
              })
            )}
          </div>
        </div>
      </div>
    </div>
  );
}

export default Bookdetails;
