import React, { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import {
  listBookmarkLawyers,
  listBookmarkBooks,
  deleteBookBookmark,
  deleteLawyerBookmark,
} from "../actions/bookmarkActions";
import { Link } from "react-router-dom";
import "./Bookmarks.css";

import {
  DELETE_BOOKMARK_LAWYER_RESET,
  DELETE_BOOKMARK_RESET,
} from "../constants/bookmarkConstants";
import { useState } from "react";

function Bookmarks() {
  const [booksLength, setBooksLength] = useState(4);
  const [lawyersLength, setLawyersLength] = useState(3);

  const dispatch = useDispatch();

  const bookmarkedLawyers = useSelector((state) => state.lawyerBookmarks);

  const { lawyers } = bookmarkedLawyers;

  const removedBookmarkedLawyer = useSelector(
    (state) => state.deleteLawyerBookmark
  );

  const { success: removedBookmarkedLawyerSuccess } = removedBookmarkedLawyer;

  const bookmarkedBooks = useSelector((state) => state.bookBookmarks);

  const { books } = bookmarkedBooks;

  const removedBookmarkedBook = useSelector(
    (state) => state.deleteBookBookmark
  );

  const { success: removedBookmarkedBooksuccess } = removedBookmarkedBook;

  useEffect(() => {
    if (removedBookmarkedLawyerSuccess || removedBookmarkedBooksuccess) {
      dispatch({ type: DELETE_BOOKMARK_LAWYER_RESET });
      dispatch({ type: DELETE_BOOKMARK_RESET });
    } else {
      dispatch(listBookmarkLawyers());
      dispatch(listBookmarkBooks());
    }
  }, [dispatch, removedBookmarkedLawyerSuccess, removedBookmarkedBooksuccess]);

  const deleteBookmarkedLawyer = (id) => {
    dispatch(deleteLawyerBookmark(id));
  };

  const deleteBookmarkedBook = (id) => {
    dispatch(deleteBookBookmark(id));
  };

  return (
    <div className="">
      <div style={{ isolation: "isolate" }} className="navbar bg-gray-600">
        <Link to="/">
          <h2 className="text-white w-16 text-lg relative top-3 mx-6">
            <i className="fas fa-arrow-left"></i>
          </h2>
        </Link>
        <h2 className="text-white text-center text-lg w-44 mx-auto relative bottom-4">
          Your Bookmarks
        </h2>
      </div>
      <div className="bookmark-container flex flex-col">
        <h2
          style={{ maxWidth: "300px", isolation: "isolate" }}
          className="lawyers-heading mx-14 text-lg bg-brandred text-white p-1 text-center mt-14 mb-6 rounded"
        >
          Bookmarked Lawyers/Lawfirms
        </h2>
        <div className="lawyers flex mx-10 flex-shrink-0 flex-wrap">
          {lawyers &&
            lawyers.slice(0, lawyersLength).map((lawyer) => (
              <div
                style={{ maxWidth: "80px" }}
                className="lawyer-bookmark mx-4"
              >
                <Link
                  to={`/member-profile/${lawyer.member}/${lawyer.category}/`}
                >
                  <img
                    className="rounded-full object-cover w-full h-20 border-gray-500 border-2 my-2"
                    src={lawyer.profile_image}
                    alt="lawyer"
                  />
                </Link>

                <button
                  style={{ maxWidth: "80px" }}
                  className="block text-sm bg-gray-600 text-gray-100 my-1 rounded-sm"
                >
                  <i
                    onClick={() => deleteBookmarkedLawyer(lawyer._id)}
                    className="fas fa-trash p-1"
                  ></i>
                </button>
                <span
                  style={{ maxWidth: "80px" }}
                  className="block text-center text-sm bg-gray-600 text-white p-1 rounded-sm"
                >
                  {lawyer.name}
                </span>
              </div>
            ))}
        </div>
        <div className="see-container ml-14 mt-6 font-semibold">
          {lawyers && lawyersLength < lawyers.length && (
            <h3
              onClick={() => setLawyersLength(lawyers.length)}
              className="cursor-pointer"
            >
              See all
            </h3>
          )}

          {lawyers && lawyersLength === lawyers.length && (
            <h3 onClick={() => setLawyersLength(3)} className="cursor-pointer">
              See less
            </h3>
          )}
        </div>

        <h2
          style={{ maxWidth: "300px", isolation: "isolate" }}
          className="books-heading mx-14 text-lg bg-brandred text-white p-1 text-center mt-14 mb-6 rounded"
        >
          Bookmarked Lawbooks
        </h2>
        <div
          style={{ maxWidth: "1200px" }}
          className="books flex mx-10 flex-wrap"
        >
          {books &&
            books.slice(0, booksLength).map((book) => (
              <div style={{ maxWidth: "80px" }} className="book-bookmark mx-4">
                <Link to={`/bookdetails/${book.category}/${book.book}/`}>
                  <img
                    className="w-full h-20 object-cover rounded border-2 border-gray-600"
                    src={book.coverImage}
                    alt="content"
                  />
                </Link>

                <button className="bg-gray-600 mt-1">
                  <i
                    onClick={() => deleteBookmarkedBook(book._id)}
                    className="fas fa-trash p-1 cursor-pointer text-white"
                  ></i>
                </button>

                <span className="block bg-gray-600 text-xs text-white text-center my-1 p-1 rounded">
                  {book.category}
                </span>
                <span className="block bg-gray-600 text-white rounded text-center text-sm p-1">
                  {book.title}
                </span>
              </div>
            ))}
        </div>

        <div className="see-container ml-14 mt-6 font-semibold">
          {books && booksLength < books.length && (
            <h3
              onClick={() => setBooksLength(books.length)}
              className="cursor-pointer"
            >
              See all
            </h3>
          )}

          {books && booksLength === books.length && (
            <h3 onClick={() => setBooksLength(3)} className="cursor-pointer">
              See less
            </h3>
          )}
        </div>
      </div>
    </div>
  );
}

export default Bookmarks;
