import {
  ADD_EMAIL_REQUEST,
  ADD_EMAIL_SUCCESS,
  ADD_EMAIL_FAIL,
  SEND_EMAIL_REQUEST,
  SEND_EMAIL_SUCCESS,
  SEND_EMAIL_FAIL,
} from "../constants/mailConstants";

import axios from "axios";

export const addEmail = (email) => async (dispatch) => {
  try {
    dispatch({
      type: ADD_EMAIL_REQUEST,
    });

    const { data } = await axios.post(
      "/mailings/join-maillist/",

      { email }
    );

    dispatch({
      type: ADD_EMAIL_SUCCESS,
      payload: data,
    });
  } catch (error) {
    dispatch({
      type: ADD_EMAIL_FAIL,
      payload:
        error.response && error.response.data.detail
          ? error.response.data.detail
          : error.message,
    });
  }
};
