export const listQuestionsReducer = (state = { userquestions: [] }, action) => {
  switch (action.type) {
    case QUESTION_REQUEST:
      return { loading: true, ...state };

    case QUESTION_SUCCESS:
      return { loading: false, success: true, userquestions: action.payload };

    case QUESTION_FAIL:
      return { loading: false, error: action.payload };

    default:
      return state;
  }
};

