export const createQuestionReducer = (state = {}, action) => {
  switch (action.type) {
    case CREATE_QUESTION_REQUEST:
      return { loading: true };

    case CREATE_QUESTION_SUCCESS:
      return { loading: false, success: true, state: action.payload };

    case CREATE_QUESTION_FAIL:
      return { loading: false, error: action.payload };

    case CREATE_QUESTION_RESET:
      return {};

    default:
      return state;
  }
};

export const updateQuestionReducer = (state = { userquestion: {} }, action) => {
  switch (action.type) {
    case UPDATE_QUESTION_REQUEST:
      return { loading: true };

    case UPDATE_QUESTION_SUCCESS:
      return {
        loading: false,
        success: true,
        message: "Updated successfully",
        userquestion: action.payload,
      };

    case UPDATE_QUESTION_FAIL:
      return { loading: false, error: action.payload };

    case UPDATE_QUESTION_RESET:
      return {};

    default:
      return state;
  }
};

