import React, { useState, useEffect } from "react";
import "./Profile.css";
import { useSelector, useDispatch } from "react-redux";
import {
  deleteUser,
  getUserDetails,
  updateUserDetails,
} from "../actions/userActions";
import { membersList } from "../actions/chamberActions";
import { planStatusAction } from "../actions/paymentActions";
import {
  USER_DETAILS_UPDATE_RESET,
  USER_DELETE_RESET,
} from "../constants/userConstants";
import { PLAN_STATUS_RESET } from "../constants/paymentConstants";
import FlashMessage from "react-flash-message";
import "react-phone-number-input/style.css";
import PhoneInput from "react-phone-number-input";
import userimg from "../images/profile-pic.jpg";

function Profile({ history }) {
  const [value, setValue] = useState();
  const [email, setEmail] = useState("");
  const [name, setName] = useState("");
  const [password, setPassword] = useState("");
  const [country, setCountry] = useState([]);
  const [imageFile, setImageFile] = useState(null);
  const [countryName, setCountryName] = useState("");
  const [state, setState] = useState([]);
  const [stateName, setStateName] = useState("");
  const [city, setCity] = useState([]);
  const [cityName, setCityName] = useState("");
  const [stateCode, setStateCode] = useState("");
  const [cityCode, setCityCode] = useState("");
  const [lexeratiPlan, setLexeratiPlan] = useState("");
  const [firmPlan, setFirmPlan] = useState("");
  const [eliteFirmPlan, setEliteFirmPlan] = useState("");

  let regionNamesInEnglish = new Intl.DisplayNames(["en"], { type: "region" });

  const dispatch = useDispatch();

  const userDetails = useSelector((state) => state.userDetails);

  const { success: detailSuccess, user } = userDetails;

  const userLogin = useSelector((state) => state.userLogin);
  const { userInfo } = userLogin;

  const userUpdate = useSelector((state) => state.userUpdate);
  const { success, message } = userUpdate;

  const userDelete = useSelector((state) => state.userDelete);
  const { success: deleteSuccess } = userDelete;

  const memberListing = useSelector((state) => state.membersListing);
  const { members, success: memberListingSuccess } = memberListing;

  const planStatus = useSelector((state) => state.planStatus);
  const { success: planStatusSuccess } = planStatus;

  useEffect(() => {
    if (!userInfo) {
      history.push("/signin");
    } else {
      if (!user || !user.name || success || deleteSuccess) {
        dispatch({ type: USER_DETAILS_UPDATE_RESET });
        dispatch({ type: USER_DELETE_RESET });
        dispatch(getUserDetails("profile"));
      } else {
        setName(user.name);
        setEmail(user.email);
        setCountryName(user.country);
        setStateName(user.state);
        setCityName(user.city);
        setValue(user.phone);
        setImageFile(user.profile_pic);
        dispatch(membersList());
      }
    }
  }, [
    // dispatch,
    history,
    userInfo,
    user,
    success,
    detailSuccess,
    deleteSuccess,
  ]);

  useEffect(() => {
    if (planStatusSuccess) {
      dispatch({ type: PLAN_STATUS_RESET });
    } else {
      dispatch(membersList());
    }
  }, [planStatusSuccess]);

  useEffect(() => {
    try {
      userInfo &&
        success &&
        members.find(
          (member) =>
            member.member == userInfo.id &&
            dispatch(planStatusAction(member._id))
        );
    } catch (e) {
      return;
    }
  }, [memberListingSuccess, success]);

  useEffect(() => {
    try {
      members.find(
        (data) =>
          data.member == userInfo.id &&
          data.is_verified &&
          (setLexeratiPlan(data.lexerati),
          setFirmPlan(data.firm),
          setEliteFirmPlan(data.elite_firm))
      );
    } catch (e) {
      return;
    }
  }, [memberListingSuccess, success]);

  const submitHandler = (e) => {
    e.preventDefault();

    dispatch(
      updateUserDetails(
        name,
        email,
        password,
        countryName,
        stateName,
        cityName,
        value,
        imageFile
      )
    );
  };

  function deleteSubmitHandler() {
    return dispatch(deleteUser(userInfo.id));
  }

  var headers = new Headers();
  headers.append(
    "X-CSCAPI-KEY",
    "VU1VYXF3U25EMkxTNFF0YmtNU3BwSHI5RVQzS3hZTDI4czVvUFl5OQ=="
  );

  var requestOptions = {
    method: "GET",
    headers: headers,
    redirect: "follow",
  };

  useEffect(async () => {
    await fetch("https://api.countrystatecity.in/v1/countries", requestOptions)
      .then((response) => response.text())
      .then((result) => setCountry(JSON.parse(result)))
      .catch((error) => console.log("error", error));
  }, []);

  useEffect(async () => {
    await fetch(
      `https://api.countrystatecity.in/v1/countries/${stateCode}/states`,
      requestOptions
    )
      .then((response) => response.text())
      .then((result) => setState(JSON.parse(result)))
      .catch((error) => console.log("error", error));
  }, [stateCode]);

  useEffect(async () => {
    await fetch(
      `https://api.countrystatecity.in/v1/countries/${stateCode}/states/${cityCode}/cities`,
      requestOptions
    )
      .then((response) => response.text())
      .then((result) => setCity(JSON.parse(result)))
      .catch((error) => console.log("error", error));
  }, [cityCode]);

  return (
    <div>
      <div className="main-container">
        <div className="mb-96">
          <h2
            onClick={() => history.push("/")}
            className="mt-10 ml-12 font-semibold text-lg cursor-pointer inline-block"
          >
            <i className="fas fa-arrow-left text-blue-500 mr-2"></i>
            Back to home
          </h2>

          <h1 className="text-3xl font-roboto text-gray-800 font-bold text-center mt-12 mb-10">
            USER PROFILE
          </h1>
          {/* {success && (
            <FlashMessage duration={4000}>
              <h3 className="font-roboto bg-green-400 h-8 text-white w-3/6 rounded text-center mx-auto mb-6 pt-1">
                {message}
              </h3>
            </FlashMessage>
          )} */}

          <form onSubmit={submitHandler} encType="multipart/formdata">
            <img
              className="rounded-full h-20 w-20 block mx-auto object-cover"
              src={imageFile == null || imageFile == "" ? userimg : imageFile}
              alt="imageProfile"
            />

            <label className="block text-center" htmlFor="pic">
              Profile pic
            </label>

            <input
              onChange={(e) => setImageFile(e.target.files)}
              className="block mx-auto text-sm mb-10 w-52"
              type="file"
              src={imageFile}
            />

            <div
              style={{ width: "520px" }}
              className="input-container mt-5 mx-auto"
            >
              <div className="plan-container mb-3">
                <h2 className="">PLAN</h2>
                {userInfo && memberListingSuccess && lexeratiPlan && (
                  <h4 className="bg-gray-300 inline-block px-1 font-semibold">
                    <span className="">
                      <i class="fa fa-gavel text-blue-500 mr-1"></i>
                    </span>
                    Lexerati
                  </h4>
                )}

                {userInfo && memberListingSuccess && firmPlan && (
                  <h4 className="bg-gray-300 inline-block px-1 font-semibold">
                    <span className="">
                      <i class="fa fa-gavel text-yellow-500 mr-1"></i>
                    </span>
                    Firm
                  </h4>
                )}

                {userInfo && memberListingSuccess && eliteFirmPlan && (
                  <h4 className="bg-gray-300 inline-block px-1 font-semibold">
                    <span className="">
                      <i class="fa fa-gavel text-green-500 mr-1"></i>
                    </span>
                    Elitefirm
                  </h4>
                )}

                {userInfo &&
                  memberListingSuccess &&
                  !eliteFirmPlan &&
                  !firmPlan &&
                  !lexeratiPlan && (
                    <h4 className="bg-gray-300 inline-block px-1 font-semibold">
                      Free
                    </h4>
                  )}
              </div>
              <label
                className="font-roboto text-sm text-gray-700"
                htmlFor="Fullname"
              >
                Name
              </label>
              <input
                className="font-roboto w-full block h-10 my-3 px-5 rounded outline-none bg-gray-200"
                type="text"
                placeholder="Fullname"
                onChange={(e) => setName(e.target.value)}
                value={name}
              />

              <label
                className="font-roboto text-sm text-gray-700"
                htmlFor="Fullname"
              >
                Email address
              </label>
              <input
                className="font-roboto w-full block h-10 my-3 px-5 rounded outline-none bg-gray-200"
                type="email"
                placeholder="Email address"
                onChange={(e) => setEmail(e.target.value)}
                value={email}
                id="Email address"
              />
              <label
                className="font-roboto text-sm text-gray-700"
                htmlFor="Phone number"
              >
                Phone number
              </label>
              {/* <input
                className="font-roboto w-full h-10 my-3 px-5 rounded outline-none bg-gray-200"
                type="text"
                onChange={(e) => (setPhone(e.target.value))}
                value={phone}
                placeholder="Phone number"
                id="Phone number"
              /> */}

              <PhoneInput
                defaultCountry="US"
                className="border mt-5 h-10 pl-2 rounded border-gray-500"
                placeholder="Phone number"
                value={value}
                onChange={setValue}
              />

              <div
                style={{ width: "520px" }}
                className="country-select-container text-center mx-auto my-5 flex justify-between"
              >
                <select
                  style={{ width: "31%" }}
                  className="h-9 rounded-md shadow-lg bg-gray-200 outline-none pl-3 pb-1"
                  name="country"
                  required
                  onChange={(e) => (
                    setStateCode(e.target.value),
                    setCountryName(regionNamesInEnglish.of(e.target.value))
                  )}
                >
                  <option value="country" selected disabled>
                    {countryName}
                  </option>

                  {country.map((data) => (
                    <option
                      className="country-code"
                      value={data.iso2}
                      placeholder={data.name}
                    >
                      {data.name}
                    </option>
                  ))}
                </select>

                <select
                  style={{ width: "31%" }}
                  className="h-9 rounded-md shadow-lg bg-gray-200 outline-none pl-3 pb-1"
                  required
                  onChange={(e) => (
                    setCityCode(e.target.value), setStateName(e.target.value)
                  )}
                >
                  <option value="state" selected disabled>
                    {stateName}
                  </option>
                  {state != "" &&
                    Array.from(state).map((data) => (
                      <option value={data.iso2} name={data.iso2}>
                        {data.name}
                      </option>
                    ))}
                </select>

                <select
                  style={{ width: "31%" }}
                  className="h-9 w-40 rounded-md shadow-lg bg-gray-200 outline-none pl-3 pb-1"
                  name="city"
                  onChange={(e) => setCityName(e.target.value)}
                  required
                >
                  <option value="city" selected disabled>
                    {cityName}
                  </option>
                  {Array.from(city).map((data) => (
                    <option value={data.iso2}>{data.name}</option>
                  ))}
                </select>
              </div>

              <label
                style={{ left: "" }}
                className="font-roboto text-sm text-gray-700"
                htmlFor="Password"
              >
                Password
              </label>
              <input
                style={{ width: "520px" }}
                className="passwd-input font-roboto block mx-auto h-10 mt-3 px-5 rounded bg-gray-200 outline-none"
                type="password"
                placeholder="Password"
                onChange={(e) => setPassword(e.target.value)}
              />
            </div>

            <input
              style={{ width: "170px" }}
              className="mx-auto block text-white bg-black h-10 font-roboto font-bold rounded mt-5 signup-btn"
              type="submit"
              value="Update"
            />
          </form>

          <button
            onClick={deleteSubmitHandler}
            className="bg-black text-white rounded p-2 w-32 block mx-auto mt-10"
          >
            Delete Account
          </button>
        </div>
      </div>
    </div>
  );
}

export default Profile;
