import React, { useEffect } from "react";

import Navbar from "../components/HOME/Navbar";
import "./Pricing.css";
import PaymentButton from "./PaymentButton";
import { useDispatch, useSelector } from "react-redux";
import { planStatusAction } from "../actions/paymentActions";
import { membersList } from "../actions/chamberActions";
import headerimg from "../images/social.jpg";

function Pricing({ history }) {
  const dispatch = useDispatch();
  const userLogin = useSelector((state) => state.userLogin);
  const { userInfo, success: userLoginSuccess } = userLogin;

  const memberListing = useSelector((state) => state.membersListing);
  const { members, success } = memberListing;

  useEffect(() => {
    dispatch(membersList());
  }, []);

  useEffect(() => {
    try {
      userInfo &&
        success &&
        members &&
        members.find(
          (member) =>
            member.member == userInfo.id &&
            dispatch(planStatusAction(member._id))
        );
    } catch (e) {
      return;
    }
  }, [success]);

  return (
    <div>
      <div
        className="bg-cover bg-center relative"
        style={{
          backgroundImage: `url(${headerimg})`,
          height: "500px",
        }}
      >
        <Navbar height="500px" />
        <div className="overlay h-full w-full bg-black absolute opacity-40 top-0 left-0"></div>

        <h1 className="bg-main-text text-center text-5xl font-bold text-white mt-14 relative z-20">
          FLEXIBLE PLANS
        </h1>
        <p
          style={{ width: "500px" }}
          className="bg-text text-white font-roboto mx-auto text-center relative z-20 mt-4"
        >
          Choose a plan that works best for you and your team.
        </p>
      </div>

      <div className="parent-container mt-20 grid">
        <div
          style={{ height: "450px" }}
          className="standard border-2 w-80 rounded-2xl border-gray-300 bg-gray-100 shadow-xl mt-2"
        >
          <h1 className="font-roboto text-center text-xl mt-4 font-bold">
            Standard
          </h1>
          <h1 className="font-roboto text-center text-3xl font-bold">FREE</h1>
          <div
            style={{ height: "1px" }}
            className="heading-line bg-gray-300 w-64 mx-auto my-5"
          ></div>
          <ul className="benefits ml-7 font-roboto text-sm h-28 justify-evenly flex flex-col">
            <li className="">
              <i class="fas fa-check mr-2 text-gray-400"></i>Basic Profile
            </li>
            <li className="">
              <i class="fas fa-check mr-2 text-gray-400"></i>Directory listing
            </li>
            <li className="">
              <i class="fas fa-check mr-2 text-gray-400"></i>Access to question
              and answer forum
            </li>
          </ul>
          {userInfo ? (
            <button
              style={{ width: "85%", isolation: "isolate" }}
              className="border outline-none bg-brandred text-white font-semibold font-roboto h-12 w-3/4 block rounded-lg mx-auto mt-36"
            >
              Current Plan
              <i class="fas fa-arrow-right ml-2 text-white text-xs"></i>
            </button>
          ) : (
            <button
              onClick={() => !userInfo && history.push("/signin")}
              style={{ width: "85%", isolation: "isolate" }}
              className="border outline-none bg-brandred text-white font-semibold font-roboto h-12 w-3/4 block rounded-lg mx-auto mt-36"
            >
              Choose Plan
              <i class="fas fa-arrow-right ml-2 text-white text-xs"></i>
            </button>
          )}
        </div>

        <PaymentButton
          name="Lexerati"
          price="30"
          profile="Enhanced Profile"
          memberType={userInfo && userInfo}
          id={userInfo && userInfo.id}
          email={userInfo && userInfo.email}
        />

        <PaymentButton
          name="Firm"
          price="50"
          profile="Basic Firm Profile"
          memberType={userInfo && userInfo}
          id={userInfo && userInfo.id}
          email={userInfo && userInfo.email}
        />

        <PaymentButton
          name="Elite Firm"
          price="150"
          profile="Enhanced Firm Profile"
          memberType={userInfo && userInfo}
          id={userInfo && userInfo.id}
          email={userInfo && userInfo.email}
        />
      </div>
    </div>
  );
}

export default Pricing;
