import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { verifyMemberAction, membersList } from "../actions/chamberActions";
import FlashMessage from "react-flash-message";

function Chamberverification() {
  const [docFile, setDocFile] = useState(null);
  const [memberID, setMemberID] = useState("");

  const dispatch = useDispatch();

  const userDetail = useSelector((state) => state.userLogin);

  const { userInfo } = userDetail;

  const memberDetail = useSelector((state) => state.membersListing);

  const { members } = memberDetail;

  const verifyDetail = useSelector((state) => state.verifyMember);

  const { success, error } = verifyDetail;

  useEffect(() => {
    dispatch(membersList());
  }, []);

  useEffect(() => {
    try {
      members.find(
        (data) =>
          data.member == userInfo.id &&
          (setMemberID(data._id), console.log(memberID))
      );
    } catch (e) {
      console.log(e);
    }
  }, [members]);

  const verificationSubmitHandler = (e) => {
    e.preventDefault();
    try {
      dispatch(verifyMemberAction(docFile, memberID));
      setDocFile(null);
    } catch (e) {
      return;
    }
  };

  return (
    <div className="">
      <Link to="/">
        <div className="bg-gray-700 pl-8">
          <i className="fas fa-arrow-left text-white mr-2 py-4"></i>
          <span className="text-white">Back To Home</span>
        </div>
      </Link>
      <div className="flex flex-col justify-center mt-52">
        {success && (
          <FlashMessage duration={4000}>
            <h3 className="font-roboto bg-green-400 h-8 text-white w-3/6 rounded text-center mx-auto mb-6 pt-1">
              File Uploaded Successfully
            </h3>
          </FlashMessage>
        )}

        {error && (
          <FlashMessage duration={4000}>
            <h3 className="font-roboto bg-red-400 h-8 text-white w-3/6 rounded text-center mx-auto mb-6 pt-1">
              {error}
            </h3>
          </FlashMessage>
        )}

        <div className="icon mb-6">
          <h2 className="text-center  font-bold text-xl px-4">
            Upload your documents here
          </h2>
          <i className="fas fa-arrow-down text-center block text-3xl"></i>
        </div>

        <div className="form-container flex justify-center items-center">
          <form onSubmit={verificationSubmitHandler}>
            <input
              onChange={(e) => setDocFile(e.target.files)}
              style={{ maxWidth: "220px" }}
              type="file"
              accept=".pdf, .doc"
              required
            />

            <input
              className="block bg-black text-white rounded mx-auto w-24 my-4"
              type="submit"
            />
          </form>
        </div>
      </div>
    </div>
  );
}

export default Chamberverification;
