import axios from "axios";

import {
  POST_REQUEST,
  POST_SUCCESS,
  POST_FAIL,
  CREATE_COMMENT_REQUEST,
  CREATE_COMMENT_SUCCESS,
  CREATE_COMMENT_FAIL,
  UPDATE_COMMENT_REQUEST,
  UPDATE_COMMENT_SUCCESS,
  UPDATE_COMMENT_FAIL,
  DELETE_COMMENT_REQUEST,
  DELETE_COMMENT_SUCCESS,
  DELETE_COMMENT_FAIL,
} from "../constants/blogConstants";

import useCustomInterceptor from "../customInterceptor/useCustomInterceptor";

export const listPosts =
  (keyword = "") =>
  async (dispatch) => {
    try {
      dispatch({ type: POST_REQUEST });
      const { data } = await axios.get(`/blog/posts${keyword}`);

      dispatch({ type: POST_SUCCESS, payload: data });
    } catch (error) {
      dispatch({
        type: POST_FAIL,
        payload:
          error.response && error.response.data.detail
            ? error.response.data.detail
            : error.message,
      });
    }
  };

export const addPostComment = (comment, id) => async (dispatch, getState) => {
  try {
    dispatch({
      type: CREATE_COMMENT_REQUEST,
    });

    const api = useCustomInterceptor();

    const {
      userLogin: { userInfo },
    } = getState();

    const config = {
      headers: {
        "Content-type": "application/json",
        Authorization: `Bearer ${userInfo.token}`,
      },
    };

    const { data } = await api.post(
      `/blog/addpost-comment/${id}/`,

      {
        comment,
      },

      config
    );

    dispatch({
      type: CREATE_COMMENT_SUCCESS,
      payload: data,
    });
  } catch (error) {
    dispatch({
      type: CREATE_COMMENT_FAIL,
      payload:
        error.response && error.response.data.detail
          ? error.response.data.detail
          : error.message,
    });
  }
};

export const updatePostComment =
  (comment, id) => async (dispatch, getState) => {
    try {
      dispatch({
        type: UPDATE_COMMENT_REQUEST,
      });

      const api = useCustomInterceptor();

      const {
        userLogin: { userInfo },
      } = getState();

      const config = {
        headers: {
          "Content-type": "application/json",
          Authorization: `Bearer ${userInfo.token}`,
        },
      };

      const { data } = await api.patch(
        `/blog/updatepost-comment/${id}/`,

        {
          comment,
        },

        config
      );

      dispatch({
        type: UPDATE_COMMENT_SUCCESS,
        payload: data,
      });
    } catch (error) {
      dispatch({
        type: UPDATE_COMMENT_FAIL,
        payload:
          error.response && error.response.data.detail
            ? error.response.data.detail
            : error.message,
      });
    }
  };

export const deletePostComment = (id) => async (dispatch, getState) => {
  try {
    dispatch({
      type: DELETE_COMMENT_REQUEST,
    });

    const api = useCustomInterceptor();

    const {
      userLogin: { userInfo },
    } = getState();

    const config = {
      headers: {
        "Content-type": "application/json",
        Authorization: `Bearer ${userInfo.token}`,
      },
    };

    const { data } = await api.delete(
      `/blog/removepost-comment/${id}/`,
      config
    );

    dispatch({
      type: DELETE_COMMENT_SUCCESS,
      payload: data,
    });
  } catch (error) {
    dispatch({
      type: DELETE_COMMENT_FAIL,
      payload:
        error.response && error.response.data.detail
          ? error.response.data.detail
          : error.message,
    });
  }
};
