import React, { useState, useEffect } from "react";
import Navbar from "../components/HOME/Navbar";
import { useSelector, useDispatch } from "react-redux";
import ReactLoading from "react-loading";
import { Link } from "react-router-dom";
import { login } from "../actions/userActions";
import "./Signin.css";
import headerimg from "../images/loginimage.jpg";
import FlashMessage from "react-flash-message";
import useCustomInterceptor from "../customInterceptor/useCustomInterceptor";

function Signin({ location, history }) {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const dispatch = useDispatch();

  const redirect = location.search ? location.search.split("=")[1] : "/";

  const userLogin = useSelector((state) => state.userLogin);

  const { error, loading, userInfo } = userLogin;

  useEffect(() => {
    if (userInfo) {
      history.push(redirect);
    }
  }, [history, userInfo, redirect]);

  function sumbitHandler(e) {
    e.preventDefault();
    
    dispatch(login(email, password));
  }

  return (
    <div className="main-container">
      <div className="mb-0">
        <div
          className="relative bg-center bg-cover"
          style={{
            backgroundImage: `url(${headerimg})`,
            height: "700px",
          }}
        >
          <Navbar />
          <div className="absolute top-0 left-0 w-full h-full bg-black overlay opacity-30"></div>
          <h1 className="relative z-20 mb-4 text-5xl font-bold text-center text-white mt-14">
            SIGN IN FOR FREE
          </h1>
          <p
            style={{ width: "500px" }}
            className="relative z-20 mx-auto mt-4 text-center text-white bg-text font-roboto"
          >
            Join the Largest Community of Legal Services Expert as someone in
            need of legal services, a lawyer, firm, or student.
          </p>
        </div>

        <h1 className="mt-12 mb-10 text-2xl font-bold text-center text-gray-800 font-roboto">
          SIGN IN
        </h1>
        {error && (
          <FlashMessage duration={3000} persistOnHover={false}>
            <h3
              style={{ width: "400px" }}
              className="px-3 py-2 mx-auto mb-3 text-center bg-red-200 rounded-sm text-md font-roboto"
            >
              {error}
            </h3>
          </FlashMessage>
        )}
        <div style={{ top: "-20%" }} className="absolute z-20 left-1/2">
          {loading && (
            <div className="flex items-center justify-center mb-12">
              <ReactLoading type="spin" color="#000" height={50} width={50} />
            </div>
          )}
        </div>
        <form onSubmit={sumbitHandler}>
          <div
            style={{ width: "520px" }}
            className="mx-auto mt-5 input-container"
          >
            <input
              onChange={(e) => setEmail(e.target.value)}
              className="block w-full h-10 px-5 mt-5 border border-gray-500 rounded email-input font-roboto"
              type="email"
              placeholder="Email address"
              required
            />
          </div>
          <input
            style={{ width: "520px" }}
            className="block h-10 px-5 mx-auto mt-5 border border-gray-500 rounded passwd-input font-roboto"
            type="password"
            placeholder="Password"
            required
            onChange={(e) => setPassword(e.target.value)}
          />
          <input
            style={{ width: "170px" }}
            className="block h-10 mx-auto mt-5 font-bold text-white bg-black rounded font-roboto signup-btn"
            type="submit"
            value="Sign In"
          />
        </form>

        {/* <h1 className="my-4 text-lg italic font-bold text-center text-gray-800 font-roboto">
          OR
        </h1>
        <div style={{ width: "380px" }} className="flex mx-auto other-options">
          <button className="w-1/2 h-10 mr-4 text-sm font-bold text-white bg-red-600 rounded">
            Sign in with google
          </button>
          <button className="w-1/2 h-10 text-sm font-bold text-white bg-blue-900 rounded">
            Sign in with Facebook
          </button>
        </div> */}
        <h1 className="mt-4 italic text-center text-gray-700 font-roboto">
          Don't have an account?
          <Link to={redirect ? `/signup?redirect=${redirect}` : "/signup"}>
            <span className="ml-2 font-bold text-black">Sign up </span>
          </Link>
        </h1>
      </div>
    </div>
  );
}

export default Signin;
