import React, { useState, useEffect } from "react";
import { useHistory } from "react-router-dom";
import "./Card.css";
import { useDispatch, useSelector } from "react-redux";
import {
  createLawyerBookmark,
  listBookmarkLawyers,
} from "../actions/bookmarkActions";

import { listMemberDetails } from "../actions/chamberActions";

import { CREATE_BOOKMARK_LAWYER_RESET } from "../constants/bookmarkConstants";

function Card({
  name,
  category,
  country,
  state,
  city,
  image,
  id,
  chamberId,
  activeStatus,
  areas,
  about,
  verifiedStatus,
}) {
  const [memberId, setMemberId] = useState("");
  const [memberCategory, setMemberCategory] = useState("");
  const [color, setColor] = useState("");

  const mainHistory = useHistory();

  const dispatch = useDispatch();
  const lawyerBookmarkList = useSelector((state) => state.lawyerBookmarks);

  const { lawyers, success } = lawyerBookmarkList;

  const memberListing = useSelector((state) => state.membersListing);
  const { members, success: membersListingSuccess } = memberListing;

  const addedBookmark = useSelector((state) => state.createLawyerBookmark);

  const { success: addedBookmarkSuccess, message } = addedBookmark;

  const userLogin = useSelector((state) => state.userLogin);
  const { userInfo, success: userLoginSuccess } = userLogin;

  useEffect(() => {
    function links() {
      return setMemberId(id), setMemberCategory(category);
    }
    links();
  }, []);

  // useEffect(() => {
  //   dispatch(practiceAreaList(id));
  // }, []);

  useEffect(() => {
    if (addedBookmarkSuccess) {
      dispatch({ type: CREATE_BOOKMARK_LAWYER_RESET });
    } else {
      dispatch(listBookmarkLawyers());
    }
  }, [addedBookmarkSuccess]);

  const bookmarkSubmitHandler = () => {
    userInfo ? dispatch(createLawyerBookmark(id)) : mainHistory.push("/signin");
  };

  const memberDetailsHandler = () => {
    if (userInfo) {
      dispatch(listMemberDetails(id, memberCategory));
      mainHistory.push(`/member-profile/${id}/${memberCategory}`);
    } else {
      mainHistory.push("/signin");
    }
  };

  let regionNamesInEnglish = new Intl.DisplayNames(["en"], { type: "region" });

  return (
    <div>
      <div
        style={{ width: "530px" }}
        className="bg-gray-300 flex h-36 items-center relative rounded-sm card"
      >
        <img
          onClick={memberDetailsHandler}
          className="h-28 w-28 mx-3 rounded-sm object-cover"
          src={image}
          alt="mr"
        />

        <div className="ml-3 card-content">
          <div className=""></div>
          <h1 className="text-gray-700 bg-transparent">
            {name}

            {verifiedStatus && (
              <svg
                xmlns="http://www.w3.org/2000/svg"
                className="h-5 w-5 inline-block ml-2"
                viewBox="0 0 20 20"
                fill="currentColor"
              >
                <path
                  fill-rule="evenodd"
                  d="M6.267 3.455a3.066 3.066 0 001.745-.723 3.066 3.066 0 013.976 0 3.066 3.066 0 001.745.723 3.066 3.066 0 012.812 2.812c.051.643.304 1.254.723 1.745a3.066 3.066 0 010 3.976 3.066 3.066 0 00-.723 1.745 3.066 3.066 0 01-2.812 2.812 3.066 3.066 0 00-1.745.723 3.066 3.066 0 01-3.976 0 3.066 3.066 0 00-1.745-.723 3.066 3.066 0 01-2.812-2.812 3.066 3.066 0 00-.723-1.745 3.066 3.066 0 010-3.976 3.066 3.066 0 00.723-1.745 3.066 3.066 0 012.812-2.812zm7.44 5.252a1 1 0 00-1.414-1.414L9 10.586 7.707 9.293a1 1 0 00-1.414 1.414l2 2a1 1 0 001.414 0l4-4z"
                  clip-rule="evenodd"
                />
              </svg>
            )}
          </h1>
          <span className="block text-xs text-gray-800  font-semibold">
            {category}
          </span>
          <span className="block text-xs text-gray-800  font-semibold">
            {city}, {state},{country}
          </span>
          <span className="block text-xs text-gray-800  font-semibold">
            {membersListingSuccess && about && about.slice(0, 38)}...
          </span>
          <span className="block text-xs text-gray-800  font-semibold">{}</span>
          {activeStatus ? (
            <span className="block text-xs font-semibold text-green-600 italic">
              Available, Online
            </span>
          ) : (
            <span className="block text-xs text-red-600 italic font-semibold">
              Unavailable, Offline
            </span>
          )}

          <div className="button-container">
            {membersListingSuccess &&
              areas &&
              areas
                .slice(0, 2)
                .map((data) => (
                  <button className="bg-gray-500 cursor-default h-6 w-36 mr-3 rounded-sm text-xs italic text-white">
                    {data.area}
                  </button>
                ))}

            {membersListingSuccess && areas && areas.length > 2 && (
              <span className="text-blue-400 text-xs italic font-semibold cursor-pointer">
                + {areas.length - 2} more
              </span>
            )}
          </div>
          <svg
            onClick={bookmarkSubmitHandler}
            xmlns="http://www.w3.org/2000/svg"
            className="bookmark h-10 absolute top-5 right-10"
            viewBox="0 0 20 20"
            fill={
              success && lawyers.find((lawyer) => lawyer.member == id)
                ? "red"
                : "gray"
            }
          >
            <path d="M5 4a2 2 0 012-2h6a2 2 0 012 2v14l-5-2.5L5 18V4z" />
          </svg>
        </div>
      </div>
    </div>
  );
}

export default Card;
