import {
  PAYMENT_PLAN_REQUEST,
  PAYMENT_PLAN_SUCCESS,
  PAYMENT_PLAN_FAIL,
  PLAN_STATUS_REQUEST,
  PLAN_STATUS_SUCCESS,
  PLAN_STATUS_FAIL,
} from "../constants/paymentConstants";

import axios from "axios";

import useCustomInterceptor from "../customInterceptor/useCustomInterceptor"

export const paymentPlanAction = (id, price) => async (dispatch, getState) => {
  try {
    dispatch({
      type: PAYMENT_PLAN_REQUEST,
    });

    const api = useCustomInterceptor();

    const userInfo = JSON.parse(localStorage.getItem("userInfo"));

    const config = {
      headers: {
        "Content-type": "application/json",
        Authorization: `Bearer ${userInfo.token}`,
      },
    };

    const { data } = await api.patch(
      `/lexquiry/premium-member/${id}/`,
      { price },
      config
    );

    dispatch({
      type: PAYMENT_PLAN_SUCCESS,
      payload: data,
    });
  } catch (error) {
    dispatch({
      type: PAYMENT_PLAN_FAIL,
      payload:
        error.response && error.response.data.detail
          ? error.response.data.detail
          : error.message,
    });
  }
};

export const planStatusAction = (id) => async (dispatch, getState) => {
  try {
    dispatch({
      type: PLAN_STATUS_REQUEST,
    });

    const api = useCustomInterceptor();

    const userInfo = JSON.parse(localStorage.getItem("userInfo"));

    const config = {
      headers: {
        "Content-type": "application/json",
        Authorization: `Bearer ${userInfo.token}`,
      },
    };

    const { data } = await api.get(
      `/lexquiry/plan-status/${id}/`,

      config
    );

    dispatch({
      type: PLAN_STATUS_SUCCESS,
      payload: data,
    });
  } catch (error) {
    dispatch({
      type: PLAN_STATUS_FAIL,
      payload:
        error.response && error.response.data.detail
          ? error.response.data.detail
          : error.message,
    });
  }
};


