import React, { useEffect } from "react";
import Navbar from "./HOME/Navbar";
import "./Membercategories.css";
import { useSelector, useDispatch } from "react-redux";
import { listCategories } from "../actions/categoryActions";
import { useState } from "react";
import ReactLoading from "react-loading";
import { LazyLoadComponent } from "react-lazy-load-image-component";
import headerimg from "../images/Discrimination.jpg";

function Lawyercategories() {
  const [categoryState, setCategoryState] = useState([]);
  const [popularLabel, setPopularLabel] = useState(false);
  const [allLabel, setAllLabel] = useState(true);

  const dispatch = useDispatch();

  const categories = useSelector((state) => state.categoriesList);

  const { error, loading, categoryList, success } = categories;

  useEffect(() => {
    dispatch(listCategories());
    setCategoryState(categoryList);
  }, [dispatch, loading]);

  function popularAreaHandler() {
    let listSize = [];
    try {
      categoryList.map((item) => {
        let ans = item.names;
        listSize.push(ans.length);
      });
      return categoryList.find((data) => {
        return Math.max(...listSize) == data.names.length
          ? setCategoryState([data])
          : "";
      });
    } catch (e) {
      return e;
    }
  }

  function allCategoriesHandler() {
    return setCategoryState(categoryList);
  }

  return (
    <div>
      <LazyLoadComponent>
        <div
          style={{
            backgroundImage: `url(${headerimg})`,
            height: "500px",
          }}
          className="bg-cover bg-center relative"
        >
          <div className="overlay h-full w-full bg-black absolute opacity-20 top-0 left-0"></div>
          <Navbar />
          <h1 className="background-text text-5xl font-bold font-roboto text-white relative z-20 text-center mb-4">
            CATEGORIES
          </h1>
          <p className="background-subtext text-white text-center font-roboto relative z-20">
            Find your ideal Lawyer amongst a variety of practice areas{" "}
          </p>
        </div>
      </LazyLoadComponent>

      <h1 className="text-center font-roboto text-2xl text-gray-700 font-bold my-10">
        BROWSE BY
      </h1>

      <form className="text-center">
        <input
          type="radio"
          id="popular-areas"
          name="radio-options"
          className="radio-button"
          hidden
          onChange={(e) =>
            e.target.checked
              ? (setPopularLabel(true), setAllLabel(false))
              : setAllLabel(true)
          }
        />

        <label
          className={`client-label font-roboto ${
            popularLabel ? "bg-black" : "bg-gray-200"
          } text-sm font-bold radio-label h-full py-2 w-40 inline-block px-4 text-center`}
          htmlFor="popular-areas"
          onClick={popularAreaHandler}
        >
          Popular areas
        </label>

        <input
          type="radio"
          id="all-areas"
          name="radio-options"
          className="radio-button"
          hidden
          onChange={(e) =>
            e.target.checked
              ? (setAllLabel(true), setPopularLabel(false))
              : setAllLabel(false)
          }
        />
        <label
          className={`font-roboto text-sm ${
            allLabel ? "bg-black text-white" : "bg-gray-200"
          } font-bold radio-label h-full py-2 w-40 inline-block text-center`}
          htmlFor="all-areas"
          onClick={allCategoriesHandler}
        >
          A-Z
        </label>
      </form>

      {loading ? (
        <div className="flex justify-center items-center mt-12">
          <ReactLoading type="spin" color="#000" height={40} width={40} />
        </div>
      ) : (
        success &&
        categoryState.map((data) => (
          <div
            style={{ width: "650px" }}
            className="all-categories-container mx-auto relative"
          >
            <h2 className="big-letter text-3xl text-gray-700 font-bold ml-3">
              {data.names.length != 0 && data.categoryLetter}
            </h2>
            <div className="all-categories-sub-container">
              {data.names.map((data) => (
                <div>
                  <div
                    style={{
                      backgroundImage: `url('${data.image}')`,
                      height: "100px",
                      width: "150px",
                    }}
                    className="all-categories--children bg-cover rounded-md mx-2 my-2 relative"
                  >
                    <div className="overlay h-full w-full bg-black absolute opacity-40 top-0 left-0"></div>
                    <h2 className="font-roboto text-white text-xs absolute bottom-2 ml-2 w-1/2 font-bold">
                      {data.name.toUpperCase()}
                    </h2>
                  </div>
                </div>
              ))}
            </div>
          </div>
        ))
      )}
    </div>
  );
}

export default Lawyercategories;
