import axios from "axios";
import jwt_decode from "jwt-decode";
import dayjs from "dayjs";

function useCustomInterceptor() {
  const baseURL = "https://metalex-production.up.railway.app";

  let authToken = JSON.parse(localStorage.getItem("userInfo"));

  const axiosInstance = axios.create({
    baseURL,
    headers: { Authorization: `Bearer ${authToken?.token}` },
  });

  axiosInstance.interceptors.request.use(async (req) => {
    const userRefresh = await jwt_decode(authToken.refresh_token);
    const isRefreshExpired = dayjs.unix(userRefresh.exp).diff(dayjs()) < 1;
    const userAccess = await jwt_decode(authToken.token);
    const isAccessExpired = dayjs.unix(userAccess.exp).diff(dayjs()) < 1;

    if (isRefreshExpired) {
      localStorage.removeItem("userInfo");
      window.location = "/signin";
    }

    if (!isAccessExpired) {
      return req;
    }

    const response = await axios.post(`${baseURL}/auth/token/refresh/`, {
      refresh: authToken.refresh_token,
    });

    authToken.access = response.data.access;
    authToken.refresh_token = response.data.refresh;
    authToken.token = response.data.access;

    localStorage.setItem("userInfo", JSON.stringify(authToken));
    req.headers.Authorization = `Bearer ${response.data.access}`;

    return req;
  });

  // axiosInstance.interceptors.response.use(
  //   (resp) => resp,
  //   async (error) => {
  //     if (error.response.status === 401) {
  //       const response = await axios.post(`${baseURL}/auth/token/refresh/`, {
  //         refresh: authToken.refresh,
  //       });

  //       authToken.access = response.data.access;
  //       authToken.refresh = response.data.refresh;
  //       authToken.token = response.data.access;

  //       localStorage.setItem("userInfo", JSON.stringify(authToken));

  //       if (response.status === 200) {
  //         return axios(error.config);
  //       }
  //     }

  //     return error;
  //   }
  // );

  return axiosInstance;
}

export default useCustomInterceptor;
