import useCustomInterceptor from "../customInterceptor/useCustomInterceptor";

export const membersList =
  (keyword = "") =>
  async (dispatch) => {
    try {
      dispatch({ type: CHAMBER_LIST_REQUEST });

      const { data } = await axios.get(`/lexquiry/members-listing${keyword}`);

      dispatch({ type: CHAMBER_LIST_SUCCESS, payload: data });
    } catch (error) {
      dispatch({
        type: CHAMBER_LIST_FAIL,
        payload:
          error.response && error.response.data.detail
            ? error.response.data.detail
            : error.message,
      });
    }
  };

export const listMemberDetails =
  (id, category) => async (dispatch, getState) => {
    try {
      dispatch({ type: MEMBER_DETAILS_REQUEST });

      const api = useCustomInterceptor();

      const userInfo = JSON.parse(localStorage.getItem("userInfo"));

      const config = {
        headers: {
          "Content-type": "application/json",
          Authorization: `Bearer ${userInfo.token}`,
        },
      };

      const { data } = await api.get(
        `/lexquiry/memberdetails/${id}/${category}/`,
        config
      );

      dispatch({ type: MEMBER_DETAILS_SUCCESS, payload: data });
    } catch (error) {
      dispatch({
        type: MEMBER_DETAILS_FAIL,
        payload:
          error.response && error.response.data.message
            ? error.response.data.message
            : error.message,
      });
    }
  };

export const updateMemberDpAction =
  (dpImage, id) => async (dispatch, getState) => {
    try {
      dispatch({
        type: UPDATE_DP_REQUEST,
      });

      const api = useCustomInterceptor();

      const userInfo = JSON.parse(localStorage.getItem("userInfo"));

      const config = {
        headers: {
          "Content-type": "multipart/form-data",
          Authorization: `Bearer ${userInfo.token}`,
        },
      };

      let formData = new FormData();
      formData.append("profileImage", dpImage[0]);

      const { data } = await api.patch(
        `/lexquiry/update-dp/${id}/`,
        formData,
        config
      );

      dispatch({
        type: UPDATE_DP_SUCCESS,
        payload: data,
      });
    } catch (error) {
      dispatch({
        type: UPDATE_DP_FAIL,
        payload:
          error.response && error.response.data.detail
            ? error.response.data.detail
            : error.message,
      });
    }
  };

