export const verifyMemberAction =
  (docFile, id) => async (dispatch, getState) => {
    try {
      dispatch({
        type: VERIFY_REQUEST,
      });

      const api = useCustomInterceptor();

      const userInfo = JSON.parse(localStorage.getItem("userInfo"));

      const config = {
        headers: {
          "Content-type": "multipart/form-data",
          Authorization: `Bearer ${userInfo.token}`,
        },
      };

      let formData = new FormData();
      formData.append("file", docFile[0]);

      const { data } = await api.patch(
        "/lexquiry/verify-member/",
        formData,
        config
      );

      dispatch({
        type: VERIFY_SUCCESS,
        payload: data,
      });
    } catch (error) {
      dispatch({
        type: VERIFY_FAIL,
        payload:
          error.response && error.response.data.detail
            ? error.response.data.detail
            : error.message,
      });
    }
  };
